@import "../../../assets/styles/colors.scss";

.btn {
  display: flex;
  align-items: center;
  justify-content: center;
  outline: none;
  border: none;
  border-radius: 5px;
  background: none;
  cursor: pointer;
  user-select: none;
  font-family: "Gilroy", sans-serif;
  white-space: nowrap;
  transition: 175ms cubic-bezier(0.1, 0.1, 0.25, 1);
}

.btn-secondary {
  background: $secondary-color;
  color: $light-color;
  
  &:hover {
    background: lighten($secondary-color, 5%);
  }
  
  &:active {
    background: darken($secondary-color, 5%);
  }
}

.btn-primary {
  background: $primary-color;
  color: $light-color;
  
  &:hover {
    background: #53A0F0;
  }
  
  &:active {
    background: #4790DC;
  }
}

.btn-success {
  background: $success-color;
  color: $light-color;
  
  &:hover {
    background: lighten($success-color, 5%);
  }
  
  &:active {
    background: darken($success-color, 5%);
  }
}

.btn-danger {
  background: $danger-color;
  color: $light-color;
  
  &:hover {
    background: lighten($danger-color, 5%);
  }
  
  &:active {
    background: darken($danger-color, 5%);
  }
}

.btn-dark {
  background: $dark-color;
  color: $light-color;
  
  &:hover {
    background: lighten($dark-color, 5%);
  }
  
  &:active {
    background: darken($dark-color, 5%);
  }
}

.btn-dark-secondary {
  background: $dark-secondary-color;
  color: $light-color;
  
  &:hover {
    background: lighten($dark-secondary-color, 5%);
  }
  
  &:active {
    background: darken($dark-secondary-color, 5%);
  }
}

.btn-light {
  background: $light-color;
  color: $dark-color;
  
  &:hover {
    background: lighten($light-color, 5%);
  }
  
  &:active {
    background: darken($light-color, 5%);
  }
}

.btn-gray-light {
  background: $gray-light-color;
  color: $light-color;
  
  &:hover {
    background: lighten($gray-light-color, 5%);
  }
  
  &:active {
    background: darken($gray-light-color, 5%);
  }
}

.btn-gray {
  background: $gray-color;
  color: $light-color;
  
  &:hover {
    background: lighten($gray-color, 5%);
  }
  
  &:active {
    background: darken($gray-color, 5%);
  }
}

.btn-outline {
  background: none;
  box-shadow: 0 0 0 2px inset $secondary-color;
  color: $light-color;
  
  &:hover {
    box-shadow: 0 0 0 2px inset $primary-color;
  }
  
  &:active {
    box-shadow: 0 0 0 2px inset $primary-color;
    background: darken($primary-color, 5%);
  }
}

.btn-dark-outline {
  background: $dark-color;
  box-shadow: 0 0 0 2px inset $dark-color;
  color: $light-color;
  
  &:hover {
    box-shadow: 0 0 0 2px inset $primary-color;
  }
  
  &:active {
    box-shadow: 0 0 0 2px inset $primary-color;
    background: darken($primary-color, 5%);
  }
}

.btn-secondary-outline {
  background: $dark-color;
  box-shadow: 0 0 0 2px inset $secondary-color;
  color: $light-color;
  
  &:hover {
    box-shadow: 0 0 0 2px inset $primary-color;
  }
  
  &:active {
    box-shadow: 0 0 0 2px inset $primary-color;
    background: darken($primary-color, 5%);
  }
}

.btn-gray-light-outline {
  background: none;
  box-shadow: 0 0 0 2px inset $gray-light-color;
  color: $light-color;
  
  &:hover {
    box-shadow: 0 0 0 2px inset $gray-light-color;
  }
  
  &:active {
    box-shadow: 0 0 0 2px inset $gray-light-color;
    background: darken($gray-light-color, 5%);
  }
}

.btn-large {
  padding: 22px 44px;
  
  font-size: 24px;
  font-weight: 600;
  
  &.btn-square {
    padding: 22px;
  }
}

.btn-medium {
  max-height: 47px;
  min-height: 47px;
  
  padding: 11px 24px;
  
  font-size: 18px;
  font-weight: 500;
  
  &.btn-square {
    padding: 12px;
  }
}

.btn-normal {
  padding: 16px 32px;
  
  font-size: 18px;
  font-weight: 600;
  
  &.btn-square {
    padding: 16px;
  }
}

.btn-small {
  padding: 8px 16px;
  
  font-size: 18px;
  font-weight: 500;
  
  
  &.btn-square {
    padding: 8px;
  }
}