@import "src/assets/styles/colors";

.price-filter {
  .inputs {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 25px;
    
    .price-input-group {
      display: flex;
      flex-direction: column;
      position: relative;
      max-width: 175px;
      border-radius: 5px;
      
      &.input-focus {
        box-shadow: 0 0 0 1.5px $primary-color;
      }
      
      &.input-error {
        box-shadow: 0 0 0 1.5px $danger-color;
      }
      
      .price-input-prepend {
        display: flex;
        position: absolute;
        top: 20px;
        left: 20px;
        bottom: 20px;
        font-size: 18px;
        font-weight: 500;
        color: $gray-light-color;
      }
      
      input {
        border-radius: 5px;
        width: 100%;
        padding: 20px 20px 20px 60px;
        background: $dark-secondary-color;
        font-size: 18px;
        font-weight: 600;
        color: $light-color;
      }
    }
  }
  
  .input-range {
    height: 1rem;
    position: relative;
    width: 100%;
    
    .input-range__label {
      display: none;
      color: $light-color;
      font-family: inherit;
      font-size: 16px;
      transform: translateZ(0);
      white-space: nowrap;
      
      &.input-range__label--min {
        bottom: -1.4rem;
        position: absolute;
        left: 0;
      }
      
      &.input-range__label--max {
        right: 0;
        bottom: -1.4rem;
        position: absolute;
      }
      
      &.input-range__label--value {
        position: absolute;
        top: -1.8rem;
      }
      
      .input-range__label-container {
        left: -50%;
        position: relative;
      }
    }
    
    .input-range__track {
      background: $secondary-color;
      border-radius: 0.3rem;
      cursor: pointer;
      height: 10px;
      display: block;
      position: relative;
      transition: left 0.1s ease-out, width 0.1s ease-out;
      
      &.input-range__track--background {
        left: 0;
        margin-top: -0.15rem;
        position: absolute;
        right: 0;
        top: 50%;
      }
      
      &.input-range__track--active {
        background: $primary-color;
      }
      
      .input-range__slider-container {
        transition: left 0.1s ease-out;
        
        .input-range__slider {
          appearance: none;
          background: transparent url("arrow-slider.svg") no-repeat;
          border: none;
          border-radius: 0;
          cursor: pointer;
          display: block;
          height: 20px;
          margin-left: -0.5rem;
          margin-top: 0;
          outline: none;
          position: absolute;
          top: 50%;
          transition: transform 0.1s ease-out, box-shadow 0.1s ease-out;
          width: 20px;
        }
      }
    }
  }
}