@import "../../assets/styles/colors.scss";
@import "../../assets/styles/breakpoints.scss";

.auth-page.non-mobile {
  display: flex;
  align-items: center;
  justify-content: center;
  
  .auth-form {
    width: 510px;
    height: 610px;
    padding: 45px 65px;
    display: flex;
    align-items: center;
    flex-direction: column;
    border-radius: 5px;
    text-align: center;
    background: $dark-color;
    
    .auth-form-head {
      .auth-form-head-title {
        margin-bottom: 20px;
        font-size: 36px;
        font-weight: 700;
      }
      
      .auth-form-head-description {
        width: 320px;
        margin-bottom: 40px;
        font-size: 18px;
        font-weight: 500;
        color: #6D7681;
        
        span {
          color: $primary-color;
        }
      }
    }
    
    .auth-form-body {
      width: 100%;
      
      .steam-button {
        width: 100%;
        background: $success-color;
        user-select: none;
        
        &:hover {
          background: #69D753;
        }
        
        &:active {
          background: #50BF3A;
        }
        
        span {
          margin-right: 10px;
          
          font-size: 18px;
          font-weight: 600;
        }
        
        svg {
          height: 21px;
          width: 21px;
          border-radius: 50%;
          
          .st0 {
            fill: $dark-color;
          }
          
          .st1 {
            fill: $light-color;
          }
        }
      }
      
      .auth-form-body-divider {
        margin: 22px 0;
        
        font-size: 18px;
        font-weight: 500;
        
        color: #6D7681;
      }
      
      .auth-form-body-inputs {
        .input {
          width: 100%;
          
          margin-bottom: 20px;
          
          border-radius: 5px;
          
          &.input-focus {
            box-shadow: 0 0 0 1.5px $primary-color;
          }
          
          &.input-error {
            box-shadow: 0 0 0 1.5px $danger-color;
          }
          
          &.input-success {
            box-shadow: 0 0 0 1.5px $success-color;
          }
          
          &:last-child {
            margin-bottom: 0;
          }
        }
        
        input {
          width: 100%;
          
          padding: 19px;
          
          border-radius: 5px;
          
          background: #0F141B;
        }
      }
      
      .auth-form-button {
        width: 100%;
        margin-top: 45px;
        
        &.disabled {
          background: rgba($primary-color, 0.55);
        }
        
        &.active {
          background: #4A99EB !important;
        }
      }
    }
  }
}

.auth-page {
  &.mobile {
    .auth-page-inner {
      display: flex;
      align-items: center;
      justify-content: center;
      background: $dark-color;
      padding: 30px;
      
      .auth-page-form {
        width: 100%;
        height: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        
        .auth-form-head {
          display: flex;
          flex-direction: column;
          align-items: center;
          justify-content: center;
          
          .auth-form-head-title {
            font-size: 24px;
            font-weight: 700;
            color: $light-color;
            margin-bottom: 15px;
            text-align: center;
          }
          
          .auth-form-head-description {
            width: 100%;
            margin-bottom: 30px;
            color: $gray-light-color;
            font-size: 14px;
            font-weight: 500;
            text-align: center;
          }
        }
        
        .auth-form-body {
          width: 100%;
          display: flex;
          flex-direction: column;
          align-items: center;
          justify-content: center;
          
          .steam-button {
            width: 100%;
            user-select: none;
          }
          
          .auth-form-body-divider {
            margin: 15px 0;
            font-size: 16px;
            font-weight: 500;
            color: $gray-light-color;
          }
  
          .auth-form-body-inputs {
            width: 100%;
            
            .input {
              width: 100%;
              margin-bottom: 10px;
              border-radius: 5px;
      
              &.input-focus {
                box-shadow: 0 0 0 1.5px $primary-color;
              }
      
              &.input-error {
                box-shadow: 0 0 0 1.5px $danger-color;
              }
      
              &:last-child {
                margin-bottom: 0;
              }
            }
    
            input {
              width: 100%;
              padding: 15px;
              border-radius: 5px;
              background: $dark-secondary-color;
              font-size: 18px;
              font-weight: 500;
            }
          }
  
          .auth-form-button {
            width: 100%;
            margin-top: 20px;
    
            &.disabled {
              background: rgba($primary-color, 0.55);
            }
    
            &.active {
              background: #4A99EB !important;
            }
          }
        }
      }
    }
  }
}

@media (max-width: $breakpoint-tablet) {
  .auth-page.non-mobile {
    width: 100%;
    
    .auth-form {
      width: 100%;
      height: 100%;
      padding: 30px;
      border-radius: 0;
      
      .auth-form-head {
        .auth-form-head-title {
          font-size: 24px;
          font-weight: 700;
          color: $light-color;
          margin-bottom: 15px;
        }
        
        .auth-form-head-description {
          font-size: 14px;
          font-weight: 500;
          color: $gray-light-color;
          text-align: center;
          line-height: 1.3;
        }
      }
      
      .auth-form-body {
        .steam-button {
          max-height: 50px;
        }
        
        .auth-form-body-divider {
          font-size: 16px;
          font-weight: 500;
          color: $gray-light-color;
        }
        
        .auth-form-body-inputs {
          .input {
            &:not(:last-child) {
              margin-bottom: 10px;
            }
            
            input {
              max-height: 50px;
            }
          }
        }
        
        .auth-form-button {
          margin-top: 20px;
        }
      }
    }
  }
}

