$breakpoint-tablet: 768px;
$breakpoint-laptop-small: 1024px;
$breakpoint-laptop-large: 1200px;
$breakpoint-desktop: 1600px;

@import "./src/assets/styles/colors.scss";

.faq-page {
  &.mobile {
    display: flex;
    flex-direction: column;
    align-items: center;
    
    .faq-block {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      width: 100%;
      background: $dark-color;
      border-radius: 5px;
      padding: 30px;
      transition: 500ms cubic-bezier(0, 0, 1, 1);
      
      &:not(:last-child) {
        margin-bottom: 20px;
      }
      
      &.closed {
        max-height: 92px;
        transition: 500ms cubic-bezier(0, 0, 1, 1);
        
        .faq-block-title {
          margin: 0;
          transition: 500ms cubic-bezier(0, 0, 1, 1);
        }
        
        .faq-block-question {
          overflow: hidden;
          margin: 0 !important;
        }
        
        .faq-block-title {
          .btn {
            svg {
              transform: rotate(90deg);
            }
          }
        }
      }
      
      .collapse-button {
        width: 50px;
        height: 50px;
        
        &.open {
          svg {
            transform: rotate(90deg);
          }
        }
        
        & > svg {
          width: 25px;
          height: 14px;
          transition: 500ms cubic-bezier(0.1, 0.1, 0.25, 1);
          transform: rotate(-90deg);
          fill: #FFFFFF;
        }
      }
      
      &.faq-attention {
        .faq-block-title {
          margin-bottom: 15px;
          color: #FFFFFF;
          font-size: 24px;
          font-weight: 700;
          justify-content: center;
        }
        
        .faq-block-description {
          line-height: 20px;
          font-size: 14px;
          font-weight: 500;
          color: $gray-light-color;
        }
        
        .faq-attention-contact {
          width: 100%;
          margin-top: 15px;
          
          .btn {
            width: 100%;
          }
          
          .discord-info {
            font-size: 14px;
            font-weight: 500;
            margin: 15px 0;
            display: block;
            
            .attention-text {
              font-weight: 600;
            }
          }
          
          .btn {
            position: relative;
            
            .icon {
              position: absolute;
              top: 0;
              left: 0;
              height: 54px;
              width: 40px;
              background: $primary-color;
              border-radius: 5px 0 0 5px;
              
              svg {
                width: 40px;
                height: 54px;
                
                .st0 {
                  fill: $primary-color;
                }
              }
            }
          }
        }
      }
      
      .faq-block-body {
        overflow: hidden;
        transition: 550ms cubic-bezier(0, 0, 1, 1) max-height;
      }
      
      .faq-block-title {
        display: flex;
        align-items: center;
        justify-content: space-between;
        width: 100%;
        font-size: 24px;
        font-weight: 700;
        color: #6D7681;
        
        .btn {
          display: flex;
          align-items: center;
          justify-content: center;
          width: 28px;
          height: 28px;
          
          &.open {
            svg {
              transform: rotate(90deg);
            }
          }
          
          svg {
            min-width: 14px;
            width: 14px;
            height: 14px;
            transform: rotate(-90deg);
            fill: $light-color;
          }
        }
      }
      
      .faq-block-question {
        position: relative;
        margin-top: 20px;
        line-height: 15px;
        
        &:not(:last-child) {
          margin-bottom: 10px;
        }
        
        .question {
          margin-bottom: 10px;
          font-size: 16px;
          font-weight: 600;
          color: $light-color;
        }
        
        .answer {
          font-size: 14px;
          font-weight: 500;
          color: #6D7681;
          line-height: 130%;
        }
      }
    }
  }
  
  &.tablet {
    position: relative;
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    
    .faq-container {
      display: flex;
    }
    
    .faq-questions {
      width: 100%;
      margin-left: 15px;
    }
    
    .faq-navigation {
      display: flex;
      align-items: flex-start;
      flex-direction: column;
      max-width: 300px;
      height: 100%;
      max-height: calc(100% - 270px);
      padding: 50px 25px;
      background: #191E28;
      border-radius: 5px;
      
      .divider-h {
        width: 100%;
        height: 1px;
        margin-bottom: 50px;
        background: #272E3D;
      }
      
      .support-button {
        width: 100%;
      }
      
      .faq-navigation-block {
        &:not(:last-child) {
          margin-bottom: 50px;
        }
        
        .faq-navigation-block-title {
          margin-bottom: 30px;
          font-size: 22px;
          font-weight: 700;
          line-height: 30px;
          color: #6D7681;
        }
        
        .faq-navigation-block-links ul {
          display: flex;
          flex-direction: column;
          
          .faq-navigation-block-link {
            font-size: 16px;
            font-weight: 500;
            line-height: 30px;
            color: $light-color;
            
            &:not(:last-child) {
              margin-bottom: 15px;
            }
            
            &.scroll-active {
              position: relative;
              color: #4A99EB;
              
              &:before {
                position: absolute;
                top: 10px;
                left: -20px;
                content: "";
                border-top: 6px solid transparent;
                border-bottom: 6px solid transparent;
                border-left: 10px solid;
                border-left-color: #4A99EB;
              }
            }
          }
        }
      }
    }
    
    .faq-block {
      display: flex;
      flex-direction: column;
      overflow: hidden;
      width: 100%;
      max-height: 100%;
      margin-bottom: 20px;
      padding: 20px 25px;
      background: #191E28;
      border-radius: 5px;
      transition: 500ms cubic-bezier(0, 0, 1, 1);
      
      &:last-child {
        margin-bottom: 0;
      }
      
      &.faq-attention {
        .faq-block-title {
          justify-content: flex-start !important;
          margin-bottom: 20px;
          color: #FFFFFF;
        }
      }
      
      .faq-block-body {
        overflow: hidden;
        transition: 550ms cubic-bezier(0, 0, 1, 1) max-height;
      }
      
      .faq-block-title {
        display: flex;
        align-items: center;
        justify-content: space-between;
        width: 100%;
        font-size: 26px;
        font-weight: 700;
        color: #6D7681;
  
        .collapse-button {
          width: 50px;
          height: 50px;
          
          &.open {
            svg {
              transform: rotate(90deg);
            }
          }
    
          & > svg {
            width: 25px;
            height: 14px;
            transition: 500ms cubic-bezier(0.1, 0.1, 0.25, 1);
            transform: rotate(-90deg);
            fill: $light-color;
          }
        }
        
        .icon {
          display: flex;
          align-items: center;
          justify-content: center;
          width: 34px;
          height: 34px;
          margin-right: 20px;
          
          &.open {
            svg {
              transform: rotate(90deg);
            }
          }
          
         & > svg {
            width: 34px;
            height: 34px;
            transition: 500ms cubic-bezier(0.1, 0.1, 0.25, 1);
            fill: $primary-color;
          }
        }
      }
      
      .faq-block-description {
        font-size: 16px;
        font-weight: 500;
        line-height: 20px;
        
        color: #6D7681;
      }
      
      .faq-block-question {
        position: relative;
        margin-top: 20px;
        margin-bottom: 30px;
        line-height: 30px;
        
        &:last-child {
          margin-bottom: 0;
        }
        
        &:not(:last-child):after {
          position: absolute;
          content: "";
          width: 100%;
          height: 1px;
          margin-top: 20px;
          background: #272E3D;
        }
        
        .question {
          margin-bottom: 25px;
          font-size: 22px;
          font-weight: 700;
          line-height: 30px;
          color: #FFFFFF;
        }
        
        .answer {
          font-size: 16px;
          font-weight: 500;
          line-height: 20px;
          color: #6D7681;
        }
      }
    }
  }
  
