  &.fullscreen {
    position: relative;
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    
    .faq-container {
      display: flex;
    }
    
    .faq-questions {
      width: 100%;
      margin-left: 20px;
    }
    
    .faq-navigation {
      display: flex;
      align-items: flex-start;
      flex-direction: column;
      width: 440px;
      max-width: 430px;
      height: 100%;
      max-height: calc(100% - 270px);
      padding: 50px 50px 50px 50px;
      background: #191E28;
      border-radius: 5px;
      
      .divider-h {
        width: 100%;
        height: 1px;
        margin-bottom: 50px;
        background: #272E3D;
      }
      
      .support-button {
        width: 100%;
      }
      
      ul {
        &:not(:last-child) {
          margin-bottom: 50px;
        }
        
        .faq-navigation-block {
          .faq-navigation-block-title {
            margin-bottom: 30px;
            font-size: 24px;
            font-weight: 700;
            line-height: 30px;
            color: #6D7681;
          }
          
          .scroll-active {
            position: relative;
            color: #4A99EB;
            
            &:before {
              position: absolute;
              top: 10px;
              left: -20px;
              content: "";
              border-top: 6px solid transparent;
              border-bottom: 6px solid transparent;
              border-left: 10px solid;
              border-left-color: #4A99EB;
            }
          }
          
          .faq-navigation-block-links ul {
            display: flex;
            flex-direction: column;
            
            .faq-navigation-block-link {
              margin: 15px 0;
              font-size: 18px;
              font-weight: 500;
              line-height: 30px;
              color: $light-color;
              
              &:first-child {
                margin-top: 0;
              }
              
              &:last-child {
                margin-bottom: 0;
              }
            }
          }
        }
      }
    }
    
    .faq-block {
      display: flex;
      flex-direction: column;
      overflow: hidden;
      width: 100%;
      max-height: 100%;
      margin-bottom: 20px;
      padding: 50px;
      background: #191E28;
      border-radius: 5px;
      transition: 500ms cubic-bezier(0, 0, 1, 1);
      
      &:last-child {
        margin-bottom: 0;
      }
      
      &.closed {
        max-height: 142px;
        transition: 500ms cubic-bezier(0, 0, 1, 1);
        
        .faq-block-title {
          margin: 0;
          transition: 500ms cubic-bezier(0, 0, 1, 1);
        }
        
        .faq-block-question {
          //display: none;
          overflow: hidden;
        }
      }
      
      .collapse-button {
        width: 50px;
        height: 50px;
        
        &.open {
          svg {
            transform: rotate(90deg);
          }
        }
        
        & > svg {
          width: 25px;
          height: 14px;
          transition: 500ms cubic-bezier(0.1, 0.1, 0.25, 1);
          transform: rotate(-90deg);
          fill: #FFFFFF;
        }
      }
      
      &.closed {
        .collapse-button {
          svg {
            transform: rotate(90deg);
          }
        }
      }
      
      &.faq-attention {
        .faq-block-title {
          justify-content: flex-start !important;
          margin-bottom: 20px;
          color: #FFFFFF;
        }
      }
      
      .faq-block-body {
        overflow: hidden;
        transition: 550ms cubic-bezier(0, 0, 1, 1) max-height;
      }
      
      .faq-block-title {
        display: flex;
        align-items: center;
        justify-content: space-between;
        width: 100%;
        font-size: 36px;
        font-weight: 700;
        color: #6D7681;
        
        .icon {
          display: flex;
          align-items: center;
          justify-content: center;
          width: 34px;
          height: 34px;
          
          margin-right: 20px;
          
          fill: #4A99EB;
          
          svg {
            width: 34px;
            height: 34px;
          }
        }
      }
      
      .faq-block-description {
        font-size: 18px;
        font-weight: 500;
        line-height: 30px;
        
        color: #6D7681;
      }
      
      .faq-block-question {
        position: relative;
        margin-top: 20px;
        margin-bottom: 80px;
        line-height: 30px;
        
        &:last-child {
          margin-bottom: 0;
        }
        
        &:not(:last-child):after {
          position: absolute;
          content: "";
          width: 100%;
          
          height: 1px;
          margin-top: 40px;
          
          background: #272E3D;
        }
        
        .question {
          margin-bottom: 25px;
          
          font-size: 24px;
          font-weight: 700;
          
          color: #FFFFFF;
        }
        
        .answer {
          font-size: 18px;
          font-weight: 500;
          color: #6D7681;
        }
      }
    }
  }
}

/* LAPTOP (1024 - 1200) */
@media (max-width: $breakpoint-laptop-large) {
  .faq {
    flex-direction: column;
    
    .faq-navigation {
      width: 100%;
      
      margin-bottom: 50px;
      
      .faq-block {
        width: 100%;
      }
    }
  }
}