@import "./src/assets/styles/colors";
@import "./src/assets/styles/breakpoints";

.transactions-page {
  
  .dropdown-menu {
    z-index: 9998;
  }
  
  &.mobile {
    width: 100%;
    
    .filters {
      .filter.game {
        width: 100%;
        
        .orders-game-selector {
          width: 100%;
          
          .orders-game-selector--selected-game {
            width: 100%;
          }
        }
      }
    }
  }
  
  &.tablet-small {
    width: 100%;
    
    .transactions-block {
      display: flex;
      flex-direction: column;
      width: 100%;
      padding: 50px;
      background: $dark-color;
      border-radius: 5px;
      
      .transactions-header {
        display: flex;
        align-items: center;
        flex-direction: column;
        justify-content: center;
        margin-bottom: 50px;
  
        .title {
          font-size: 24px;
          font-weight: 700;
          color: $light-color;
          margin-bottom: 20px;
        }
  
        .filters {
          display: flex;
          align-items: center;
          flex-direction: row;
          justify-content: center;
          width: 100%;
    
          .game {
            width: 100%;
          }
    
          .orders-game-selector {
            width: 100%;
      
            .orders-game-selector--selected-game {
              width: 100%;
            }
          }
    
          .filter {
            &:not(:last-child) {
              margin-right: 20px;
            }
      
            .select {
              width: 100%;
            }
          }
        }
      }
      
      .transactions-content {
        display: flex;
        align-items: center;
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: center;
        width: 100%;
        overflow: hidden;
      }
    }
  }
  
  &.tablet-large {
    width: 100%;
    
    .transactions-block {
      display: flex;
      flex-direction: column;
      width: 100%;
      padding: 50px;
      background: $dark-color;
      border-radius: 5px;
      
      .transactions-header {
        display: flex;
        align-items: center;
        flex-direction: column;
        justify-content: center;
        margin-bottom: 50px;
  
        .title {
          font-size: 24px;
          font-weight: 700;
          color: $light-color;
          margin-bottom: 20px;
        }
  
        .filters {
          display: flex;
          align-items: center;
          flex-direction: row;
          justify-content: center;
          width: 100%;
    
          .filter {
            width: 100%;
      
            &:not(:last-child) {
              margin-right: 20px;
            }
      
            .orders-game-selector {
              width: 100%;
        
              .orders-game-selector--selected-game {
                width: 100%;
              }
            }
      
            .select {
              width: 100%;
            }
          }
        }
      }
      
      .transactions-content {
        display: flex;
        align-items: center;
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: center;
        width: 100%;
        overflow: hidden;
      }
    }
  }
  
  &.laptop {
    width: 100%;
    
    .transactions-block {
      display: flex;
      flex-direction: column;
      width: 100%;
      padding: 50px;
      background: $dark-color;
      border-radius: 5px;
      
      .transactions-header {
        display: flex;
        align-items: center;
        flex-direction: column;
        justify-content: center;
        margin-bottom: 50px;
        
        .title {
          font-size: 24px;
          font-weight: 700;
          color: $light-color;
          margin-bottom: 20px;
        }
        
        .filters {
          display: flex;
          align-items: center;
          flex-direction: row;
          justify-content: center;
          width: 100%;
          
          .filter {
            width: 100%;
  
            &:not(:last-child) {
              margin-right: 20px;
            }
  
            .orders-game-selector {
              width: 100%;
    
              .orders-game-selector--selected-game {
                width: 100%;
              }
            }
  
            .select {
              width: 100%;
            }
          }
        }
      }
      
      .transactions-content {
        display: flex;
        align-items: center;
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: center;
        width: 100%;
        overflow: hidden;
      }
    }
  }
  
  &.desktop_large-desktop {
    width: 100%;
    padding: 0 50px;
    
    .transactions-block {
      display: flex;
      flex-direction: column;
      width: 100%;
      padding: 50px;
      background: $dark-color;
      border-radius: 5px;
      
      .transactions-header {
        display: flex;
        align-items: center;
        flex-direction: row;
        
        .title {
          font-size: 36px;
          font-weight: 700;
          white-space: nowrap;
          color: $light-color;
        }
        
        .filters {
          display: flex;
          align-items: center;
          flex-direction: row;
          margin-left: auto;
          
          .filter {
            .orders-game-selector {
              width: 100%;
    
              .orders-game-selector--selected-game {
                width: 100%;
              }
            }
  
            &:not(:last-child) {
              margin-right: 20px;
            }
  
            &.status {
              max-width: 150px;
    
              .dropdown-menu {
                min-width: 100%;
              }
            }
  
            &.method {
              width: 250px;
              min-width: 250px;
    
              .dropdown-menu {
                min-width: 100%;
              }
            }
          }
        }
      }
      
      .transactions-content {
        display: flex;
        align-items: center;
        flex-direction: column;
        justify-content: center;
        margin-top: 40px;
      }
    }
  }
  
  &.large-desktop {
    width: 100%;
    padding: 0 50px;
  
    .tooltip-container {
      display: flex;
      align-items: center;
      flex-direction: column;
      justify-content: center;
      min-width: 150px;
      padding: 10px 25px;
      background: $secondary-color;
      border: 0;
      border-radius: 5px;
      opacity: 1;
    
      .sticker-title {
        margin-bottom: 10px;
        font-size: 18px;
        font-weight: 600;
        color: $light-color;
      }
    
      .sticker-wear {
        font-size: 18px;
        font-weight: 600;
        color: $gray-light-color;
      }
    
      .tooltip-arrow {
        &:before {
          border-color: transparent transparent $secondary-color transparent
        }
      
        &:after {
          border-color: transparent transparent $secondary-color transparent;
        }
      }
    }
  
    .transactions-block {
      display: flex;
      flex-direction: column;
      width: 100%;
      padding: 50px;
      background: $dark-color;
      border-radius: 5px;
    
      .transactions-header {
        display: flex;
        align-items: center;
        flex-direction: row;
        
        .title {
          font-size: 36px;
          font-weight: 700;
          color: $light-color;
        }
        
        .filters {
          display: flex;
          align-items: center;
          flex-direction: row;
          margin-left: auto;
  
          .filter {
            .orders-game-selector {
              width: 100%;
      
              .orders-game-selector--selected-game {
                width: 100%;
              }
            }
    
            &:not(:last-child) {
              margin-right: 20px;
            }
    
            &.game {
              width: 150px;
            }
    
            &.status {
              width: 250px;
              min-width: 250px;
              max-width: 250px;
      
              .dropdown-menu {
                min-width: 100%;
              }
            }
    
            &.method {
              width: 250px;
              min-width: 250px;
      
              .dropdown-menu {
                min-width: 100%;
              }
            }
          }
        }
      }
      
      .transactions-content {
        display: flex;
        align-items: center;
        flex-direction: column;
        justify-content: center;
        margin-top: 40px;
      }
    }
  }
  
  &.fullscreen {
    width: 100%;
    padding: 0 50px;
    
    .transactions-block {
      display: flex;
      flex-direction: column;
      width: 100%;
      padding: 50px;
      background: $dark-color;
      border-radius: 5px;
      
      .transactions-header {
        display: flex;
        align-items: center;
        flex-direction: row;
        
        .title {
          font-size: 36px;
          font-weight: 700;
          color: $light-color;
        }
        
        .filters {
          display: flex;
          align-items: center;
          flex-direction: row;
          margin-left: auto;
          
          .select-dropdown-menu {
            display: flex;
            flex-direction: row;
            align-items: center;
            color: $light-color;
            font-size: 18px;
            font-weight: 600;
            min-width: 280px;
            position: relative;
            
            .select-dropdown--head {
              display: flex;
              flex-direction: row;
              align-items: center;
              cursor: pointer;
              width: 100%;
              padding: 20px;
              border-radius: 5px;
              background: $secondary-color;
              
              .select-dropdown--head-title {
                display: flex;
                flex-direction: row;
                align-items: center;
              }
              
              .select-dropdown--input {
                width: 100%;
              }
              
              .select-dropdown--controls {
                display: flex;
                flex-direction: row;
                align-items: center;
                margin-left: auto;
                
                .select-dropdown--remove {
                  margin-left: 20px;
                  border-right: 6px solid transparent;
                  border-left: 6px solid transparent;
                  border-top: 7px solid;
                  border-top-color: $light-color;
                }
                
                .select-dropdown--arrow {
                  margin-left: 20px;
                  border-right: 6px solid transparent;
                  border-left: 6px solid transparent;
                  border-top: 7px solid;
                  border-top-color: $light-color;
                }
              }
            }
            
            .select-dropdown--items {
              position: absolute;
              top: 70px;
              height: 300px;
              overflow-y: scroll;
              display: flex;
              flex-direction: column;
              width: 100%;
              z-index: 999;
              border-radius: 5px;
              background: $secondary-color;
              
              .select-dropdown--item {
                display: flex;
                flex-direction: row;
                align-items: center;
                padding: 20px;
                cursor: pointer;
                
                &.selected {
                  background: $primary-color;
                  
                  &:hover {
                    background: $primary-color;
                  }
                }
                
                &:hover {
                  background: $dark-secondary-color;
                }
              }
            }
          }
          
          .dropdown-menu {
            display: flex;
            flex-direction: row;
            align-items: center;
            color: $light-color;
            font-size: 18px;
            font-weight: 600;
            min-width: 280px;
            position: relative;
            
            .dropdown-menu--head {
              display: flex;
              flex-direction: row;
              align-items: center;
              cursor: pointer;
              width: 100%;
              padding: 20px;
              border-radius: 5px;
              background: $secondary-color;
              
              .dropdown-menu--title {
                display: flex;
                flex-direction: row;
                align-items: center;
                user-select: none;
              }
              
              .dropdown-menu--controls {
                display: flex;
                flex-direction: row;
                align-items: center;
                margin-left: auto;
                
                .dropdown-menu--reset {
                  margin-right: 10px;
                  display: flex;
                  flex-direction: row;
                  align-items: center;
                  transform: rotate(45deg);
                  
                  &:hover {
                    svg {
                      fill: $primary-color;
                    }
                  }
                  
                  svg {
                    pointer-events: none;
                    width: 14px;
                    height: 14px;
                    fill: $light-color;
                  }
                }
                
                .dropdown-menu--arrow {
                  border-right: 6px solid transparent;
                  border-left: 6px solid transparent;
                  border-top: 7px solid;
                  border-top-color: $light-color;
                  
                  &.active {
                    border-top-color: $primary-color;
                  }
                }
              }
            }
            
            .dropdown-menu--options {
              position: absolute;
              top: 70px;
              max-height: 310px;
              overflow-y: auto;
              display: flex;
              flex-direction: column;
              width: 100%;
              z-index: 999;
              border-radius: 5px;
              background: $secondary-color;
              
              .dropdown-menu--option {
                display: flex;
                flex-direction: row;
                align-items: center;
                padding: 20px;
                cursor: pointer;
                color: $gray-light-color;
                user-select: none;
                
                &.selected {
                  background: $primary-color;
                  color: $light-color;
                  
                  &:hover {
                    background: darken($primary-color, 5%);
                  }
                }
                
                &:hover {
                  background: darken($secondary-color, 5%);
                }
              }
            }
          }
          
          .filter {
            &:not(:last-child) {
              margin-right: 40px;
            }
  
            &.game {
              width: 150px;
    
              .orders-game-selector {
                width: 100%;
              }
            }
          }
        }
      }
      
      .transactions-content {
        display: flex;
        align-items: center;
        flex-direction: column;
        justify-content: center;
        margin-top: 40px;
      }
    }
    
    .item-sticker {
      .__react_component_tooltip {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        max-width: 280px;
        min-width: 150px;
        padding: 15px;
        border-radius: 5px;
        background: $secondary-color;
        font-size: 14px;
        font-weight: 600;
        white-space: normal;
        opacity: 1;
        
        &.place-top {
          &:after {
            border-top-color: $secondary-color;
          }
        }
        
        &.place-left {
          &:after {
            border-left-color: $secondary-color;
          }
        }
        
        &.place-bottom {
          &:after {
            border-bottom-color: $secondary-color;
          }
        }
        
        &.place-right {
          &:after {
            border-right-color: $secondary-color;
          }
        }
  
        .sticker-title {
          font-size: 18px;
          font-weight: 600;
          color: $light-color;
          margin-bottom: 10px;
        }
  
        .sticker-wear {
          font-size: 18px;
          font-weight: 600;
          color: $gray-light-color;
        }
      }
    }
    
    .tooltip-container {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      min-width: 150px;
      padding: 10px 25px;
      background: $secondary-color;
      border-radius: 5px;
      border: 0;
      opacity: 1;
      
      .sticker-title {
        font-size: 18px;
        font-weight: 600;
        color: $light-color;
        margin-bottom: 10px;
      }
      
      .sticker-wear {
        font-size: 18px;
        font-weight: 600;
        color: $gray-light-color;
      }
      
      .tooltip-arrow {
        &:before {
          border-color: transparent transparent $secondary-color transparent
        }
        
        &:after {
          border-color: transparent transparent $secondary-color transparent;
        }
      }
    }
  }
}

@media (max-width: $breakpoint-tablet) {
  .transactions-page {
    padding: 0;
    
    .transactions-block {
      flex-direction: column;
      padding: 30px;
      
      .transactions-header {
        flex-direction: column;
        margin-bottom: 20px;
        
        .title {
          font-size: 24px;
          font-weight: 700;
          margin-bottom: 20px;
        }
        
        .filters {
          flex-direction: column;
          align-content: flex-start;
          width: 100%;
          
          .filter {
            width: 100%;
            
            &:not(:last-child) {
              margin-bottom: 10px;
              margin-right: 0;
            }
            
            max-width: 100%;
          }
        }
      }
      
      .transactions-content {
        margin-top: 0;
      }
    }
  }
}

