@import "./src/assets/styles/colors";

.tippy-popper[x-placement="bottom"] {
  .arrow-regular {
    position: absolute;
    top: -7px;
    content: "";
    width: 0;
    height: 0;
    border-right: 9px solid transparent;
    border-bottom: 7px solid #272E3D;
    border-left: 9px solid transparent;
  }
}

.tippy-popper[x-placement="top"] {
  .arrow-regular {
    position: absolute;
    bottom: -7px;
    content: "";
    width: 0;
    height: 0;
    border-top: 7px solid #272E3D;
    border-right: 9px solid transparent;
    border-left: 9px solid transparent;
  }
}


.checkbox-indeterminate {
  display: flex;
  align-items: center;
  flex-direction: row;
  justify-content: center;
  margin-bottom: 20px;
  user-select: none;
  
  &.active {
    .check-box {
      background: #4A99EB;
    }
  }
  
  &.indeterminate {
    .check-box {
      background: #4BC334;
    }
  }
  
  .check-box {
    position: relative;
    width: 23px;
    min-width: 23px;
    height: 23px;
    min-height: 23px;
    margin: 0 15px 0 0;
    cursor: pointer;
    background: #191E28;
    border: 1px solid #6D7681;
    border-radius: 5px;
    outline: 0;
    transition: background 175ms cubic-bezier(0.1, 0.1, 0.25, 1);
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
  }
  
  span {
    cursor: pointer;
    font-size: 18px;
    font-weight: 700;
    color: #6D7681;
  }
}

.market-page {
  &.mobile {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 100%;
    
    &.hide-sidebar {
      .sidebar-filter {
        transform: translateX(-100%);
      }
    }
    
    .sidebar-filter {
      position: fixed;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      z-index: 10000;
      overflow-y: scroll;
      width: 100%;
      max-width: 100%;
      height: 100%;
      min-height: 100vh;
      margin: 0;
      padding: 30px;
      
      .control {
        display: flex;
        align-items: center;
        flex-direction: row;
        justify-content: center;
        width: 100%;
        margin-top: -30px;
        padding: 30px;
        cursor: pointer;
        font-size: 16px;
        font-weight: 600;
        color: $gray-light-color;
        
        &:hover {
          color: $danger-color;
          
          svg {
            fill: $danger-color;
          }
        }
        
        svg {
          width: 16px;
          height: 16px;
          margin-left: 15px;
          fill: $gray-light-color;
        }
      }
      
      .inputs {
        display: flex;
        flex-direction: column;
        
        .price-input-group, .lock-input-group {
          width: 100%;
          max-width: 100%;
          
          &:first-child {
            margin-bottom: 10px;
          }
        }
      }
      
      .exterior-inputs {
        flex-direction: column;
        
        .exterior-input-group {
          width: 100%;
          max-width: 100%;
          
          &:first-child {
            margin-bottom: 10px;
          }
        }
      }
      
      .stickers-filter--stickers {
        padding: 0;
        
        .sticker {
          img, svg {
            width: 80px;
            height: 80px;
          }
          
          &:not(:last-child) {
            margin-right: 20px;
          }
        }
      }
    }
    
