              height: 19px;
              margin-right: 10px;
              fill: $primary-color;
            }
          }
        }
        
        .right {
          .view-btns {
            display: flex;
            align-items: center;
            flex-direction: row;
            justify-content: center;
            margin-left: 50px;
            
            .list-btn {
              position: relative;
              display: flex;
              align-items: center;
              justify-content: center;
              margin-right: 20px;
              cursor: pointer;
              
              svg {
                width: 25px;
                height: 25px;
                fill: $gray-light-color;
              }
            }
            
            .card-btn {
              position: relative;
              display: flex;
              align-items: center;
              justify-content: center;
              cursor: pointer;
              
              svg {
                width: 25px;
                height: 25px;
                fill: $gray-light-color;
              }
              
              &.active {
                &:after {
                  position: absolute;
                  top: -37px;
                  right: 0;
                  left: 0;
                  content: "";
                  height: 3px;
                  background: $primary-color;
                }
                
                svg {
                  fill: $light-color;
                }
              }
            }
          }
        }
      }
      
      .items-block {
        .items {
          display: flex;
          flex-wrap: wrap;
          width: 100%;
          
          .item {
            margin-bottom: 35px;
          }
        }
      }
    }
  }
}