@import "src/assets/styles/colors";

.date-selector {
  display: flex;
  flex-direction: row;
  align-items: center;
  position: relative;
  
  .date-selector-tab {
    color: $gray-light-color;
    font-size: 18px;
    font-weight: 500;
    cursor: pointer;
    user-select: none;
    
    &.active {
      color: $primary-color;
    }
    
    &:not(:last-child) {
      padding-right: 20px;
    }
  }
}