@import "../../../assets/styles/colors.scss";

.checkbox {
  input[type="checkbox"] {
    position: relative;
    width: 23px;
    min-width: 23px;
    height: 23px;
    min-height: 23px;
    margin: 0 15px 0 0;
    border-radius: 5px;
    border: 1px solid $gray-light-color;
    background: $dark-color;
    appearance: none;
    outline: 0;
    cursor: pointer;
    transition: background 175ms cubic-bezier(0.1, 0.1, 0.25, 1);

    &:before {
      content: "";
      position: absolute;
      display: block;
      opacity: 0;
    }

    &:checked {
      border-color: $primary-color;
      background: $primary-color;

      &::before {
        opacity: 1;
        border-style: solid;
        border-color: #FFFFFF;
        border-width: 0 3px 3px 0;
        transform: rotate(45deg);
        top: 3px;
        left: 7px;
        width: 5px;
        height: 10px;
      }

      ~ span, ~ label {
        color: $light-color;
      }
    }
    
    &:indeterminate {
      border-color: $primary-color;
      background: $primary-color;

      &::before {
        opacity: 1;
        content: "+";
        top: -8px;
        bottom: 0;
        right: 0;
        left: 2px;
        font-size: 32px;
      }

      ~ span, ~ label {
        color: $light-color;
      }
    }
  }

  label {
    position: relative;

    cursor: pointer;
    font-size: 18px;
    font-weight: 500;
    user-select: none;

    display: flex;
    flex-direction: row;
    align-items: center;
    
    color: $gray-light-color;

    span {
      color: $gray-light-color;
    }
  }
}