@import "../../../assets/styles/colors";

.payment-service {
  width: 100%;
  height: 60px;
  padding: 15px 25px !important;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  border-radius: 5px;
  background: $secondary-color;
  cursor: pointer;
  user-select: none;
  white-space: nowrap;
  
  .payment-icon {
    margin-right: 20px;
    display: flex;
    align-items: center;
    justify-content: center;
    
    svg, img {
      width: 34px;
      height: 34px;
    }
  }
  
  .payment-name {
    font-size: 20px;
    font-weight: 600;
    color: $gray-light-color;
  }
  
  &.active {
    background: #2C3443;
    box-shadow: 0 0 0 1px inset #4A99EB;
    
    .payment-name {
      color: $light-color;
    }
  }
}