@import "src/assets/styles/colors";

.orders-game-selector {
  position: relative;
  z-index: 9998 !important;
  display: flex;
  align-items: center;
  flex-direction: row;
  justify-content: center;
  width: 125px;
  height: 60px;
  margin-right: 30px;
  -webkit-user-select: none;
  user-select: none;
  font-size: 18px;
  font-weight: 600;
  color: #FFFFFF;
  
  .orders-game-selector--selected-game {
    display: flex;
    align-items: center;
    flex-direction: row;
    justify-content: center;
    width: 125px;
    height: 60px;
    cursor: pointer;
    background: #272E3D;
    border-radius: 5px;
    
    svg {
      width: 70px;
      height: 30px;
      fill: $light-color;
    }
  
    g {
      fill: $light-color;
    }
  
    polyline, path, rect {
      stroke: $light-color;
    }
  }
  
  .orders-game-selector--games {
    position: absolute;
    top: 70px;
    display: flex;
    flex-direction: column;
    overflow-y: auto;
    width: 100%;
    max-height: 310px;
    background: #272E3D;
    border-radius: 5px;
    
    .orders-game-selector--game {
      display: flex;
      align-items: center;
      flex-direction: row;
      justify-content: center;
      padding: 20px;
      cursor: pointer;
      -webkit-user-select: none;
      user-select: none;
      color: #6D7681;
      
      svg {
        width: 70px;
        height: 30px;
        fill: #6D7681;
        stroke: #6D7681;
      }
  
      g {
        fill: $gray-light-color;
      }
  
      polyline, path, rect {
        stroke: $gray-light-color;
      }
  
      &.selected {
        color: #FFFFFF;
        background: #4A99EB;
        
        svg, g, path {
          fill: #FFFFFF;
        }
      }
    }
  }
}