@import "src/assets/styles/colors";

.orders-form-item-search-quantity {
  min-width: 120px;
  max-width: 150px;
  margin-right: 15px;
  background: $dark-secondary-color;
  height: 60px;
  padding: 15px 20px;
  border-radius: 5px;
  font-size: 18px;
  font-weight: 500;
  display: flex;
  flex-direction: row;
  align-items: center;
  
  &.input-focus {
    box-shadow: 0 0 0 1.5px $primary-color;
  }
  
  &.input-error {
    box-shadow: 0 0 0 1.5px $danger-color;
  }
}