@import "../../../../assets/styles/colors";

.switch-button {
  display: flex;
  align-items: center;
  flex-direction: row;
  width: 100%;
  font-size: 18px;
  border-radius: 5px;
  cursor: pointer;
  user-select: none;
  
  & > div {
    text-align: center;
    
    &:first-child {
      width: 100%;
      padding: 15px 30px;
      border-radius: 5px 0 0 5px;
    }
    
    &:last-child {
      width: 100%;
      padding: 15px 30px;
      border-radius: 0 5px 5px 0;
    }
  }
  
  .switch-button--active {
    background: $primary-color;
    color: $light-color;
    font-weight: 700;
  }
  
  .switch-button--inactive {
    background: $secondary-color;
    color: $gray-light-color;
    font-weight: 600;
  }
}