@import "src/assets/styles/colors";

.cart-item.mobile {
  position: relative;
  display: flex;
  flex-direction: column;
  white-space: nowrap;
  border-radius: 5px;
  margin-bottom: 10px;
  min-width: 260px;
  padding: 20px;
  background: #212734;
  transition: 375ms cubic-bezier(0.1, 0.1, 0.25, 1), z-index 0s 175ms;
  
  .cart-item--stickers {
    position: absolute;
    top: 20px;
    right: 20px;
    display: flex;
    flex-direction: column;
    
    .item-sticker {
      display: flex;
      flex-direction: row;
      align-items: center;
      justify-content: center;
      
      &:not(:last-child) {
        margin-bottom: 0px;
      }
      
      img, svg {
        width: 32px;
        height: 32px;
      }
      
      svg {
        fill: $gray-light-color;
      }
      
      .__react_component_tooltip {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        padding: 20px;
        border-radius: 5px;
        background: $secondary-color;
        font-size: 14px;
        font-weight: 600;
        white-space: normal;
        opacity: 1;
        
        &.place-top {
          &:after {
            border-top-color: $secondary-color;
          }
        }
        
        &.place-left {
          &:after {
            border-left-color: $secondary-color;
          }
        }
        
        &.place-bottom {
          &:after {
            border-bottom-color: $secondary-color;
          }
        }
        
        &.place-right {
          &:after {
            border-right-color: $secondary-color;
          }
        }
        
        .sticker-title {
          font-size: 14px;
          font-weight: 600;
          color: $light-color;
          margin-bottom: 10px;
        }
        
        .sticker-wear {
          font-size: 14px;
          font-weight: 600;
          color: $gray-light-color;
        }
      }
    }
  }
  
  .cart-item--info {
    .cart-item--main-name {
      font-size: 14px;
      font-weight: 500;
      color: $gray-light-color;
      margin-bottom: 10px;
    }
    
    .cart-item--secondary-name {
      font-size: 16px;
      font-weight: 600;
      color: $light-color;
    }
  }
  
  .cart-item--image {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 140px;
    height: 140px;
    
    img {
      width: auto;
      max-width: 140px;
      height: auto;
    }
  }
  
  .cart-item--additional {
    display: flex;
    flex-direction: row;
    align-items: center;
    margin-bottom: 20px;
    
    .cart-item--lock-status {
      display: flex;
      flex-direction: row;
      align-items: center;
      margin-right: 15px;
      
      span {
        font-size: 16px;
        font-weight: 600;
        margin-left: 5px;
      }
      
      &.unlocked {
        svg {
          fill: $success-color;
        }
      }
      
      &.locked {
        span {
          color: $danger-color;
        }
        
        svg {
          fill: $danger-color;
        }
      }
      
      svg {
        width: 16px;
        height: 16px;
        fill: $gray-light-color;
      }
    }
    
    .cart-item--tag {
      display: flex;
      flex-direction: row;
      align-items: center;
      
      svg {
        width: 16px;
        height: 16px;
        fill: $light-color;
      }
    }
    
    .cart-item--float {
      display: flex;
      flex-direction: row;
      align-items: center;
      margin-left: auto;
      font-size: 16px;
      font-weight: 600;
      color: $gray-light-color;
    }
  }
  
  .cart-item--price, .cart-item--remove {
    display: flex;
    flex-direction: row;
    align-items: center;
    
    .cart-item--our-price {
      display: flex;
      flex-direction: row;
      align-items: center;
      font-size: 24px;
      font-weight: 600;
      color: $light-color;
      
      span.cart-item--discount {
        display: flex;
        align-items: center;
        justify-content: center;
        margin-left: 20px;
        padding: 3px 6px;
        font-size: 14px;
        background: $danger-color;
        border-radius: 5px;
      }
    }
    
    .cart-item--remove-item {
      display: flex;
      flex-direction: row;
      align-items: center;
      justify-content: center;
      margin-left: auto;
      
      svg {
        width: 24px;
        height: 24px;
        fill: $danger-color;
      }
    }
  }
}