@import "src/assets/styles/colors";

.content-tabs {
  display: flex;
  flex-direction: row;
  align-items: center;
  position: relative;
  
  .content-tab {
    color: $gray-light-color;
    font-size: 24px;
    font-weight: 700;
    cursor: pointer;
    user-select: none;
    
    &.active {
      color: $light-color;
      
      &:before {
        position: absolute;
        top: -50px;
        content: "";
        width: 30px;
        height: 4px;
        background: $primary-color;
        border-radius: 5px;
      }
    }
    
    &:not(:last-child) {
      margin-right: 15px;
    }
  }
}