@import "../../../assets/styles/colors.scss";

.select {
  width: 370px;

  position: relative;

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;

  background: $secondary-color;
  border-radius: 5px;
  cursor: pointer;
  //z-index: 9997;

  border-bottom: 1px solid $secondary-color;

  &.toggle {
    & {
      border-bottom-right-radius: 0;
      border-bottom-left-radius: 0;
    }

    .select-menu {
      border-top-left-radius: 0;
      border-top-right-radius: 0;
    }
    &.select-with-input {
      .select-toggle {
        span {
          display: none;
        }

        span.close {
          display: flex;
        }

        .select-input {
          display: block;
        }
      }
    }

    .select-menu {
      display: flex;
    }
  }

  .select-toggle {
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding: 19px;

    span {
      font-size: 18px;
      font-weight: 600;

      color: $light-color;

      .count {
        color: $gray-light-color;
      }
    }

    .select-input {
      display: none;
    }
  }

  .select-menu {
    width: 100%;

    position: absolute;
    top: 60px;

    display: none;
    flex-direction: column;
    align-items: center;

    background: $secondary-color;
    border-radius: 5px;
    z-index: 1;

    .select-item {
      width: 100%;

      padding: 20px;

      font-size: 18px;
      font-weight: 600;

      .checkbox {
        input {
          border: none;
        }
      }
    }
  }

  &.select-with-input {
    .select-menu {
      top: 80px;
    }
  }
}