$breakpoint-tablet: 768px;
$breakpoint-laptop-small: 1024px;
$breakpoint-laptop-large: 1200px;
$breakpoint-desktop: 1600px;
$breakpoint-desktop-fullscreen: 1850px;

@import "./src/assets/styles/colors";

.payment-service svg {
  .g2a-1 {
    fill: #056AAB;
  }
  
  .g2a-2 {
    fill: #26A2DC;
  }
  
  .g2a-3 {
    fill: #F68B00;
  }
  
  .g2a-4 {
    fill: #E74011;
  }
  
  .mastercard-0 {
    fill: #CC0001;
  }
  
  .mastercard-1 {
    fill: #FE9900;
  }
  
  .yd-0 {
    fill: #EF7F19;
  }
  
  .yd-1 {
    fill: #272E3D;
  }
  
  .yd-2 {
    fill: #272E3D;
  }
  
  .webmoney-0 {
    fill: #036CB5;
  }
  
  .qiwi-0, .qiwi-1 {
    fill: #FF8C00;
  }
  
  .paypal-0 {
    fill: #019CDE;
  }
  
  .paypal-1 {
    fill: #003085;
  }
  
  .dota-0 {
    fill: #E82D3A;
  }
  
  .crypto-0 {
    fill: #A951F3;
  }
  
  .crypto-1 {
    fill: #FFFFFF;
  }
}

.balance-page {
  .btn-primary {
    &.btn-primary {
      &:disabled {
        background: rgba($primary-color, 0.55);
      }
    }
  }
  
  &.mobile {
    background: $dark-color;
    width: 100%;
    padding: 30px;
    display: flex;
    flex-direction: column;
    align-items: center;
    
    .balance-page-title {
      font-size: 24px;
      font-weight: 700;
      text-align: center;
      width: 100%;
      margin-bottom: 30px;
    }
    
    .balance-page-body {
      width: 100%;
      
      .switch-button {
        margin-bottom: 20px;
      }
      
      .dropdown-menu {
        width: 100%;
        &:not(:last-child) {
          margin-bottom: 20px;
        }
      }
      
      .input-group {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        
        &:not(:last-child) {
          margin-bottom: 20px;
        }
        
        .input-group-title {
          font-size: 18px;
          font-weight: 500;
          color: $light-color;
          margin-bottom: 10px;
        }
        
        .input-group-description {
          margin-top: -5px;
          font-size: 16px;
          font-weight: 500;
          color: $gray-light-color;
          margin-bottom: 10px;
        }
        
        .amount-input {
          display: flex;
          flex-direction: column;
          position: relative;
          width: 100%;
          border-radius: 5px;
          
          &.input-focus {
            box-shadow: 0 0 0 1.5px $primary-color;
          }
          
          &.input-error {
            box-shadow: 0 0 0 1.5px $danger-color;
          }
          
          .amount-input--prepend {
            display: flex;
            position: absolute;
            top: 20px;
            left: 20px;
            bottom: 20px;
            font-size: 18px;
            font-weight: 500;
            color: $gray-light-color;
          }
          
          input {
            border-radius: 5px;
            width: 100%;
            padding: 20px 20px 20px 40px;
            background: $dark-secondary-color;
            font-size: 18px;
            font-weight: 600;
            color: $light-color;
          }
        }
        
        .promo-code-input {
          display: flex;
          flex-direction: column;
          position: relative;
          width: 100%;
          border-radius: 5px;
          
          &.input-focus {
            box-shadow: 0 0 0 1.5px $primary-color;
          }
          
          &.input-success {
            box-shadow: 0 0 0 1.5px $success-color;
          }
          
          &.input-error {
            box-shadow: 0 0 0 1.5px $danger-color;
          }
          
          .promo-code-input--prepend {
            display: flex;
            position: absolute;
            top: 20px;
            left: 20px;
            bottom: 20px;
            font-size: 18px;
            font-weight: 500;
            color: $gray-light-color;
          }
          
          input {
            border-radius: 5px;
            width: 100%;
            padding: 20px;
            background: $dark-secondary-color;
            font-size: 18px;
            font-weight: 600;
            color: $light-color;
          }
        }
        
        &.confirmation-input-group {
          display: none;
          
          &.show {
            display: flex;
          }
        }
        
        .input-group-content {
          display: flex;
          flex-direction: row;
          flex-wrap: wrap;
          width: calc(100% + 10px);
          margin: -5px;
          
          .balance-amount-button {
            display: flex;
            width: auto;
            flex: 1 1 auto;
            margin: 5px;
            padding: 15px 30px;
            font-size: 16px;
            
            &.btn-secondary {
              color: $gray-light-color;
            }
          }
        }
        
        .confirmation-input {
          display: flex;
          flex-direction: column;
          position: relative;
          width: 100%;
          border-radius: 5px;
          
          &.input-focus {
            box-shadow: 0 0 0 1.5px $primary-color;
          }
          
          &.input-success {
            box-shadow: 0 0 0 1.5px $success-color;
          }
          
          &.input-error {
            box-shadow: 0 0 0 1.5px $danger-color;
          }
          
          .confirmation-input--prepend {
            display: flex;
            position: absolute;
            top: 20px;
            left: 20px;
            bottom: 20px;
            font-size: 18px;
            font-weight: 500;
            color: $gray-light-color;
          }
          
          input {
            border-radius: 5px;
            width: 100%;
            padding: 20px;
            background: $dark-secondary-color;
            font-size: 18px;
            font-weight: 600;
            color: $light-color;
          }
        }
      }
      
      .balance-attention {
        border-radius: 5px;
        padding: 15px;
        color: $gray-light-color;
        border: 1px solid $secondary-color;
        font-size: 16px;
        font-weight: 500;
        margin: 20px 0;
      }
      
      .balance-sum-block {
        margin: 40px 0;
        position: relative;
        
        &:before, &:after {
          content: "";
          position: absolute;
          width: 100%;
          height: 1px;
          min-height: 1px;
          background: $secondary-color;
        }
        
        &:before {
          top: -20px;
        }
        
        &:after {
          bottom: -20px;
        }
        
        .balance-block-row {
          display: flex;
          flex-direction: row;
          align-items: center;
          justify-content: space-between;
          font-size: 18px;
          font-weight: 500;
          color: $gray-light-color;
          
          &:not(:last-child) {
            margin-bottom: 20px;
          }
          
          .balance-block-row-num {
            font-weight: 700;
          }
          
          &.bonus {
            .balance-block-row-num {
              font-weight: 600;
            }
          }
          
          &.sum-to-pay {
            color: $light-color;
          }
        }
      }
      
      .btn {
        width: 100%;
      }
    }
  }
  
