      display: flex;
      flex-direction: row;
      justify-content: space-between;
      border-radius: 5px;
      background: #191E28;
    }
    
    .balance-block {
      width: 100%;
      
      .balance-row {
        display: flex;
        
        .dropdown-menu {
          min-width: 300px;
        }
      }
      
      &.payment-services {
        width: auto;
        
        .payment-services {
          display: flex;
          flex-direction: row;
        }
        
        .payment-service {
          &:not(:last-child) {
            margin-right: 15px;
          }
        }
      }
      
      &.action-part {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        
        .bottom-row {
          display: flex;
          align-items: flex-end;
          justify-content: space-between;
        }
        
        .balance-row {
          &.title {
            margin-bottom: 50px;
          }
        }
        
        .balance-block-title {
          display: flex;
          flex-direction: row;
          align-items: center;
          justify-content: center;
          font-size: 36px;
          font-weight: 700;
          line-height: 40px;
          cursor: pointer;
          user-select: none;
          
          .balance-btn {
            &.deposit {
              color: $gray-light-color;
            }
            
            &.withdraw {
              color: $light-color;
            }
          }
          
          &.active {
            .balance-btn {
              &.deposit {
                color: $light-color;
              }
              
              &.withdraw {
                color: $gray-light-color;
              }
            }
          }
          
          .balance-divider {
            margin: 0 20px;
            width: 5px;
            height: 30px;
            background: $primary-color;
