            width: 100%;
            border-radius: 5px;
            
            &.input-focus {
              box-shadow: 0 0 0 1.5px $primary-color;
            }
            
            &.input-success {
              box-shadow: 0 0 0 1.5px $success-color;
            }
            
            &.input-error {
              box-shadow: 0 0 0 1.5px $danger-color;
            }
            
            input {
              width: 100%;
              height: 74px;
              border-radius: 5px;
              padding: 22px;
              background: $dark-secondary-color;
              font-size: 20px;
              font-weight: 600;
              color: $light-color;
            }
          }
          
          .phone-number-input {
            width: auto;
          }
          
          input {
            width: 230px;
            height: 74px;
            padding: 22px;
            -webkit-border-radius: 5px;
            -moz-border-radius: 5px;
            border-radius: 5px;
            color: $gray-light-color;
            font-size: 20px;
            font-weight: 600;
          }
        }
        
