              color: $gray-light-color;
            }
          }
        }
        
        .balance-input-title, .balance-button-title {
          margin-bottom: 30px;
          font-size: 18px;
          font-weight: 500;
          color: #6D7681;
        }
        
        .balance-attention {
          width: 100%;
          display: flex;
          flex-direction: column;
          justify-content: flex-end;
          
          .button-outline {
            width: 100%;
            height: 74px;
            padding: 20px 30px;
            display: flex;
            align-items: center;
            border: 2px solid #272E3D;
            border-radius: 5px;
            font-size: 18px;
            font-weight: 600;
            color: #6D7681;
            
            .icon {
              margin-right: 20px;
              display: flex;
              align-items: center;
              
              svg {
                width: 40px;
                height: 40px;
                fill: #4A99EB;
              }
            }
          }
        }
        
        .sum {
          display: flex;
          align-items: center;
          margin-right: 0;
          margin-bottom: 15px;
          
          .title {
            font-size: 24px;
            font-weight: 500;
            color: $light-color;
          }
          
          .amount {
            display: flex;
            align-items: center;
            margin-left: auto;
            font-size: 36px;
            font-weight: 700;
            
            .currency {
              margin-right: 10px;
              
              color: #4A99EB;
            }
          }
        }
        
        .confirmation {
          display: flex;
          flex-direction: row;
          align-items: flex-end;
          
          .balance-input-title {
            display: flex;
            flex-direction: row;
            align-items: center;
            width: 100%;
            justify-content: space-between;
          }
          
          .icon {
            width: 28px;
            height: 28px;
            display: flex;
            align-content: center;
            justify-content: center;
            line-height: 28px;
            border-radius: 50%;
            background: #272E3D;
            font-size: 18px;
            font-weight: 700;
          }
        }
