          &.confirmation-input-group {
            margin-bottom: 0 !important;
            display: none;
            
            &.show {
              display: flex;
            }
          }
          
          .confirmation-input {
            display: flex;
            flex-direction: column;
            position: relative;
            width: 100%;
            border-radius: 5px;
            
            &.input-focus {
              box-shadow: 0 0 0 1.5px $primary-color;
            }
            
            &.input-success {
              box-shadow: 0 0 0 1.5px $success-color;
            }
            
            &.input-error {
              box-shadow: 0 0 0 1.5px $danger-color;
            }
            
            input {
              width: 100%;
              height: 74px;
              border-radius: 5px;
              padding: 22px;
              background: $dark-secondary-color;
              font-size: 20px;
              font-weight: 600;
              color: $light-color;
            }
          }
          
          .phone-number-input {
            width: auto;
          }
          
          input {
            width: 300px;
            height: 74px;
            padding: 22px;
            -webkit-border-radius: 5px;
            -moz-border-radius: 5px;
            border-radius: 5px;
            color: $gray-light-color;
            font-size: 20px;
            font-weight: 600;
          }
        }
        
        .balance-buttons-group {
          display: flex;
          flex-direction: row;
          margin: -5px;
          
          .balance-amount-button {
            flex: 1 1 auto;
            height: 74px;
            font-size: 24px;
            font-weight: 700;
            margin: 5px;
            
            &.btn-secondary {
              color: $gray-light-color;
            }
          }
        }
        
        .balance-input-title, .balance-button-title {
          margin-bottom: 30px;
          font-size: 18px;
          font-weight: 500;
          color: $gray-light-color;
        }
        
        .balance-attention {
          width: 100%;
          display: flex;
          flex-direction: column;
          justify-content: flex-end;
          
          .button-outline {
            width: 100%;
            height: 74px;
            padding: 20px 30px;
            display: flex;
            align-items: center;
            border: 2px solid $secondary-color;
            font-size: 18px;
            font-weight: 600;
            color: $gray-light-color;
            
            .icon {
              margin-right: 20px;
              display: flex;
              align-items: center;
              
              svg {
                width: 40px;
                height: 40px;
                fill: $primary-color;
              }
            }
          }
        }
        
        .sum {
          display: flex;
          align-items: center;
          margin-right: 0;
          margin-bottom: 15px;
          
          .title {
            font-size: 24px;
            font-weight: 500;
            color: $light-color;
          }
          
          .amount {
            margin-left: 50px;
            display: flex;
            align-items: center;
            font-size: 36px;
            font-weight: 700;
            
            .currency {
              margin-right: 10px;
              color: $primary-color;
            }
          }
        }
        
        .confirmation {
          display: flex;
          flex-direction: row;
          align-items: flex-end;
          margin-left: 150px;
          margin-right: auto;
          
          .balance-input-title {
            display: flex;
            flex-direction: row;
            align-items: center;
            justify-content: space-between;
            width: 100%;
          }
          
          .icon {
            width: 28px;
            height: 28px;
            display: flex;
            align-content: center;
            justify-content: center;
            line-height: 28px;
            border-radius: 50%;
            background: $secondary-color;
            font-size: 18px;
            font-weight: 700;
          }
        }
      }
    }
  }
}

/* PHONES */
@media (max-width: $breakpoint-tablet) {

}

/* TABLETS (768 - 1024) */
@media (max-width: $breakpoint-laptop-small) {
  .balance-page {
    .balance-block {
      &.action-part {
        .balance-block {
          .balance-row {
            flex-direction: column !important;
          }
        }
      }
    }
  }
}

/* LAPTOP (1200 - 1600) */
@media (max-width: $breakpoint-desktop) {
  .balance-page {
    flex-direction: column;
    
    .balance-block {
      width: 100%;
      
      margin-right: 0;
      
      
      &.payment-services {
        width: 100%;
        
        margin-right: 0;
        margin-bottom: 50px;
        
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        
        .payment-service {
          width: 85px;
          
          margin-right: 25px;
          
          .payment-name {
            display: none;
          }
        }
      }
      
      &.action-part {
        
        .balance-block {
          .balance-col {
            width: 100%;
            
            margin-right: 0;
            margin-bottom: 50px;
          }
          
          .balance-row {
            flex-direction: row;
            flex-wrap: wrap;
            
            .sum {
              justify-content: space-between;
            }
            
            .confirmation {
              margin-bottom: 0;
              
              justify-content: space-between;
              
              .balance-col, .deposit-button {
                margin: 0;
              }
              
              .deposit-button {
                width: 225px;
              }
              
              .balance-input {
                width: 315px;
              }
              
            }
            
            .balance-buttons-group {
              flex-wrap: wrap;
              
              .balance-amount-button {
                margin-right: 15px;
                margin-bottom: 15px;
              }
            }
          }
        }
        
        .balance-attention .button-outline {
          height: auto;
        }
      }
    }
  }
  
  
}

/* DESKTOP */
@media (max-width: $breakpoint-desktop-fullscreen) {
  .balance-page {
    .balance-block {
      &.payment-services {
        width: auto;
        margin-right: 60px;
        
        .payment-service {
          width: 85px;
          
          .payment-name {
            display: none;
          }
        }
      }
    }
  }
}