@import "src/assets/styles/colors";

.item-image-slider-image {
  padding: 10px 5px;
  cursor: pointer;
  width: 300px;
  position: relative;
  user-select: none;
  
  &:hover {
    .cover {
      display: flex;
    }
  }
  
  .cover {
    width: calc(100% - 10px);
    height: calc(100% - 24px);
    position: absolute;
    margin: 10px 5px;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    display: none;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    background: rgba($secondary-color, 0.7);
    
    svg {
      width: 50px;
      height: 50px;
      fill: $light-color;
    }
  }
  
  img {
    max-width: 100%;
    height: auto;
    border-radius: 5px;
  }
}