@import "./src/assets/styles/colors";

.orders-page {
  position: relative;
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
  
  &.mobile {
    .containers {
      display: flex;
      align-items: flex-start;
      flex-direction: column;
      
      .active-orders {
        margin-bottom: 20px;
      }
      
      .row {
        display: flex;
        flex-direction: row;
        width: 100%;
      }
      
      .column {
        display: flex;
        flex-direction: column;
        width: 100%;
      }
    }
    
    .pagination {
      display: flex;
      align-items: center;
      flex-direction: row;
      justify-content: center;
      width: 100%;
      margin: 30px auto 0 auto;
      user-select: none;
      
      
      .pagination-item-info {
        font-size: 16px;
        font-weight: 600;
        color: $gray-light-color;
      }
      
      .pagination-item {
        display: flex;
        align-items: center;
        flex-direction: row;
        justify-content: center;
        width: 48px;
        height: 48px;
        cursor: pointer;
        font-size: 16px;
        font-weight: 700;
        color: $light-color;
        background: #1A1E29;
        border-radius: 5px;
        transition: 0.25s;
        
        &:hover {
          background: lighten(#1A1E29, 5%);
        }
        
        &:active {
          background: darken(#1A1E29, 5%);
        }
        
        
        &.pagination-item-arrow {
        
        }
        
        &.active {
          background: $primary-color;
          
          &:hover {
            background: darken($primary-color, 10%);
          }
          
          &:active {
            background: darken($primary-color, 10%);
          }
        }
        
        &.pagination-item-arrow-active {
          background: $primary-color;
          
          &:hover {
            background: darken($primary-color, 10%);
          }
          
          &:active {
            background: darken($primary-color, 5%);
          }
        }
        
        svg {
          width: 18px;
          height: 18px;
          fill: $light-color;
        }
        
        &:not(:last-child) {
          margin-right: 15px;
        }
        
        &:first-child {
          margin-right: 30px;
          
          svg {
            transform: rotate(180deg);
          }
        }
        
        &:last-child {
          margin-left: 15px;
        }
      }
    }
    
    .orders-container {
      position: relative;
      display: flex;
      flex-direction: column;
      justify-content: center;
      padding: 30px;
      background: $dark-color;
      
      &:not(:last-child) {
        margin-bottom: 20px;
      }
      
      &.orders-history {
        display: flex;
        flex-direction: column;
        margin-top: 20px;
        padding: 0 0 50px 0;
        
        .orders-history-top {
          display: flex;
          align-items: center;
          flex-direction: row;
          flex-wrap: wrap;
          width: 100%;
          //height: 125px;
          margin-bottom: 20px;
          padding: 30px;
          white-space: nowrap;
          background: #212734;
          border-radius: 5px;
          
          .block {
            display: flex;
            align-items: center;
            flex-direction: row;
            justify-content: center;
            width: 100%;
            
            &:not(:last-child) {
              margin-bottom: 20px;
            }
          }
          
          .sidebar-filter-dropdown-menu {
            position: relative;
            display: flex;
            align-items: center;
            flex-direction: column;
            width: 100%;
            height: 60px;
            font-size: 18px;
            font-weight: 600;
            color: $light-color;
            
            .dropdown-menu--head {
              display: flex;
              align-items: center;
              flex-direction: row;
              width: 100%;
              padding: 20px;
              cursor: pointer;
              background: $secondary-color;
              border-radius: 5px;
              
              .dropdown-menu--title {
                display: flex;
                align-items: center;
                flex-direction: row;
                user-select: none;
                
                span {
                  margin-left: 5px;
                  color: $gray-light-color;
                }
              }
              
              .dropdown-menu--controls {
                display: flex;
                align-items: center;
                flex-direction: row;
                margin-left: auto;
                
                .dropdown-menu--reset {
                  display: flex;
                  align-items: center;
                  flex-direction: row;
                  margin-right: 10px;
                  transform: rotate(45deg);
                  
                  &:hover {
                    svg {
                      fill: $primary-color;
                    }
                  }
                  
                  svg {
                    width: 14px;
                    height: 14px;
                    pointer-events: none;
                    fill: $light-color;
                  }
                }
                
                .dropdown-menu--arrow {
                  border-top: 7px solid;
                  border-top-color: $light-color;
                  border-right: 6px solid transparent;
                  border-left: 6px solid transparent;
                  
                  &.active {
                    border-top-color: $primary-color;
                  }
                }
              }
            }
            
            .dropdown-menu--options {
              position: absolute;
              top: 70px;
              z-index: 999;
              display: flex;
              flex-direction: column;
              overflow-y: auto;
              width: 100%;
              max-height: 310px;
              margin-top: 1px;
              background: $secondary-color;
              border-radius: 5px;
              
              .dropdown-menu--option {
                display: flex;
                align-items: center;
                flex-direction: row;
                padding: 20px;
                cursor: pointer;
                user-select: none;
                color: $gray-light-color;
                
                &:hover {
                  background: #232A37;
                }
              }
            }
            
            label {
              display: flex;
              align-items: center;
              flex-direction: row;
              cursor: pointer;
              user-select: none;
              font-size: 18px;
              font-weight: 500;
              color: $gray-light-color;
              
              input[type="checkbox"] {
                position: relative;
                width: 24px;
                min-width: 24px;
                height: 24px;
                min-height: 24px;
                margin: 0 15px 0 0;
                cursor: pointer;
                background: $dark-color;
                border-radius: 5px;
                outline: 0;
                transition: background 175ms cubic-bezier(0.1, 0.1, 0.25, 1);
                appearance: none;
                
                &:before {
                  position: absolute;
                  content: "";
                  display: block;
                  opacity: 0;
                }
                
                &:checked {
                  background: $primary-color;
                  border-color: $primary-color;
                  
                  &::before {
                    top: 4px;
                    left: 8px;
                    width: 5px;
                    height: 10px;
                    border-width: 0 3px 3px 0;
                    border-style: solid;
                    border-color: #FFFFFF;
                    opacity: 1;
                    transform: rotate(45deg);
                  }
                  
                  ~ span, ~ label {
                    color: $light-color;
                  }
                }
                
                &:indeterminate {
                  background: $primary-color;
                  border-color: $primary-color;
                  
                  &::before {
                    top: -8px;
                    right: 0;
                    bottom: 0;
                    left: 2px;
                    content: "+";
                    font-size: 32px;
                    opacity: 1;
                  }
                  
                  ~ span, ~ label {
                    color: $light-color;
                  }
                }
              }
            }
          }
          
          .quality-filter {
            width: 100%;
            min-width: auto;
          }
          
          .price-switcher-filter {
            display: flex;
            align-items: center;
            flex-direction: row;
            justify-content: center;
            width: 100%;
            height: 60px;
            max-height: 60px;
            border-radius: 5px;
            
            &.low {
              path:first-child {
                fill: $light-color;
              }
            }
            
            &.high {
              path:last-child {
                fill: $light-color;
              }
            }
            
            svg {
              width: 24px;
              min-width: 24px;
              height: 24px;
              fill: $gray-light-color;
            }
          }
          
          .orders-game-selector {
          
          }
          
          .filters-game-selector {
            display: flex;
            align-items: center;
            flex-direction: row;
            justify-content: center;
            width: 125px;
            min-width: 125px;
            height: 60px;
            margin-right: 40px;
            background: $secondary-color;
            border-radius: 5px;
            
            img {
              width: 35px;
              height: 35px;
            }
            
          }
          
          .orders-history-input {
            height: 60px;
            padding: 15px;
            background: $secondary-color;
            border-radius: 5px;
            
            &:not(:last-child) {
              margin-right: 40px;
            }
          }
        }
        
        .orders-history-transactions {
          display: flex;
          flex-direction: column;
          overflow-x: scroll;
          width: 100%;
          margin-top: 10px;
          padding-bottom: 20px;
          
          .table {
            display: flex;
            flex-direction: column;
            width: 100%;
            height: 100%;
            
            .orders-history-transactions-card.mobile {
              position: relative;
              display: flex;
              flex-direction: column;
              width: 100%;
              padding: 15px 30px;
              background: $dark-secondary-color;
              
              &:nth-child(odd) {
                background: #171C25;
              }
              
              .block {
                display: flex;
                flex-direction: row;
                
                &:first-child {
                  margin-bottom: 10px;
                }
              }
              
              .item-block {
                font-size: 16px;
                font-weight: 600;
                color: $light-color;
                
                span {
                  font-weight: 500;
                  color: $gray-light-color;
                }
                
                &.item-img {
                  height: 90px;
                  margin-right: 20px;
                  
                  img {
                    width: 100px;
                    height: auto;
                  }
                }
                
                &.item-info {
                  .item-item {
                    margin-bottom: 5px;
                    color: $gray-light-color;
                  }
                  
                  .item-name {
                    margin-bottom: 5px;
                  }
                  
                  .item-game {
                    color: $gray-light-color;
                  }
                }
                
                &.item-info-price {
                  width: 120px;
                  
                  .item-price {
                    margin-bottom: 5px;
                  }
                  
                  .item-quantity {
                    margin-bottom: 5px;
                  }
                }
                
                &.item-order-info {
                  width: 120px;
                  
                  .item-chance {
                    margin-bottom: 5px;
                  }
                }
                
                &.item-action {
                  position: absolute;
                  bottom: 30px;
                  right: 30px;
                  display: flex;
                  align-items: center;
                  flex-direction: row;
                  justify-content: center;
                  width: 30px;
                  height: 30px;
                  background: $danger-color;
                  border-radius: 5px;
                  
                  .trash-icon {
                    width: 100%;
                    height: 100%;
                  }
                  
                  svg {
                    width: 20px;
                    height: 20px;
                    fill: $light-color;
                  }
                }
              }
            }
          }
          
          
          .trash-icon {
            cursor: pointer;
            
            svg {
              width: 24px;
              height: 24px;
              fill: $danger-color;
            }
          }
        }
      }
      
      .orders-container-title {
        margin-bottom: 25px;
        font-size: 20px;
        font-weight: 700;
        color: $light-color;
      }
      
      .orders-form {
        display: flex;
        align-items: flex-start;
        flex-direction: column;
        width: 100%;
        padding: 0;
        border-radius: 5px;
  
        .orders-game-selector {
          width: 100%;
          margin-bottom: 20px;
    
          .orders-game-selector--selected-game {
            width: 100%;
          }
        }
  
        & > span {
          margin-right: 25px;
          margin-left: 50px;
          font-size: 18px;
          font-weight: 500;
          white-space: nowrap;
          color: $gray-light-color;
        }
  
        .autocomplete-input {
          width: 100%;
          margin-bottom: 20px;
    
          input {
            width: 100%;
          }
    
          .autocomplete-input--selected-item {
            width: 100%;
          }
    
          .remove-input-value {
            top: 0;
            right: 0;
            display: flex;
            align-items: center;
            flex-direction: row;
            justify-content: center;
            width: 30px;
            height: 60px;
            background: rgba($danger-color, 0.5);
            border-radius: 0 5px 5px 0;
      
            svg {
              fill: $light-color;
            }
          }
    
          .suggestions-container {
            width: 100%;
          }
        }
  
        .btn {
          width: 100%;
        }
      }
    }
    
    .sidebar-order-container {
      display: flex;
      flex-direction: column;
      width: 100%;
      background: $dark-color;
      border-radius: 5px;
      
      &:not(:last-child) {
        margin-right: 20px;
      }
      
      &.item-container {
        display: flex;
        align-items: center;
        flex-direction: column;
        width: 100%;
        height: 100%;
        margin-bottom: 20px;
        padding: 20px;
        
        .item-img {
          display: flex;
          align-items: center;
          flex-direction: row;
          justify-content: center;
          width: 122px;
          height: 64px;
          margin-bottom: 30px;
          
          img {
            width: auto;
            max-width: 122px;
            height: auto;
          }
        }
        
        .item-name {
          margin-bottom: 30px;
          font-size: 20px;
          font-weight: 600;
          color: $light-color;
        }
        
        .item-exterior {
          margin-bottom: 10px;
          font-size: 16px;
          font-weight: 500;
          color: $gray-light-color;
        }
        
        .item-order-controls {
          display: flex;
          align-items: center;
          flex-direction: row;
          width: 100%;
          margin-bottom: 20px;
        }
        
        .make-order {
          width: 100%;
        }
      }
      
      &.last-orders {
        width: 100%;
        margin-bottom: 20px;
      }
      
      &.last-orders, &.last-sales {
        .sidebar-order-table {
          overflow-x: scroll;
          width: 100%;
          
          tr {
            display: table;
            width: 100%;
          }
          
          tbody {
            display: block;
            height: 250px;
          }
          
          span {
            margin-right: 0;
          }
        }
      }
      
      &.active-orders {
        width: 100%;
        height: 400px;
        
        .sidebar-order-container-title {
          font-size: 20px;
        }
      }
      
      .sidebar-order-container-title {
        display: flex;
        align-items: center;
        flex-direction: row;
        justify-content: space-between;
        padding: 20px;
        font-size: 20px;
        font-weight: 600;
        color: $light-color;
      }
      
      .content-tabs .content-tab.active:before {
        top: -20px;
      }
    }
    
    .empty {
      display: flex;
      align-items: center;
      flex-direction: row;
      justify-content: center;
      padding: 15px;
      font-size: 16px;
      font-weight: 500;
      text-align: center;
      color: $gray-light-color;
    }
    
    .sidebar-order-table {
      display: flex;
      flex-direction: column;
      overflow-x: scroll;
      width: 100%;
      
      &.active-orders {
        .sidebar-order-table-body {
          overflow-y: auto;
          height: 285px;
        }
      }
      
      .sidebar-order-table-row {
        display: flex;
        align-items: center;
        flex-direction: row;
        flex-wrap: nowrap;
        width: 100%;
        padding: 15px 30px;
        
        &:last-child {
          border-radius: 0 0 5px 5px;
        }
      }
      
      .sidebar-order-table-cell {
        flex: 1;
        font-size: 16px;
        font-weight: 500;
        text-align: center;
        white-space: nowrap;
        color: $light-color;
        
        &:first-child {
          text-align: left;
        }
        
        &:last-child {
          text-align: right;
        }
        
        span {
          color: $gray-light-color;
        }
      }
      
      .sidebar-order-table-head {
        display: flex;
        align-items: center;
        flex-direction: row;
        
        .sidebar-order-table-cell {
          font-size: 16px;
          font-weight: 600;
          text-align: center;
          white-space: nowrap;
          color: $gray-light-color;
          
          &:first-child {
            text-align: left;
          }
          
          &:last-child {
            text-align: right;
          }
        }
      }
      
      .sidebar-order-table-body {
        display: flex;
        align-items: center;
        flex-direction: column;
        overflow-y: auto;
        width: 100%;
        height: 250px;
        
        .sidebar-order-table-row {
          background: $dark-color;
          
          &:nth-child(odd) {
            background: #171C25;
          }
        }
      }
    }
    
    table {
      width: 100%;
      border-spacing: 0px;
      border-collapse: collapse;
      
      thead {
        th {
          color: $gray-light-color;
        }
      }
      
      tbody {
        tr:nth-child(odd) {
          background: #171C25;
        }
      }
      
      th, td {
        padding: 15px;
        font-size: 16px;
        font-weight: 500;
        text-align: center;
        white-space: nowrap;
        
        span {
          margin-right: 20px;
          
          color: $gray-light-color;
        }
      }
      
      th:first-child, td:first-child {
        padding-left: 30px;
      }
      
      th:last-child, td:last-child {
        padding-right: 30px;
      }
      
      .trash-icon {
        svg {
          width: 20px;
          height: 20px;
          fill: $danger-color;
        }
      }
    }
    
    .orders-container.orders-history {
      .orders-history-type-switcher {
        display: flex;
        align-items: center;
        flex-direction: row;
        justify-content: center;
        width: 100%;
        
        .orders-history-type {
          position: relative;
          display: flex;
          align-items: center;
          flex-direction: row;
          justify-content: center;
          margin-bottom: 20px;
          cursor: pointer;
          font-size: 24px;
          font-weight: 700;
          color: $gray-light-color;
          
          &:not(:last-child) {
            margin-right: 50px;
          }
          
          &.active {
            font-weight: 700;
            color: $light-color;
            
            &:before {
              position: absolute;
              top: -30px;
              content: "";
              width: 30px;
              height: 4px;
              background: $primary-color;
            }
          }
        }
      }
    }
  }
  
  &.tablet {
    .containers {
      display: flex;
      align-items: flex-start;
      flex-direction: column;
    
      .row {
