      .trash-icon {
        svg {
          fill: $danger-color;
          width: 20px;
          height: 20px;
        }
      }
    }
    
    .orders-container.orders-history {
      .orders-history-type-switcher {
        display: flex;
        align-items: center;
        flex-direction: row;
        justify-content: flex-start;
        margin-right: 50px;
        
        .orders-history-type {
          position: relative;
          display: flex;
          align-items: center;
          flex-direction: row;
          justify-content: center;
          cursor: pointer;
          font-size: 24px;
          font-weight: 700;
          color: $gray-light-color;
          
          &:not(:last-child) {
            margin-right: 30px;
          }
          
          &.active {
            font-weight: 700;
            color: $light-color;
            
            &:before {
              position: absolute;
              top: -48px;
              content: "";
              width: 30px;
              height: 4px;
              background: $primary-color;
            }
          }
        }
      }
    }
  }
}