@import "src/assets/styles/colors";

.image-modal-wrapper {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 10000;
  display: none;
  align-items: center;
  flex-direction: row;
  justify-content: center;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  
  &.active {
    display: flex;
  }
  
  .modal-block {
    position: relative;
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;
    overflow: hidden;
    width: auto;
    background: $dark-color;
    border-radius: 5px;
    
    .close-button {
      position: absolute;
      top: 20px;
      right: 20px;
      cursor: pointer;
      
      &:hover {
        svg {
          fill: $danger-color;
        }
      }
      
      svg {
        width: 20px;
        height: 20px;
        fill: $light-color;
      }
    }
    
    .modal-body {
      display: flex;
      align-items: center;
      flex-direction: column;
      padding: 50px;
      
      img {
        width: calc(1920px / 2);
        height: calc(1080px / 2);
      }
    }
  }
}