@import "./src/assets/styles/breakpoints.scss";
@import "./src/assets/styles/colors.scss";

footer {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  width: 100%;
  max-width: 100% !important;
  height: 100%;
  padding: 40px 50px 40px 50px !important;
  color: #6D7681;
  background: #191E28;
  position: relative;
  z-index: 9997 !important;
  overflow: hidden;
  
  & > .row {
    &.laptop {
      flex-wrap: wrap;
      justify-content: center;
      
      .col {
        width: 100%;
        
        &:not(:last-child) {
          margin-bottom: 25px;
        }
        
        &.links {
          .footer-row {
            .footer-block {
              &:not(:last-child) {
                margin-bottom: 30px;
              }
            }
          }
        }
        
        .footer-row {
          margin-bottom: 0;
          
          .footer-block {
            .footer-title {
              margin-bottom: 20px;
            }
          }
        }
        
        &.social-and-pay {
          justify-content: space-between;
        }
      }
    }
    
    &.desktop-small {
      flex-wrap: wrap;
      justify-content: center;
      
      & > .col {
        width: 100%;
      }
      
      .col {
        &:not(:last-child) {
          margin-bottom: 25px;
        }
        
        &.links {
          flex-direction: row;
          justify-content: space-between;
          
          .footer-row {
            &:last-child {
              text-align: right;
            }
            
            .footer-block {
              &.footer-logo-copyright {
                margin: 0 0 50px 0;
              }
              
              &.design-author {
                margin: 0;
              }
            }
          }
        }
        
        &.social-and-pay {
          justify-content: center;
          flex-direction: row;
          
          .footer-row {
            width: 100%;
            
            .footer-col {
              width: 100%;
              flex-direction: row;
              justify-content: space-between;
            }
          }
        }
        
        .footer-row {
          margin-bottom: 0;
          
          .footer-block {
            .footer-title {
              margin-bottom: 20px;
            }
          }
        }
        
        &.social-and-pay {
          justify-content: space-between;
        }
      }
    }
  
    &.desktop-large {
      flex-wrap: wrap;
      justify-content: center;
    
      & > .col {
        width: 100%;
      }
    
      .col {
        &:not(:last-child) {
          margin-bottom: 25px;
        }
      
        &.links {
          flex-direction: row;
          justify-content: space-between;
        
          .footer-row {
            &:last-child {
              text-align: right;
            }
          
            .footer-block {
              &.footer-logo-copyright {
                margin: 0 0 50px 0;
              }
            
              &.design-author {
                margin: 0;
              }
            }
          }
        }
      
        &.social-and-pay {
          justify-content: center;
          flex-direction: row;
        
          .footer-row {
            width: 100%;
          
            .footer-col {
              width: 100%;
              flex-direction: row;
              justify-content: space-between;
            }
          }
        }
      
        .footer-row {
          margin-bottom: 0;
        
          .footer-block {
            .footer-title {
              margin-bottom: 20px;
            }
          }
        }
      
        &.social-and-pay {
          justify-content: space-between;
        }
      }
    }
  }
  
  & > .row {
    width: 100%;
    max-width: 1920px;
  }
  
  .footer-row {
    display: flex;
    flex-direction: row;
  }
  
  .footer-col {
    display: flex;
    flex-direction: column;
    
    .footer-block {
      margin-bottom: 40px;
    }
  }
  
  .footer-block {
    display: flex;
    flex-direction: column;
    
    &:not(:last-child) {
      margin-right: 75px;
    }
  }
  
  .social-and-pay, .info {
    .footer-row {
      .footer-block {
        margin-right: 0;
      }
    }
    
    .footer-col {
      .footer-block:last-child {
        margin-bottom: 0;
      }
    }
  }
  
  .footer-block.footer-logo-copyright {
    margin-bottom: 50px;
    
    & .logo {
      margin-bottom: 15px;
      
      font-size: 28px;
      font-family: "TT Lakes Condensed Bold Italic", sans-serif;
      letter-spacing: 0.12em;
      
      & > span {
        font-size: 28px;
        
        color: #4A99EB;
      }
    }
    
    span {
      font-size: 18px;
      font-weight: 500;
      
      color: #3E444C;
    }
  }
  
  .footer-block.design-author {
    span {
      font-size: 18px;
      font-weight: 500;
      
      color: #3E444C;
    }
    
    a {
      margin-top: 15px;
      
      font-size: 21px;
      
      color: #6D7681;
    }
  }
  
  .footer-title {
    font-size: 18px;
    font-weight: 600;
    
    color: #FFFFFF;
  }
  
  .footer-list {
    display: flex;
    flex-direction: column;
    
    a {
      &:not(:last-child) {
        margin-bottom: 25px;
      }
      
      color: #6D7681;
    }
  }
  
  .footer-title {
    margin-bottom: 25px;
  }
  
  .footer-list.row {
    flex-direction: row;
  }
  
  .pay-service {
    width: 70px;
    height: 33px;
    
    margin-right: 10px;
    padding: 8px 9px;
    
    display: flex;
    align-items: center;
    justify-content: center;
    
    border-radius: 3.5px;
    
    background: #272E3D;
    
    &:last-child {
      margin-right: 0;
    }
    
    img {
      height: 18px;
    }
    
    svg {
      width: 100%;
      height: 18px;
      fill: gray;
      
      .st0 {
        fill: $secondary-color;
      }
      
      .st1 {
        fill: $gray-light-color;
      }
    }
  }
  
  .social {
    margin-right: 25px;
    
    &:last-child {
      margin-right: 0;
    }
    
    img {
      width: 33px;
      height: 33px;
    }
    
    cursor: pointer;
    
    svg {
      width: 33px;
      height: 33px;
      
      .st0 {
        fill: $secondary-color;
      }
      
      &:hover {
        .st1 {
          fill: $primary-color;
        }
      }
      
      .st1 {
        fill: $gray-light-color;
      }
    }
  }
}

/* LARGE LAPTOPS */
@media (max-width: $breakpoint-laptop-large) {
  footer {
    flex-direction: column;
    justify-content: center;
  }
  
  .col:last-child {
    .footer-row {
      margin-bottom: 0;
    }
  }
  
  .footer-row {
    width: 100%;
    
    margin-bottom: 25px;
    
    justify-content: space-between;
    
    .footer-block {
      margin-right: 0;
    }
  }
  
  .footer-col {
    width: 100%;
  }
  
  .info {
    .footer-row {
      margin-bottom: 0;
    }
    
    .footer-col {
      flex-direction: row;
      justify-content: space-between;
      
      .footer-block {
        margin-bottom: 0;
      }
    }
  }
}

@media (min-width: $breakpoint-laptop-large) and (max-width: $breakpoint-desktop) {
  .footer-block {
    margin-right: 35px;
  }
  
  .col.links {
    width: 550px;
  }
  
  .col.info {
    width: 100%;
    
    margin-top: 50px;
    
    .footer-row {
      margin: 0 auto;
      
      .footer-col {
        flex-direction: row;
        justify-content: center;
        
        .footer-block {
          margin: 0;
          
          &:first-child {
            text-align: end;
          }
          
          &:last-child {
            text-align: left;
          }
          
          &:first-child {
            margin-right: 50px;
          }
          
          &:last-child {
            margin-left: 50px;
          }
        }
      }
    }
  }
}

@media (max-width: $breakpoint-desktop) {
  .footer-row {
    flex-direction: column;
  }
}