@import "../../../../assets/styles/colors";

.subscription-modal-wrapper {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 10000;
  display: none;
  align-items: center;
  flex-direction: row;
  justify-content: center;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  
  &.active {
    display: flex;
  }
  
  .modal-block {
    position: relative;
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;
    overflow: hidden;
    width: 450px;
    min-height: 400px;
    background: $dark-color;
    border-radius: 5px;
    
    .close-button {
      position: absolute;
      top: 20px;
      right: 20px;
      cursor: pointer;
      
      &:hover {
        svg {
          fill: $danger-color;
        }
      }
      
      svg {
        width: 20px;
        height: 20px;
        fill: $light-color;
      }
    }
    
    .modal-body {
      display: flex;
      align-items: center;
      flex-direction: column;
      padding: 50px;
      
      .subscription-icon {
        display: flex;
        align-items: center;
        flex-direction: row;
        justify-content: center;
        width: 60px;
        height: 60px;
        margin-bottom: 30px;
        background: $secondary-color;
        border-radius: 50%;
        
        svg {
          width: 32px;
          height: 32px;
          fill: $light-color;
        }
      }
      
      .subscription-title {
        display: flex;
        align-items: center;
        flex-direction: row;
        justify-content: center;
        margin-bottom: 15px;
        font-size: 24px;
        font-weight: 600;
        color: $light-color;
      }
      
      .subscription-description {
        color: $gray-light-color;
        font-size: 18px;
        font-weight: 500;
        text-align: center;
        margin-bottom: 30px;
      }
      
      .modal-buttons {
        display: flex;
        align-items: center;
        flex-direction: row;
        
        .btn {
          width: 100%;
          
          &:last-child {
            margin-left: 15px;
          }
        }
      }
    }
  }
}