$breakpoint-tablet: 768px;
$breakpoint-laptop-small: 1024px;
$breakpoint-laptop-large: 1200px;
$breakpoint-desktop: 1600px;

.advanced-search-button {
  width: 250px;
  height: 47px;

  button {
    .arrow {
      margin-left: 20px;

      border-right: 6px solid transparent;
      border-left: 6px solid transparent;
      border-top: 7px solid;
      border-top-color: #FFFFFF;
    }
  }

  &.toggle .arrow {
    border-top-color: #4A99EB;
  }

  .advanced-search-filters {
    width: 100%;

    position: absolute;
    top: 220px;
    left: 0;
    z-index: 9998;

    padding: 0 50px;

    display: none;


    & > .filters {
      width: 100%;
      height: 100%;

      padding: 50px;

      border-radius: 5px;
      background: #191E28;

      box-shadow: 0 0 5px 0 rgba(15, 20, 27, 0.75);

      //input {
      //  width: 100%;
      //}

      .row {
        margin-bottom: 50px;

        &:last-child {
          margin-bottom: 0;
        }
      }

      .arrow {
        margin-left: 10px;

        border-right: 6px solid transparent;
        border-left: 6px solid transparent;
        border-top: 7px solid;
        border-top-color: #FFFFFF;
      }

      &.toggle .arrow {
        border-top-color: #4A99EB;
      }

      .game-filter {
        height: 60px;

        margin: 0;

        .game-filter-list {
          top: 80px;
        }
      }

      .advanced-search {
        width: 100%;
        height: 60px;

        padding: 19px;
        margin-right: 80px;

        display: flex;
        align-items: center;

        border-radius: 5px;
        background: #0F141B;

        .icon {
          width: 19px;

          margin-right: 18px;

          fill: #FFFFFF;
        }

      }

      .advanced-filter-title {
        margin-bottom: 30px;

        display: flex;
        align-items: center;
        justify-content: space-between;

        font-size: 21px;
        font-weight: 600;

        color: #FFFFFF;

        .amount {
          display: flex;
          align-items: center;
        }
      }

      .advanced-filter-inputs {
        width: 370px;

        display: flex;
        align-items: center;
        justify-content: space-between;
      }

      .advanced-filter-input {
        width: 175px;

        padding: 20px;
        margin-right: 0px;

        display: flex;
        align-items: center;

        border-radius: 5px;
        background: #0F141B;

        .input-prepend {
          margin-right: 10px;
        }

        .input-append {
          margin-left: 10px;
        }

        input {
          width: calc(100% - 50px);
        }

        span.currency {
          margin-left: 5px;

          color: #FFFFFF;

          font-size: 18px;
          font-weight: 600;
        }

        &:last-child {
          margin-right: 0;
        }

        & > span:first-child {
          margin-right: 5px;
        }

        & > span:last-child {
          margin-left: 5px;
        }

        &.input-days {
          input {
            width: 12px;

            text-align: center;
          }
        }

        &.big-input {
          width: 370px;

          .icon {
            width: 19px;

            margin-right: 18px;

            fill: #FFFFFF;
          }
        }
      }

      .advanced-filter-dropdown {
        width: 370px;
      }
    }
  }

  &.toggle .advanced-search-filters {
    display: flex;
  }

  .multi-select {
    --rmsc-main: #4285f4;
    --rmsc-hover: #f1f3f5;
    --rmsc-selected: #e2e6ea;
    --rmsc-border: #272E3D;
    --rmsc-gray: #aaa;
    --rmsc-bg: #272E3D;
    --rmsc-p: 0px; /* Spacing */
    --rmsc-radius: 5px; /* Radius */
    --rmsc-h: 60px; /* Height */
  }
}

.dropdown-container {
  width: 370px;
  height: 60px;

  padding: 20px;

  position: relative;

  display: flex;
  align-items: center;
  justify-content: space-between;

  border-radius: 5px;

  border: none;

  background: #272E3D;

  & > div.dropdown-arrow {
    margin-left: 10px;
    border-right: 6px solid transparent;
    border-left: 6px solid transparent;
    border-top: 7px solid;
    border-top-color: #FFFFFF;
  }

  &.toggle > div.dropdown-arrow {
    border-top-color: #4A99EB;
  }

  span:first-child {
    font-size: 18px;
    font-weight: 500;
    color: #FFFFFF;
  }

  &.toggle > .dropdown-list {
    display: flex;
  }

  .dropdown-list {
    width: 370px;
    height: 240px;

    position: absolute;
    top: 60px;
    left: 0;

    display: none;
    flex-direction: column;
    align-items: center;
  }
}

.advanced-filter-switcher {
  display: flex;
  align-items: center;

  label {
    cursor: pointer;
  }

  span {
    width: 100%;
    height: 30px;

    margin-left: 20px;

    display: flex;
    align-items: center;

    font-size: 18px;
    font-weight: 500;
  }
}

.button.reset-filters {
  width: auto;
}

@media (min-width: $breakpoint-desktop) {
  .advanced-search-button .advanced-search-filters > .filters {
    padding: 25px;
  }

  .sm-col {
    flex-wrap: wrap;

    .col {
      margin-bottom: 25px;
      margin-right: 25px;
    }
  }
}

@media (max-width: $breakpoint-desktop) {

  .sm-col {
    flex-wrap: wrap;

    .col {
      margin-bottom: 25px;
    }

    .advanced-filter-switcher {
      margin: 0 15px 50px 0;
    }
  }

  .button.reset-filters {
    margin-top: 0 !important;
    width: 100%;
  }

  .advanced-search-button .advanced-search-filters > .filters .advanced-filter-input {
    width: 225px;
  }

  .advanced-search-button .advanced-search-filters > .filters .advanced-filter-input.big-input {
    width: 470px;
  }

  .dropdown-container {
    width: 470px;
  }
}

@media (max-width: $breakpoint-laptop-large) {
  .advanced-search-button .advanced-search-filters > .filters {
    padding: 25px;
  }

  .advanced-search-button .row {
    margin-bottom: 25px !important;
  }

  .sm-col .advanced-filter-switcher {
    margin: 0 0 50px 0;
  }

  .button.reset-filters {
    margin-top: -50px;
  }

  .advanced-search-button .advanced-search-filters > .filters .advanced-filter-input {
    width: 175px;
  }

  .advanced-search-button .advanced-search-filters > .filters .advanced-filter-input.big-input {
    width: 370px;
  }

  .dropdown-container {
    width: 370px;
  }
}

/* TABLET (768 - 1024) */
@media (max-width: $breakpoint-laptop-small) {
  .advanced-search-button .advanced-search-filters > .filters .advanced-filter-inputs {
    flex-direction: column;

    & > .advanced-filter-input {
      margin-top: 20px;
      margin-right: 0;

      &:first-child {
        margin-top: 0;
      }
    }
  }

  .input-cols .col:last-child {
    width: 100%;

    .advanced-filter-inputs {
      width: 100%;

      .big-input {
        width: 100%;
      }
    }
  }

  .button.reset-filters {
    width: 100%;

    margin-top: 0;
  }

  .advanced-search-button .advanced-search-filters > .filters .advanced-filter-input {
    width: 175px;
  }

  .dropdown-container {
    width: 300px !important;
  }
}

