@import "./src/assets/styles/colors";

.language-selector {
  width: 78px;
  height: 22px;
  position: relative;
  margin-right: 60px;
  user-select: none;
  
  &.toggle {
    .arrow {
      border-top-color: $primary-color;
    }
  }
  
  .dropdown-toggle {
    cursor: pointer;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    
    &:hover {
      .language-title {
        color: $primary-color;
      }
      
      .arrow {
        border-top-color: $primary-color;
      }
    }
  }
  
  .language {
    width: 78px;
    height: 22px;
    padding: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: row;
    cursor: pointer;
    
    &:hover .language-title {
      color: $primary-color;
    }
  }
  
  .languages {
    width: 94px;
    height: auto;
    position: absolute;
    top: 40px;
    left: -9px;
    z-index: 9999;
    display: none;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    border-radius: 5px;
    background: #272E3D;
    
    .language-title {
      margin: 0 0 0 12px;
    }
    
    .language {
      width: 100%;
      height: 0;
      padding: 15px 0;
      
      &:first-child {
        margin-top: 15px;
      }
      
      &:last-child {
        margin-bottom: 15px;
      }
    }
  }
  
  &.active .languages:before {
    content: "";
    height: 0;
    width: 0;
    position: absolute;
    border-left: 15px solid transparent;
    border-right: 15px solid transparent;
    border-bottom: 15px solid #272E3D;
    top: -10px;
  }
  
  .language-flag {
    display: flex;
    align-items: center;
    
    img {
      height: 13px;
      width: 20px;
    }
  }
  
  .language-title {
    margin: 0 9px 0 12px;
    font-size: 18px;
    font-weight: 600;
    text-transform: uppercase;
    color: $light-color;
  }
  
  .arrow {
    border-right: 6px solid transparent;
    border-left: 6px solid transparent;
    border-top: 7px solid;
    border-top-color: $light-color;
  }
  
  &.active .arrow {
    border-top-color: #4A99EB;
  }
}
