@import "src/assets/styles/colors";

.item-container.item-data {
  &.full {
    position: relative;
    align-items: flex-start;
    min-width: 705px;
    margin-right: 50px;
    
    .additional {
      position: absolute;
      top: 50px;
      right: 50px;
      display: flex;
      align-items: center;
      flex-direction: row;
      justify-content: center;
      
      .item-search {
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: center;
        margin-right: 70px;
        cursor: pointer;
        height: 50px;
        width: 50px;
        
        &:hover {
          svg {
            fill: $primary-color;
          }
        }
        
        svg {
          width: 30px;
          height: 30px;
          fill: $gray-light-color;
        }
      }
      
      .item-nametag {
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: center;
        position: absolute;
        right: 0;
        top: 0;
        padding: 10px;
        border-radius: 5px;
        background: #272E3D;
        transition: 0.5s;
        cursor: pointer;
        height: 50px;
        width: 50px;
        
        &:hover {
          width: auto;
          white-space: nowrap;
          
          span {
            width: auto;
            margin-right: 10px;
          }
          
          svg {
            fill: $primary-color;
          }
        }
        
        span {
          width: 0;
          overflow: hidden;
          font-size: 18px;
          font-weight: 500;
          z-index: 2;
          color: $gray-light-color
        }
        
        svg {
          height: 30px;
          width: 30px;
          fill: $gray-light-color;
        }
      }
    }
    
    .item-name {
      margin-bottom: 10px;
      font-size: 36px;
      font-weight: 700;
      color: $light-color;
    }
    
    .item-weapon {
      margin-bottom: 75px;
      font-size: 18px;
      font-weight: 500;
      color: $gray-light-color;
    }
    
    .item-image {
      display: flex;
      align-items: center;
      flex-direction: row;
      justify-content: center;
      width: 438px;
      width: calc(100% - 52px);
      height: 222px;
      margin-bottom: 75px;
      
      img {
        width: auto;
        max-width: 438px;
        height: auto;
      }
      
      .item-stickers {
        position: absolute;
        right: 50px;
        display: flex;
        align-items: center;
        flex-direction: column;
        justify-content: center;
        
        .__react_component_tooltip {
          display: flex;
          flex-direction: column;
          justify-content: center;
          align-items: center;
          max-width: 280px;
          min-width: 150px;
          padding: 15px;
          border-radius: 5px;
          background: $secondary-color;
          box-shadow: 0 0 5px 0 $secondary-color;
          font-size: 14px;
          font-weight: 600;
          white-space: normal;
          opacity: 1;
          
          &.place-top {
            &:after {
              border-top-color: $secondary-color;
            }
          }
          
          &.place-left {
            &:after {
              border-left-color: $secondary-color;
            }
          }
          
          &.place-bottom {
            &:after {
              border-bottom-color: $secondary-color;
            }
          }
          
          &.place-right {
            &:after {
              border-right-color: $secondary-color;
            }
          }
          
          .sticker-title {
            font-size: 18px;
            font-weight: 600;
            color: $light-color;
            margin-bottom: 10px;
          }
          
          .sticker-wear {
            font-size: 18px;
            font-weight: 600;
            color: $gray-light-color;
          }
        }
        
        .item-sticker {
          img {
            width: 52px;
            height: 52px;
          }
          
          svg {
            width: 52px;
            height: 52px;
            fill: $secondary-color;
          }
          
          &:not(:last-child) {
            margin-bottom: 30px;
          }
        }
      }
      
      .item-lock {
        position: absolute;
        top: 350px;
        left: 50px;
        display: flex;
        align-items: center;
        flex-direction: row;
        padding: 12px;
        background: $secondary-color;
        border-radius: 5px;
        
        &.unlocked {
          svg {
            fill: $success-color;
          }
          
          span {
            color: $success-color;
          }
        }
        
        &.locked {
          svg {
            fill: $danger-color;
          }
          
          span {
            color: $danger-color;
          }
        }
        
        svg {
          width: 24px;
          height: 24px;
        }
        
        span {
          margin-left: 10px;
          font-size: 18px;
          font-weight: 500;
          color: $light-color;
          fill: $danger-color;
        }
      }
    }
    
    .item-float {
      display: flex;
      align-items: center;
      flex-direction: row;
      justify-content: center;
      width: 100%;
      margin-bottom: 50px;
      
      .float-value {
        font-size: 18px;
        font-weight: 700;
        color: $light-color;
      }
      
      .floatbar {
        height: 10px;
        margin: 0 15px;
      }
      
      .float-exterior {
        font-size: 18px;
        font-weight: 700;
        white-space: nowrap;
        color: $gray-light-color;
      }
    }
    
    .item-price {
      display: flex;
      align-items: center;
      flex-direction: row;
      width: 100%;
      margin-bottom: 50px;
      
      .prices {
        display: flex;
        flex-direction: column;
        width: 100%;
        
        .price {
          display: flex;
          align-items: center;
          flex-direction: row;
          margin-bottom: 10px;
          font-size: 36px;
          font-weight: 700;
          color: $light-color;
          
          .discount {
            display: flex;
            align-items: center;
            justify-content: center;
            margin-left: 20px;
            padding: 3px 6px;
            font-size: 16px;
            font-weight: 700;
            background: $danger-color;
            border-radius: 5px;
          }
        }
        
        .steam-price {
          font-size: 18px;
          font-weight: 600;
          color: $gray-light-color;
        }
      }
      
      .btns {
        display: flex;
        align-items: center;
        flex-direction: row;
        justify-content: center;
        margin-left: auto;
        
        .btn {
          height: 54px;
          
          &:last-child {
            margin-left: 20px;
          }
          
          svg {
            width: 30px;
            height: 30px;
            transform: scaleX(-1);
            fill: $gray-light-color;
          }
        }
      }
    }
    
    .item-order {
      display: flex;
      align-items: center;
      flex-direction: row;
      padding: 30px;
      background: $dark-secondary-color;
      border-radius: 5px;
      
      svg {
        width: 35px;
        height: 35px;
        fill: $gray-light-color;
      }
      
      span {
        margin: 0 30px 0 20px;
        font-size: 24px;
        font-weight: 700;
        color: $light-color;
      }
      
      input {
        width: 100%;
        
        &.count-input {
          text-align: center;
        }
      }
      
      .count-input-group {
        padding: 20px;
        background: #0F141B;
        border-radius: 5px;
        width: 75px;
        margin-right: 20px;
        
        &.input-focus {
          box-shadow: 0 0 0 1.5px inset $primary-color;
        }
        
        &.input-error {
          box-shadow: 0 0 0 1.5px inset $danger-color;
        }
        
        &.input-success {
          box-shadow: 0 0 0 1.5px inset $success-color
        }
      }
      
      .price-input-group {
        padding: 20px;
        background: #0F141B;
        border-radius: 5px;
        width: 135px;
        margin-right: 30px;
        
        &.input-focus {
          box-shadow: 0 0 0 1.5px inset $primary-color;
        }
        
        &.input-error {
          box-shadow: 0 0 0 1.5px inset $danger-color;
        }
        
        &.input-success {
          box-shadow: 0 0 0 1.5px inset $success-color
        }
      }
      
      .btn {
        margin-left: auto;
      }
    }
  }
  
