        }
      }
    }
    
    .item-float {
      display: flex;
      align-items: center;
      flex-direction: column;
      justify-content: center;
      width: 100%;
      margin-bottom: 30px;
      
      .float-value {
        font-size: 18px;
        font-weight: 700;
        color: $light-color;
        margin-bottom: 10px;
      }
      
      .floatbar {
        height: 10px;
        margin-bottom: 10px;
      }
      
      .float-exterior {
        font-size: 18px;
        font-weight: 700;
        white-space: nowrap;
        color: $gray-light-color;
      }
    }
    
    .item-price {
      display: flex;
      flex-direction: column !important;
      width: 100%;
      margin-bottom: 20px;
      
      .prices {
        display: flex;
        flex-direction: column;
        width: 100%;
        margin-bottom: 20px;
        
        .price {
          width: 100%;
          display: flex;
          align-items: center;
          flex-direction: row;
          font-size: 30px;
          font-weight: 700;
          color: $light-color;
          margin-bottom: 10px;
          
          .discount {
            display: flex;
            align-items: center;
            justify-content: center;
            margin-left: 20px;
            padding: 3px 6px;
            font-size: 16px;
            font-weight: 700;
            background: $danger-color;
            border-radius: 5px;
          }
        }
        
        .steam-price {
          width: 100%;
          font-size: 18px;
          font-weight: 600;
          color: $gray-light-color;
        }
      }
      
      .btns {
        display: flex;
        align-items: center;
        flex-direction: row;
        justify-content: center;
        width: 100%;
        
        .btn {
          height: 60px;
          
          &.buy {
            min-width: 150px;
            width: 100%;
          }
          
          &:last-child {
            margin-left: 20px;
          }
          
          svg {
            width: 30px;
            height: 30px;
            transform: scaleX(-1);
            fill: $gray-light-color;
          }
        }
      }
    }
    
    .item-order {
      display: flex;
      flex-direction: column;
      width: 100%;
      padding: 30px;
      background: $dark-secondary-color;
      border-radius: 5px;
      
      span {
        margin-bottom: 20px;
        font-size: 24px;
        font-weight: 700;
        color: $light-color;
      }
      
      input {
        width: 100%;
        
        &.count-input {
        }
      }
      
      .count-input-group {
        padding: 20px;
        background: #0F141B;
        border-radius: 5px;
        width: 100%;
        margin-bottom: 10px;
        
        &.input-focus {
          box-shadow: 0 0 0 1.5px inset $primary-color;
        }
        
        &.input-error {
          box-shadow: 0 0 0 1.5px inset $danger-color;
        }
        
        &.input-success {
          box-shadow: 0 0 0 1.5px inset $success-color
        }
      }
      
      .price-input-group {
        padding: 20px;
        background: #0F141B;
        border-radius: 5px;
        width: 100%;
        margin-bottom: 10px;
        
        &.input-focus {
          box-shadow: 0 0 0 1.5px inset $primary-color;
        }
        
        &.input-error {
          box-shadow: 0 0 0 1.5px inset $danger-color;
        }
        
        &.input-success {
          box-shadow: 0 0 0 1.5px inset $success-color
        }
      }
      
      .btn {
        width: 100%;
      }
    }
  }
}