@import "src/assets/styles/colors";

.inventory-page {
  position: relative;
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
  
  .input-compound {
    &.input-focus {
      box-shadow: 0 0 0 1.5px $primary-color;
    }
  }
  
  .input {
    &.input-focus {
      box-shadow: 0 0 0 1.5px $primary-color;
    }
  }
  
  .price-switcher-filter {
    display: flex;
    align-items: center;
    flex-direction: row;
    justify-content: center;
    width: 62px;
    height: 62px;
    margin-right: 20px;
    border-radius: 5px;
    
    path {
      fill: $gray-light-color;
    }
    
    &.low {
      path:first-child {
        fill: $light-color;
      }
    }
    
    &.high {
      path:last-child {
        fill: $light-color;
      }
    }
    
    
    svg {
      width: 24px;
      min-width: 24px;
      height: 24px;
      fill: $gray-light-color;
    }
  }
  
  &.mobile {
    
    .columns {
      display: flex;
      align-items: flex-start;
      width: 100%;
      
      & > div:last-child {
        z-index: 9999;
      }
    }
    
    .inventory-filter-block {
      display: flex;
      flex-direction: column;
      width: 100%;
      margin-bottom: 25px;
      padding: 30px;
      background: $dark-color;
      
      .block {
        display: flex;
        align-items: center;
        flex-direction: row;
        width: 100%;
        
        &:not(:last-child) {
          margin-bottom: 20px;
        }
      }
      
      .inventory-game-selector {
        display: flex;
        align-items: center;
        flex-direction: row;
        justify-content: center;
        width: 125px;
        height: 48px;
        margin-right: 20px;
        font-size: 18px;
        background: $secondary-color;
        border-radius: 5px;
      }
      
      .price-switcher-filter {
        width: 100%;
        margin-right: 0;
      }
      
      .status-filter {
        width: 100%;
        min-width: 0;
      }
      
      .inventory-filter-block--input-block {
        display: flex;
        align-items: center;
        flex-direction: row;
        width: 100%;
        height: 60px;
        padding: 15px 20px;
        background: $dark-secondary-color;
        border-radius: 5px;
        
        span {
          display: flex;
          align-items: center;
          flex-direction: row;
          justify-content: center;
          margin-right: 15px;
          
          svg {
            width: 20px;
            height: 20px;
          }
        }
      }
      
    }
    
    .inventory-items-list {
      display: flex;
      flex-direction: row;
      flex-wrap: wrap;
      padding: 0 10px;
    }
    
    .inventory-selected-info {
      position: fixed;
      bottom: 40px;
      left: 100px;
      z-index: 44444;
      display: none;
      align-items: center;
      flex-direction: column;
      width: 100%;
      height: 238px;
      margin: -40px -100px;
      padding: 20px;
      background: $dark-color;
      box-shadow: 0 0 10px 1px $dark-secondary-color;
      border-radius: 5px;
  
      .row {
        display: flex;
        align-items: center;
        flex-direction: row;
        justify-content: space-between;
        margin-bottom: 7px;
        
        &:last-child {
          padding-bottom: 30px;
        }
      }
  
      &.show {
        display: flex;
      }
  
      .inventory-selected-divider {
        display: none;
        align-items: center;
        flex-direction: row;
        width: 1px;
        height: 30px;
        margin: 0 30px;
        background: $secondary-color;
        border-radius: 5px;
      }
  
      .inventory-selected-info-col {
        display: flex;
        flex-direction: column;
    
        .text-block {
          display: flex;
          align-items: flex-start;
          flex-direction: column;
          font-size: 16px;
          color: $gray-color;
      
          span {
            &:first-child {
              margin-bottom: 5px;
            }
        
            &:last-child {
              font-size: 16px;
            }
          }
        }
    
        &.you-get {
          display: flex;
          flex-direction: row;
          align-items: center;
          justify-content: space-between;
          width: 100%;
          margin: 20px 0;
      
          & > div {
            &:first-child {
              font-size: 14px;
              font-weight: 500;
              color: $gray-light-color;
            }
        
            &:last-child {
              font-size: 14px;
              font-weight: 600;
              color: $light-color;
            }
          }
        }
    
        &.buttons {
          display: flex;
          align-items: center;
          flex-direction: row;
          justify-content: space-between;
          width: 100%;
          margin-right: 0;
          padding-bottom: 30px !important;
      
          .btn {
            width: 100%;
            font-size: 16px;
          }
      
          & > div {
            &:not(:last-child) {
              margin-right: 15px;
            }
          }
        }
    
        .fee {
          .text {
            .light-text {
              margin-right: 5px;
            }
          }
        }
    
        .additional, .fee {
          .text {
            .light-text {
              font-weight: 500;
            }
          }
        }
    
        & > div {
          display: flex;
          align-items: center;
          flex-direction: row;
      
          span {
            &:first-child {
              display: flex;
              align-items: center;
              flex-direction: row;
              margin-right: 10px;
          
              svg {
                width: 26px;
                height: 26px;
                fill: $primary-color;
              }
            }
        
            &:last-child {
              display: flex;
              align-items: center;
              flex-direction: row;
              font-size: 14px;
              font-weight: 600;
              color: $light-color;
          
              & > div {
                &.unit-name {
                  margin-right: 5px;
                  font-size: 16px;
                  font-weight: 600;
                }
              }
            }
        
            &.icon {
              display: flex;
              align-items: center;
              flex-direction: row;
              justify-content: center;
              width: 32px;
              height: 32px;
              background: $dark-secondary-color;
              border-radius: 5px;
          
              svg {
                width: 12px;
                height: 12px;
                fill: $light-color;
              }
            }
        
            &.text {
              font-size: 16px;
              font-weight: 500;
              color: $gray-light-color;
            }
          }
        }
      }
    }
  }
  
