        position: relative;
        display: flex;
        align-items: center;
        flex-direction: row;
        min-width: 280px;
        font-size: 18px;
        font-weight: 600;
        color: $light-color;
      
        &.price-filter {
          margin-right: 30px;
        }
      
        .dropdown-menu--head {
          display: flex;
          align-items: center;
          flex-direction: row;
          width: 100%;
          padding: 20px;
          cursor: pointer;
          background: $secondary-color;
          border-radius: 5px;
        
          .dropdown-menu--title {
            display: flex;
            align-items: center;
            flex-direction: row;
            user-select: none;
          }
        
          .dropdown-menu--controls {
            display: flex;
            align-items: center;
            flex-direction: row;
            margin-left: auto;
          
            .dropdown-menu--reset {
              display: flex;
              align-items: center;
              flex-direction: row;
              margin-right: 10px;
              transform: rotate(45deg);
            
              &:hover {
                svg {
                  fill: $primary-color;
                }
              }
            
              svg {
                width: 14px;
                height: 14px;
                pointer-events: none;
                fill: $light-color;
              }
            }
          
            .dropdown-menu--arrow {
              border-top: 7px solid;
              border-top-color: $light-color;
              border-right: 6px solid transparent;
              border-left: 6px solid transparent;
            
              &.active {
                border-top-color: $primary-color;
              }
            }
          }
        }
      
        .dropdown-menu--options {
          position: absolute;
          top: 70px;
          z-index: 999;
          display: flex;
          flex-direction: column;
          overflow-y: auto;
          width: 100%;
          max-height: 310px;
          background: $secondary-color;
          border-radius: 5px;
        
          .dropdown-menu--option {
