              color: $gray-light-color;
            }
          }
        }
      }
    }
  }
  
  &.fullscreen {
  
    .columns {
      display: flex;
      align-items: flex-start;
      width: 100%;
    
      & > div:last-child {
        z-index: 9999;
      }
    }
  
    .inventory-filter-block {
      display: flex;
      align-items: center;
      flex-direction: row;
      width: 100%;
      margin-bottom: 25px;
      padding: 20px;
      background: $dark-color;
    
      .dropdown-menu {
        position: relative;
        display: flex;
        align-items: center;
        flex-direction: row;
        min-width: 280px;
        font-size: 18px;
        font-weight: 600;
        color: $light-color;
      
        &.price-filter {
          margin-right: 30px;
        }
      
        .dropdown-menu--head {
          display: flex;
          align-items: center;
          flex-direction: row;
          width: 100%;
          padding: 20px;
          cursor: pointer;
          background: $secondary-color;
          border-radius: 5px;
        
          .dropdown-menu--title {
            display: flex;
            align-items: center;
            flex-direction: row;
            user-select: none;
          }
        
          .dropdown-menu--controls {
            display: flex;
            align-items: center;
            flex-direction: row;
            margin-left: auto;
          
            .dropdown-menu--reset {
              display: flex;
              align-items: center;
              flex-direction: row;
              margin-right: 10px;
              transform: rotate(45deg);
            
              &:hover {
                svg {
                  fill: $primary-color;
                }
              }
            
              svg {
                width: 14px;
                height: 14px;
                pointer-events: none;
                fill: $light-color;
              }
            }
          
            .dropdown-menu--arrow {
              border-top: 7px solid;
              border-top-color: $light-color;
              border-right: 6px solid transparent;
              border-left: 6px solid transparent;
            
              &.active {
                border-top-color: $primary-color;
              }
            }
          }
        }
      
        .dropdown-menu--options {
          position: absolute;
          top: 70px;
          z-index: 999;
          display: flex;
          flex-direction: column;
          overflow-y: auto;
          width: 100%;
          max-height: 310px;
          background: $secondary-color;
          border-radius: 5px;
        
          .dropdown-menu--option {
            display: flex;
            align-items: center;
            flex-direction: row;
            padding: 20px;
            cursor: pointer;
            user-select: none;
            color: $gray-light-color;
          
            &.selected {
              color: $light-color;
              background: $primary-color;
            
              &:hover {
                background: darken($primary-color, 5%);
              }
            }
          
            &:hover {
              background: darken($secondary-color, 5%);
            }
          }
        }
      }
    
      .inventory-game-selector {
        display: flex;
        align-items: center;
        flex-direction: row;
        justify-content: center;
        width: 125px;
        height: 48px;
        margin-right: 20px;
        font-size: 18px;
        background: $secondary-color;
        border-radius: 5px;
      }
    
      .inventory-filter-block--input-block {
        display: flex;
        align-items: center;
        flex-direction: row;
        width: auto;
        min-width: 500px;
        height: 60px;
        margin-right: auto;
        padding: 15px 20px;
        background: $dark-secondary-color;
        border-radius: 5px;
      
        span {
          display: flex;
          align-items: center;
          flex-direction: row;
          justify-content: center;
          margin-right: 15px;
        
          svg {
            width: 20px;
            height: 20px;
          }
        }
      }
    
      .item-cards-view {
        position: relative;
        display: flex;
        align-items: center;
        flex-direction: row;
      
        .list, .card {
          display: flex;
          align-items: center;
          flex-direction: row;
          justify-content: center;
          width: 30px;
          height: 60px;
          cursor: pointer;
        
          &.active {
            &:before {
              position: absolute;
              top: -20px;
              content: "";
              width: 24px;
              height: 4px;
              background: $primary-color;
              border-radius: 5px;
            }
          
            svg {
              fill: $light-color;
            }
          }
        
          svg {
            width: 24px;
            height: 24px;
            fill: $gray-light-color;
          }
        }
      
        .list {
          margin-right: 15px;
        }
      }
    }
    
    .inventory-items-list {
      display: flex;
      flex-direction: row;
      flex-wrap: wrap;
    }
    
    .inventory-selected-info {
      position: fixed;
      bottom: 50px;
      left: 100px;
      z-index: 44444;
      display: none;
      align-items: center;
      flex-direction: row;
      width: calc(100% - 100px);
      margin: -50px;
      padding: 30px;
      background: $dark-color;
      box-shadow: 0 0 10px 1px $dark-secondary-color;
      border-radius: 5px;
  
      &.show {
        display: flex;
      }
  
      .inventory-selected-divider {
        display: flex;
        align-items: center;
        flex-direction: row;
        width: 1px;
        height: 30px;
        margin: 0 30px;
        background: $secondary-color;
        border-radius: 5px;
      }
      
      .inventory-selected-info-col {
        display: flex;
        flex-direction: column;
        
        .text-block {
          display: flex;
          align-items: flex-start;
          flex-direction: column;
          font-size: 16px;
          color: $gray-color;
          
          span {
            &:first-child {
              margin-bottom: 5px;
            }
            
            &:last-child {
              font-size: 16px;
            }
          }
        }
        
        &.additionals {
          margin-left: 60px;
        }
        
        &.fees {
          margin-left: auto;
        }
        
        &.you-get {
          justify-content: center;
          margin-right: 50px;
          
          & > div {
            &:first-child {
              margin-bottom: 10px;
              font-size: 16px;
              font-weight: 500;
              color: $gray-light-color;
            }
            
            &:last-child {
              font-size: 16px;
              font-weight: 600;
              color: $light-color;
            }
          }
        }
        
        &.buttons {
          display: flex;
          align-items: center;
          flex-direction: row;
          
          & > div {
            &:not(:last-child) {
              margin-right: 15px;
            }
          }
        }
        
        .fee {
          .text {
            .light-text {
              margin-right: 5px;
            }
          }
        }
        
        .additional, .fee {
          .text {
            .light-text {
              font-weight: 500;
            }
          }
        }
        
        & > div {
          display: flex;
          align-items: center;
          flex-direction: row;
          
          span {
            &:first-child {
              display: flex;
              align-items: center;
              flex-direction: row;
              margin-right: 15px;
              
              svg {
                width: 30px;
                height: 30px;
                fill: $primary-color;
              }
            }
            
            &:last-child {
              display: flex;
              align-items: center;
              flex-direction: row;
              font-size: 16px;
              font-weight: 600;
              color: $light-color;
              
              & > div {
                &.unit-name {
                  margin-right: 5px;
                  font-size: 16px;
                  font-weight: 600;
                }
              }
            }
            
            &.icon {
              display: flex;
              align-items: center;
              flex-direction: row;
              justify-content: center;
              width: 45px;
              height: 45px;
              background: $dark-secondary-color;
              border-radius: 5px;
              
              svg {
                width: 18px;
                height: 18px;
                fill: $light-color;
              }
            }
            
            &.text {
              font-size: 16px;
              font-weight: 500;
              color: $gray-light-color;
            }
          }
        }
      }
    }
  }
}