@import "./src/assets/styles/colors";
@import "./src/assets/styles/breakpoints";

.mobile-header {
  width: 100%;
  height: 80px;
  background: $dark-color;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 9999 !important;
  
  &.active {
    .sidebar {
      transform: translateX(0%);
    }
  }
  
  .main-bar {
    display: flex;
    flex-direction: row;
    align-items: center;
    overflow: hidden;
    flex-wrap: nowrap;
    height: 100%;
    width: 100%;
    padding: 30px;
  }
  
  .sidebar {
    width: 100%;
    z-index: 9999 !important;
    transition: 0.5s all;
    height: 100vh;
    position: absolute;
    top: 10px;
    left: 0;
    right: 0;
    bottom: 0;
    overflow: hidden;
    transform: translateX(-100%);
    background: $dark-secondary-color;
    
    .sidebar-header {
      background: $dark-color;
      height: 80px;
      width: 100%;
      margin-bottom: 10px;
      display: flex;
      flex-direction: row;
      align-items: center;
      justify-content: space-between;
      padding: 30px;
      
      .game-filter-mobile {
        cursor: pointer;
        padding: 8px 20px;
        background: $secondary-color;
        border-radius: 5px;
        font-size: 18px;
        font-weight: 600;
        color: $light-color;
      }
      
      .sidebar-header-close-btn {
        cursor: pointer;
        
        svg {
          width: 20px;
          height: 20px;
          fill: $light-color;
        }
      }
    }
    
    .sidebar-content {
      height: 100%;
      width: 100%;
      padding: 30px;
      background: $dark-color;
      display: flex;
      flex-direction: column;
      align-items: center;
      
      .sidebar-links {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        width: 100%;
        
        .link-row {
          width: 100%;
          font-size: 18px;
          font-weight: 500;
          color: $gray-light-color;
          text-align: center;
          
          &:not(:last-child) {
            margin-bottom: 30px;
          }
          
          &:hover, &:active {
            color: $light-color;
          }
          
          &.btn {
            color: inherit;
          }
        }
        
        .language-selector {
          margin: 0 0 30px 0;
        }
      }
      
      .sidebar-footer {
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: space-evenly;
        
        a {
          color: $gray-light-color;
          font-size: 16px;
          font-weight: 500;
          margin: 17px;
          text-align: center;
          
          &:hover, &:active {
            color: $light-color;
          }
        }
      }
    }
  }
  
  .sidebar-toggler {
    cursor: pointer;
    
    div {
      width: 25px;
      height: 3px;
      background-color: $light-color;
      
      &:not(:last-child) {
        margin-bottom: 6px;
      }
    }
  }
  
  .logo {
    margin-left: 20px;
    font-size: 24px;
    font-weight: 700;
    font-family: "TT Lakes Condensed Bold Italic", sans-serif;
    letter-spacing: 0.12em;
    
    span {
      color: #4A99EB;
    }
  }
  
  .navigation {
    margin-left: auto;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    
    a, div {
      &:not(:last-child) {
        margin-right: 20px;
      }
      
      svg {
        width: 20px;
        height: 20px;
        fill: $gray-light-color;
      }
    }
    
    .notification-btn {
      position: relative;
      
      .notifications-badge {
        width: 8px;
        height: 8px;
        position: absolute;
        top: -3px;
        right: 0;
        border: 2px solid $dark-color;
        border-radius: 50%;
        background: $danger-color;
        box-sizing: content-box;
      }
    }
  }
  
}

@media (max-width: $breakpoint-laptop-small) {
  .mobile-header {
    .sidebar {
      background: #0f141b;
  
      .sidebar-header {
        height: 70px;
      }
      
      .sidebar-links, .sidebar-footer {
        max-width: 450px;
      }
    }
  }
}