@import "../../../../assets/styles/colors";

.code-modal-wrapper {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 10000;
  display: none;
  align-items: center;
  flex-direction: row;
  justify-content: center;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  
  &.active {
    display: flex;
  }
  
  .modal-block {
    position: relative;
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;
    overflow: hidden;
    width: auto;
    background: $dark-color;
    border-radius: 5px;
    
    .close-button {
      position: absolute;
      top: 20px;
      right: 20px;
      cursor: pointer;
      
      &:hover {
        svg {
          fill: $danger-color;
        }
      }
      
      svg {
        width: 20px;
        height: 20px;
        fill: $light-color;
      }
    }
    
    .modal-body {
      display: flex;
      align-items: center;
      flex-direction: column;
      padding: 50px;
      
      .modal-icon {
        display: flex;
        align-items: center;
        flex-direction: row;
        justify-content: center;
        width: 60px;
        height: 60px;
        margin-bottom: 30px;
        background: $danger-color;
        border-radius: 50%;
        
        svg {
          width: 32px;
          height: 32px;
          fill: $light-color;
        }
      }
      
      .modal-title {
        display: flex;
        align-items: center;
        flex-direction: row;
        justify-content: center;
        margin-bottom: 25px;
        font-size: 24px;
        font-weight: 600;
        color: $light-color;
      }
      
      .modal-inputs {
        display: flex;
        flex-direction: column;
        align-items: center;
        margin-bottom: 25px;
        width: 100%;
        white-space: nowrap;
        
        & > div {
          width: 100%;
          
          .modal-input-block {
            width: 100%;
            
            &:last-child {
              margin-left: 25px;
            }
          }
        }
        
        .modal-input-block {
          display: flex;
          width: 100%;
          flex-direction: column;
          margin-bottom: 20px;
          
          .modal-input-block--title {
            font-size: 18px;
            font-weight: 600;
            margin-bottom: 10px;
            color: $light-color;
          }
          
          .modal-input-block--input {
            display: flex;
            flex-direction: row;
            align-items: center;
            width: 100%;
            background: $dark-secondary-color;
            padding: 15px;
            border-radius: 5px;
            font-size: 18px;
            font-weight: 500;
            
            &.input-focus {
              box-shadow: 0 0 0 1.5px inset $primary-color;
            }
            
            &.input-error {
              box-shadow: 0 0 0 1.5px inset $danger-color;
            }
            
            &.input-success {
              box-shadow: 0 0 0 1.5px inset $success-color;
            }
          }
          
          .input-error-text {
            display: flex;
            font-size: 14px;
            font-weight: 400;
            color: $danger-color;
            margin-top: 10px;
          }
        }
      }
      
      .modal-buttons {
        display: flex;
        align-items: center;
        flex-direction: row;
        width: 100%;
        
        .btn {
          width: 100%;
          
          &.modal-button-disable {
            &.disabled {
              background: rgba($danger-color, 0.55);
            }
          }
          
          &.modal-button-activate {
            &.disabled {
              background: rgba($primary-color, 0.55);
            }
          }
          
          &:last-child {
            margin-left: 15px;
          }
        }
      }
    }
  }
}