$breakpoint-tablet: 768px;
$breakpoint-laptop-small: 1024px;
$breakpoint-laptop-large: 1200px;
$breakpoint-desktop: 1600px;
@import "./src/assets/styles/colors";

.support-page {
  width: 100%;
  position: relative;
  
  &.mobile {
    background: $dark-color;
    padding: 30px;
    
    .ticket-modal {
      position: fixed;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      min-height: 100vh;
      height: 100%;
      width: 100%;
      overflow-y: scroll;
      transform: translateX(-100%);
      transition: 375ms cubic-bezier(0.1, 0.1, 0.25, 1);
      background: $dark-secondary-color;
      z-index: 9999;
      
      &.active {
        transform: translateX(0%);
      }
      
      .ticket-modal-head {
        display: flex;
        flex-direction: row;
        align-items: center;
        width: 100%;
        height: 80px;
        background: $dark-color;
        padding: 20px 30px;
        margin-bottom: 10px;
        
        .favorite-icon {
          margin-right: 30px;
          display: flex;
          align-items: center;
          justify-content: center;
          
          &.favorite-active {
            svg {
              fill: $primary-color;
            }
          }
          
          &.favorite-inactive {
            svg {
              fill: $secondary-color;
            }
          }
          
          svg {
            width: 24px;
            height: 24px;
            fill: $gray-light-color;
          }
        }
        
        .ticket-info {
          display: flex;
          flex-direction: row;
          align-items: center;
          font-size: 18px;
          font-weight: 500;
          color: $gray-light-color;
          
          .ticket-category {
            margin-right: 5px;
          }
          
          .ticket-divider {
            margin: 0 15px;
          }
        }
        
        .ticket-close-btn {
          position: absolute;
          top: 32px;
          right: 30px;
          
          svg {
            width: 20px;
            height: 20px;
            fill: $light-color;
          }
        }
      }
      
      .ticket-modal-title {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        padding: 30px;
        background: $dark-color;
        margin-bottom: 20px;
        
        svg {
          width: 20px;
          height: 20px;
        }
        
        span {
          font-size: 18px;
          font-weight: 700;
          color: $light-color;
          margin-bottom: 20px;
        }
        
        .btn {
          width: 100%;
          position: relative;
          
          svg {
            position: absolute;
            left: 15px;
            fill: $danger-color;
          }
        }
      }
      
      .ticket-modal-chat {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        padding: 30px;
        background: $dark-color;
        
        .ticket-chat-messages {
          max-height: 450px;
          width: 100%;
          overflow: hidden scroll;
          
          .message {
            display: flex;
            flex-direction: column;
            padding: 15px;
            border-radius: 15px;
            font-size: 14px;
            font-weight: 500;
            background: $secondary-color;
            text-align: left;
            margin-bottom: 20px;
            max-width: 300px;
            margin-left: auto;
            margin-right: 0;
            
            &.support {
              background: $primary-color;
              margin-right: auto;
              margin-left: 0;
              
              .text {
                color: $light-color;
              }
              
              .time {
                color: #B3D8FF;
              }
            }
            
            &.close {
              background: $danger-color;
              
              .text {
                color: $light-color;
              }
              
              .time {
                color: $light-color;
              }
              
              svg {
                width: 20px;
                height: 20px;
              }
            }
            
            .text {
              color: #C6C6C6;
              margin-bottom: 10px;
            }
            
            .time {
              color: $primary-color;
            }
          }
        }
        
        .ticket-chat-interaction {
          margin-top: 30px;
          width: 100%;
          
          .input {
            padding: 15px;
            border-radius: 5px;
            background: $dark-secondary-color;
            display: flex;
            flex-direction: row;
            align-items: center;
            justify-content: center;
            position: relative;
            
            .input-append {
              position: absolute;
              right: 7px;
            }
            
            input {
              font-size: 14px;
              font-weight: 500;
            }
          }
          
          .btn {
            svg {
              width: 16px;
              height: 16px;
              fill: $light-color;
            }
          }
        }
      }
    }
    
    .ticket-modal {
      padding: 30px;
      
      .ticket-modal-close {
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: center;
        width: 54px;
        height: 54px;
        border-radius: 5px;
        cursor: pointer;
        
        svg {
          width: 20px;
          height: 20px;
          fill: $light-color;
        }
      }
      
      .chat-head {
        margin-bottom: 30px;
        display: flex;
        flex-direction: column;
        align-items: center;
        
        .info {
          display: flex;
          flex-direction: column;
          margin-bottom: 15px;
          
          .top-info {
            display: flex;
            flex-direction: column;
            margin-bottom: 20px;
            font-size: 18px;
            font-weight: 500;
            color: $gray-light-color;
            
            .creation-date {
              margin-bottom: 5px;
            }
            
            .top-info-divider {
              display: none;
            }
            
            .category {
            }
          }
          
          .bottom-info {
            .ticket-title {
              font-size: 24px;
              font-weight: 700;
              
              color: #FFFFFF;
            }
          }
        }
        
        .buttons {
          display: flex;
          flex-direction: row;
          align-content: center;
          width: 100%;
          
          .btn {
            width: 100%;
          }
          
          .close-ticket-button {
            margin-right: 10px;
          }
        }
        
        .close-ticket-button {
          margin-right: 75px;
          
          svg {
            width: 20px;
            height: 20px;
            fill: $danger-color;
          }
          
          &.active {
            svg {
              margin-right: 0;
            }
          }
        }
      }
      
      .chat-body {
        width: calc(100% + 15px);
        margin: 0 -30px 0 0;
        
        .chat-container {
          max-height: 600px;
          overflow-y: scroll;
          padding-right: 10px;
          
          &::-webkit-scrollbar {
            width: 5px;
          }
          
          &::-webkit-scrollbar-track {
            width: 5px;
          }
          
          .message {
            min-height: 125px;
            width: 100%;
            padding: 25px;
            
            &:not(:last-child) {
              margin-bottom: 20px;
            }
            
            border-radius: 0 22px 22px 22px;
            background: #272F3C;
            
            &.staff {
              margin-left: auto;
              
              border-radius: 22px 22px 0 22px;
              background: #4A99EB;
              
              .body {
                color: #FFFFFF;
              }
              
              .date {
                color: #B3D8FF;
              }
            }
            
            &.close {
              background: $danger-color;
              
              .body {
                color: $light-color;
              }
            }
            
            .body {
              margin-bottom: 20px;
              font-size: 16px;
              font-weight: 500;
              line-height: 30px;
              color: #A4B1C1;
            }
            
            .files {
              margin-bottom: 20px;
              
              img {
                height: 100px;
                width: auto;
                
                &:not(:last-child) {
                  margin-right: 15px;
                }
              }
            }
            
            .date {
              font-size: 18px;
              font-weight: 500;
              
              color: #4A99EB;
            }
          }
        }
        
        .attached {
          margin-top: 50px;
          margin-bottom: 20px;
          display: flex;
          flex-direction: row;
          overflow: hidden;
          
          .file {
            position: relative;
            display: flex;
            align-items: center;
            flex-direction: column;
            justify-content: center;
            width: 100px;
            height: 80px;
            color: $gray-light-color;
            background: $secondary-color;
            border-radius: 15px;
            
            &:not(:last-child) {
              margin-right: 15px;
              margin-bottom: 15px;
            }
            
            .remove {
              position: absolute;
              top: 10px;
              right: 10px;
              display: flex;
              align-items: center;
              flex-direction: row;
              justify-content: center;
              padding: 5px;
              cursor: pointer;
              background: $dark-secondary-color;
              border-radius: 5px;
              
              &:hover {
                svg {
                  fill: $danger-color;
                }
              }
              
              svg {
                width: 15px;
                height: 15px;
                fill: $gray-light-color;
              }
            }
            
            span {
              display: none;
              max-width: 150px;
              font-size: 14px;
              font-weight: 500;
            }
            
            img {
              width: 100px;
              max-width: 100px;
              height: 80px;
              border-radius: 15px;
              object-fit: cover;
            }
          }
        }
        
        .input-closed {
          width: 100%;
          display: flex;
          flex-direction: row;
          align-items: center;
          justify-content: center;
          background: #0F141B;
          border-radius: 5px;
          height: 75px;
          padding: 0 13px 0 22px;
          font-size: 18px;
          font-weight: 500;
          color: $gray-light-color;
        }
        
        .input-button {
          width: 100%;
          display: flex;
          flex-direction: column;
          align-items: center;
          background: #0F141B;
          border-radius: 5px;
          
          &.input-focus {
            input {
              box-shadow: 0 0 0 1.5px $primary-color;
            }
          }
          
          &.input-error {
            input {
              box-shadow: 0 0 0 1.5px $danger-color;
            }
          }
          
          input {
            height: 75px;
            padding: 0 13px 0 22px;
            width: 100%;
            border-radius: 5px;
            font-size: 18px;
            font-weight: 500;
            color: #FFFFFF;
          }
          
          .icon {
            width: 30px;
            height: 30px;
          }
          
          .buttons {
            width: 100%;
            display: flex;
            flex-direction: row;
            align-items: center;
            padding: 25px;
            
            .icon {
              display: flex;
              flex-direction: row;
              align-items: center;
              justify-content: center;
              margin-right: auto;
              cursor: pointer;
              height: 49px;
              width: 81px;
              
              svg {
                width: 30px;
                height: 30px;
                
                fill: #272E3D;
              }
            }
            
            .send-button {
              svg {
                width: 17px;
                height: 17px;
                
                fill: #FFFFFF;
              }
            }
          }
        }
      }
    }
    
    .support-page-head {
      display: flex;
      flex-direction: column;
      align-items: center;
      margin-bottom: 50px;
      width: 100%;
      
      .support-page-title {
        display: flex;
        flex-direction: row;
        align-items: center;
        font-size: 36px;
        font-weight: 700;
        color: $light-color;
        text-align: center;
        margin-bottom: 20px;
        
        .tickets-count {
          margin-left: 20px;
          color: $gray-light-color;
        }
      }
      
      .support-page-tabs {
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: center;
        
        .icon {
          cursor: pointer;
          
          &:not(:last-child) {
            margin-right: 30px;
          }
          
          svg {
            width: 36px;
            height: 36px;
            fill: #272E3D;
          }
          
          &.active {
            position: relative;
            
            svg {
              fill: $primary-color;
            }
          }
        }
      }
    }
    
    .new-ticket-button {
      display: flex;
      flex-direction: row;
      align-items: center;
      justify-content: center;
      height: 54px;
      margin-bottom: 20px;
      
      .btn {
        width: 100%;
      }
      
      svg {
        width: 16px;
        min-width: 16px;
        height: 16px;
        margin-right: 10px;
        fill: #FFFFFF;
      }
    }
    
    .support-page-tickets {
      width: calc(100% + 15px);
      display: flex;
      flex-direction: column;
      align-items: center;
      max-height: 500px;
      overflow-y: scroll;
      margin: 0 -30px 0 0;
      padding-right: 30px;
      
      .ticket {
        width: 100%;
        display: flex;
        flex-direction: column;
        position: relative;
        margin: 30px 0;
        cursor: pointer;
        
        &:not(:last-child) {
          &:before {
            content: "";
            position: absolute;
            bottom: -30px;
            min-height: 1px;
            height: 1px;
            width: 100%;
            background: $secondary-color;
          }
        }
        
        .icon {
          display: none;
        }
        
        .ticket-info {
          margin-bottom: 5px;
          
          .ticket-title {
            margin-bottom: 8px;
            font-size: 18px;
            font-weight: 600;
            color: $light-color;
          }
          
          .ticket-description {
            display: flex;
            flex-direction: row;
            max-width: 100%;
            margin-bottom: 12px;
            font-size: 16px;
            font-weight: 500;
            color: $gray-light-color;
            overflow: hidden;
            white-space: nowrap;
            text-overflow: ellipsis;
          }
          
          .ticket-id {
            font-size: 18px;
            font-weight: 500;
            color: #3E444C;
            
            span {
            
            }
          }
        }
        
        .additional {
          display: flex;
          flex-direction: row;
          align-content: center;
          white-space: nowrap;
          
          .time {
            font-size: 18px;
            font-weight: 500;
            color: #6D7681;
          }
          
          .favorite-icon {
            display: flex;
            flex-direction: row;
            align-items: center;
            justify-content: center;
            width: 20px;
            height: 20px;
            margin-left: auto;
            
            svg {
              min-width: 28px;
              width: 28px;
              height: 28px;
              pointer-events: none;
            }
            
            &.favorite-active {
              svg {
                fill: $primary-color;
              }
            }
            
            &.favorite-inactive {
              svg {
                fill: $secondary-color;
              }
            }
          }
        }
        
        &:first-child {
          margin: 0 0 30px 0;
        }
      }
    }
  }
  
