          .support-col-title {
            font-size: 36px;
            font-weight: 700;
            color: #FFFFFF;
            
            .tickets-count {
              margin-left: 35px;
              font-weight: 500;
              color: #6D7681;
            }
          }
          
          .support-col-types {
            display: flex;
            
            .icon {
              cursor: pointer;
              
              &:not(:last-child) {
                margin-right: 35px;
              }
              
              svg {
                width: 32px;
                height: 32px;
                
                fill: #272E3D;
              }
              
              &.active {
                position: relative;
                
                &:before {
                  width: 27px;
                  height: 3px;
                  
                  content: "";
                  position: absolute;
                  top: -40px;
                  
                  background: #4A99EB;
                }
                
                svg {
                  fill: #FFFFFF;
                }
              }
            }
          }
        }
        
        .support-col-body {
          width: 100%;
          height: 100%;
          display: flex;
          flex-direction: column;
          align-items: center;
          justify-content: space-between;
          
          .tickets-list {
            display: flex;
            flex-direction: column;
            align-items: center;
            height: 770px;
            overflow-y: scroll;
            
            .ticket {
              width: 600px;
              height: 154px;
              min-height: 154px;
              position: relative;
              padding: 0 50px;
              display: flex;
              flex-direction: row;
              align-items: center;
              cursor: pointer;
              
              &:hover {
                background: rgba(39, 46, 61, 0.2);
              }
              
              .icon {
                width: 55px;
                height: 94px;
                margin-right: 25px;
                padding: 30px 12px;
                display: flex;
                align-items: center;
                border-radius: 5px;
                background: $secondary-color;
                
                &.icon-customer {
                  background: $secondary-color;
                }
                
                &.icon-support {
                  background: $success-color;
                }
                
                &.icon-closed {
                  background: $danger-color;
                }
                
                svg {
                  width: 32px;
                  height: 32px;
                  fill: $light-color;
                }
              }
              
              .ticket-info {
                .ticket-title {
                  margin-bottom: 8px;
                  font-size: 22px;
                  font-weight: 600;
                  color: #A4B1C1;
                }
                
                .ticket-description {
                  max-width: 320px;
                  
                  margin-bottom: 12px;
                  
                  font-size: 18px;
                  font-weight: 500;
                  
                  color: #6D7681;
                  
                  overflow: hidden;
                  white-space: nowrap;
                  text-overflow: ellipsis;
                }
                
                .ticket-id {
                  font-size: 18px;
                  font-weight: 500;
                  
                  color: #3E444C;
                  
                  span {
                  
                  }
                }
              }
              
              .additional {
                white-space: nowrap;
                display: flex;
                flex-direction: column;
                align-items: flex-end;
                justify-content: center;
                width: 98px;
                margin-left: auto;
                
                .time {
                  margin-bottom: 40px;
                  
                  font-size: 18px;
                  font-weight: 500;
                  
                  color: #6D7681;
                }
                
                .favorite-icon {
                  display: flex;
                  flex-direction: row;
                  align-items: center;
                  justify-content: center;
                  width: 20px;
                  height: 20px;
                  
                  svg {
                    width: 20px;
                    height: 20px;
                    pointer-events: none;
                  }
                  
                  &.favorite-active {
                    svg {
                      fill: $primary-color;
                    }
                  }
                  
                  &.favorite-inactive {
                    svg {
                      fill: $secondary-color;
                    }
                  }
                }
              }
              
              &:after {
                content: "";
                
                width: calc(100% - 50px - 50px);
                height: 1px;
                margin-left: 50px;
                margin-right: 50px;
                position: absolute;
                bottom: 0;
                left: 0;
                
                background: #272E3D;
              }
              
              &:last-child {
                &:after {
                  display: none;
                }
              }
            }
          }
        }
        
        .new-ticket-button {
          width: calc(100% - 50px);
          margin: auto 50px;
          
          .btn {
            width: 100%;
          }
          
          svg {
            width: 20px;
            height: 20px;
            
            margin-right: 10px;
            
            fill: #FFFFFF;
          }
        }
      }
      
      &.chat-window {
        width: 100%;
        padding: 50px;
        border-radius: 5px;
        background: #151921;
        
        .chat-head {
          margin-bottom: 30px;
          
          display: flex;
          flex-direction: row;
          align-items: center;
          justify-content: space-between;
          
          .info {
            display: flex;
            flex-direction: column;
            
            .top-info {
              display: flex;
              flex-direction: row;
              margin-bottom: 15px;
              font-size: 18px;
              font-weight: 500;
              color: #3E444C;
              
              .creation-date {
              }
              
              .top-info-divider {
                margin: 0 20px;
              }
              
              .category {
              }
            }
            
            .bottom-info {
              max-width: 700px;
              
              .ticket-title {
                font-size: 36px;
                font-weight: 700;
                
                color: #FFFFFF;
              }
            }
          }
          
          .close-ticket-button {
            &.active {
              svg {
                margin-right: 0;
              }
            }
            
            svg {
              width: 20px;
              height: 20px;
              margin-right: 12px;
              fill: #EA3B4F;
            }
          }
        }
      }
      
      .chat-body {
        .chat-container {
          max-height: 600px;
          overflow-y: scroll;
          
          &::-webkit-scrollbar {
            width: 5px;
          }
          
          &::-webkit-scrollbar-track {
            width: 5px;
          }
          
          .message {
            max-width: 800px;
            min-height: 125px;
            
            padding: 25px;
            margin-bottom: 40px;
            
            border-radius: 0 22px 22px 22px;
            background: #272F3C;
            
            &.staff {
              margin-left: auto;
              
              border-radius: 22px 22px 0 22px;
              background: #4A99EB;
              
              .body {
                color: #FFFFFF;
              }
              
              .date {
                color: #B3D8FF;
              }
            }
            
            &.close {
              background: $danger-color;
              
              .body {
                color: $light-color;
              }
            }
            
            .body {
              margin-bottom: 20px;
              font-size: 18px;
              font-weight: 500;
              line-height: 30px;
              color: #A4B1C1;
            }
            
            .files {
              margin-bottom: 20px;
              
              img {
                height: 100px;
                width: auto;
                
                &:not(:last-child) {
                  margin-right: 15px;
                }
              }
            }
            
            .date {
              font-size: 18px;
              font-weight: 500;
              
              color: #4A99EB;
            }
          }
        }
        
        .attached {
          margin-top: 50px;
          margin-bottom: 20px;
          display: flex;
          flex-direction: row;
          overflow: hidden;
          
          .file {
            position: relative;
            display: flex;
            align-items: center;
            flex-direction: column;
            justify-content: center;
            width: 100px;
            height: 80px;
            color: $gray-light-color;
            background: $secondary-color;
            border-radius: 15px;
            
            &:not(:last-child) {
              margin-right: 15px;
              margin-bottom: 15px;
            }
            
            .remove {
              position: absolute;
              top: 10px;
              right: 10px;
              display: flex;
              align-items: center;
              flex-direction: row;
              justify-content: center;
              padding: 5px;
              cursor: pointer;
              background: $dark-secondary-color;
              border-radius: 5px;
              
              &:hover {
                svg {
                  fill: $danger-color;
                }
              }
              
              svg {
                width: 15px;
                height: 15px;
                fill: $gray-light-color;
              }
            }
            
            span {
              display: none;
              max-width: 150px;
              font-size: 14px;
              font-weight: 500;
            }
            
            img {
              width: 100px;
              max-width: 100px;
              height: 80px;
              border-radius: 15px;
              object-fit: cover;
            }
          }
        }
        
        .input-closed {
          width: 100%;
          display: flex;
          flex-direction: row;
          align-items: center;
          justify-content: center;
          background: #0F141B;
          border-radius: 5px;
          height: 75px;
          padding: 0 13px 0 22px;
          font-size: 18px;
          font-weight: 500;
          color: $gray-light-color;
        }
        
        .input-button {
          width: 100%;
          display: flex;
          flex-direction: row;
          align-items: center;
          background: #0F141B;
          border-radius: 5px;
          
          &.input-focus {
            box-shadow: 0 0 0 1.5px $primary-color;
          }
          
          &.input-error {
            box-shadow: 0 0 0 1.5px $danger-color;
          }
          
          input {
            height: 75px;
            padding: 0 13px 0 22px;
            width: 100%;
            border-radius: 5px;
            font-size: 18px;
            font-weight: 500;
            color: #FFFFFF;
          }
          
          .icon {
            width: 30px;
            height: 30px;
            
          }
          
          .buttons {
            display: flex;
            flex-direction: row;
            align-items: center;
            margin-left: 15px;
            padding-right: 20px;
            
            .icon {
              margin-right: 35px;
              cursor: pointer;
              
              svg {
                width: 30px;
                height: 30px;
                
                fill: #272E3D;
              }
            }
            
            .send-button {
              svg {
                width: 17px;
                height: 17px;
                
                fill: #FFFFFF;
              }
            }
          }
        }
      }
      
      .chat-body-no-chat {
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: center;
        width: 100%;
        height: calc(100% - 25%);
        
        font-size: 24px;
        font-weight: 600;
      }
    }
  }
}