$breakpoint-tablet: 768px;
$breakpoint-laptop-small: 1024px;
$breakpoint-laptop-large: 1200px;
$breakpoint-desktop: 1600px;

.navigation-bar {
  border-bottom: 1px solid #272E3D;
  
  .language-selector {
    .dropdown-menu {
      min-width: 0;
    }
  }
  
  .logo {
    margin-right: 65px;
    
    font-size: 28px;
    font-family: "TT Lakes Condensed Bold Italic", sans-serif;
    letter-spacing: 0.12em;
    
    span {
      color: #4A99EB;
    }
  }
  
  .sections {
    .navigation-section {
      display: flex;
      
      align-items: center;
      margin-right: 30px;
      
      &:hover {
        span {
          color: #4A99EB;
        }
        
        span.inventory-badge {
          color: #FFFFFF;
        }
        
        .navigation-section-icon {
          fill: #4A99EB;
        }
      }
      
      .navigation-section-icon {
        width: 22px;
        height: 22px;
        
        fill: #6D7681;
      }
      
      &:last-child {
        margin-right: 0;
      }
      
      span {
        margin-left: 12px;
        
        font-size: 18px;
        font-weight: 500;
        color: #6D7681;
      }
      
      .inventory-badge {
        color: #FFFFFF;
      }
    }
  }
  
  span.inventory-badge {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 46px;
    height: 25px;
    margin-left: 18px;
    font-size: 16px;
    font-weight: 600;
    color: #FFFFFF;
    background: #4A99EB;
    border-radius: 12.5px;
  }
  
  .icon {
    width: 25px;
    height: 25px;
    fill: #FFFFFF;
  }
  
  .notifications-button {
    position: relative;
    margin-right: 30px;
    
    .notifications-badge {
      box-sizing: content-box;
      position: absolute;
      top: 8px;
      right: 12px;
      width: 9px;
      height: 9px;
      background: #EA3146;
      border: 3px solid #272E3D;
      border-radius: 50%;
    }
  }
  
  .button {
    width: 47px;
    height: 47px;
  }
  
  .cart-button {
    position: relative;
    
    .cart-badge {
      box-sizing: content-box;
      position: absolute;
      right: -14px;
      display: flex;
      align-items: center;
      justify-content: center;
      width: 18px;
      height: 18px;
      font-size: 12px;
      font-weight: 600;
      background: #EA3B4F;
      border: 5px solid #191E28;
      border-radius: 50%;
    }
  }
  
  .balance-button {
    margin-left: 30px;
    font-size: 18px;
    font-weight: 700;
    color: #5ACD44;
    
    .currency {
      margin-right: 5px;
    }
    
    .amount {
      margin-right: 19px;
    }
    
    .deposit-button {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 31px;
      height: 31px;
      font-size: 38px;
      color: #69717C;
      background: #141B23;
      border-radius: 5px;
    }
  }
  
  .profile-button {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    
    img.profile-image {
      width: 47px;
      height: 47px;
      border-radius: 5px;
    }
    
    .profile-name {
      overflow: hidden;
      max-width: 72px;
      margin: 0 16px 0 18px;
      font-size: 18px;
      font-weight: 500;
      text-overflow: ellipsis;
      white-space: nowrap;
      color: #FFFFFF;
    }
    
    .arrow {
      border-top: 7px solid;
      border-top-color: #FFFFFF;
      border-right: 6px solid transparent;
      border-left: 6px solid transparent;
    }
    
    &.toggle .arrow {
      border-top-color: #4A99EB;
    }
  }
  
  
  .navigation-menu {
    //min-height: 550px;
    
    position: absolute;
    top: 80px;
    right: -10px;
    z-index: 9999;
    
    display: none;
    
    flex-direction: column;
    padding: 30px;
    
    background: #272E3D;
    
    box-shadow: 0 0 5px 0 rgba(15, 20, 27, 0.75);
    border-radius: 5px;
    
    .nav-link {
      display: flex;
      align-items: center;
      
      width: 100%;
      
      height: 30px;
      margin: 11px 0;
      
      cursor: pointer;
      
      &:first-child {
        margin-top: 0;
      }
      
      &:last-child {
        margin-bottom: 0;
      }
      
      &:hover {
        .icon {
          fill: #4A99EB;
        }
        
        span, a {
          color: #FFFFFF;
        }
      }
      
      .icon {
        width: 22px;
        height: 22px;
        
        fill: #6D7681;
      }
      
      span, a {
        margin-left: 15px;
        
        font-size: 20px;
        font-weight: 500;
        
        white-space: nowrap;
        
        color: #6D7681;
      }
      
      .inventory-badge {
        color: #FFFFFF;
      }
    }
    
    .logout-button {
      width: 100%;
      height: 47px;
      
      margin-top: 20px;
      
      background: #4A99EB;
      
      .icon {
        width: 22px;
        height: 22px;
        
        margin-right: 12px;
        
        fill: #FFFFFF;
      }
    }
  }
  
  &.toggle .navigation-menu {
    display: flex;
  }
  
  /* PHONES */
  @media (max-width: $breakpoint-tablet) {
  
  }
  
  /* TABLET (768 - 1024) */
  @media (min-width: $breakpoint-tablet) and (max-width: $breakpoint-laptop-small) {
    .container-left {
      margin-right: 15px;
    }
    
    .logo {
      margin-right: 30px;
    }
    
    .language-selector {
      margin-right: 30px;
    }
    
    .sections {
      .support {
        display: none;
      }
      
      .inventory {
        display: none;
      }
    }
    
    .btn {
      margin: 0 10px;
    }
    
    .dropdown-divider {
      display: none;
    }
    
    .sell-button {
      display: none;
    }
    
    .notifications-button {
      display: none;
    }
    
    .balance-button {
      width: 100px;
      
      .amount {
        margin-right: 0;
      }
      
      .deposit-button {
        display: none;
      }
    }
    
    .profile-button {
      margin-left: 10px;
      
      .profile-name {
        display: none;
      }
      
      .arrow {
        margin-left: 15px;
      }
    }
  }
  
  /* LAPTOP (1024 - 1200) */
  @media (min-width: $breakpoint-laptop-small) and (max-width: $breakpoint-laptop-large) {
    .container-left {
      margin-right: 15px;
    }
    
    .logo {
      margin-right: 30px;
    }
    
    .language-selector {
      margin-right: 30px;
    }
    
    .sections {
      .support {
        display: none;
      }
      
      .inventory {
        .inventory-badge {
          display: none;
        }
      }
    }
    
    .btn {
      margin: 0 10px;
    }
    
    .dropdown-divider {
      display: none;
    }
    
    .balance-button {
      width: 100px;
      
      .amount {
        margin-right: 0;
      }
      
      .deposit-button {
        display: none;
      }
    }
    
    .profile-button {
      margin-left: 10px;
      
      .profile-name {
        display: none;
      }
      
      .arrow {
        margin-left: 15px;
      }
    }
  }
  
  /* LAPTOP (1200 - 1600) */
  @media (min-width: $breakpoint-laptop-large) and (max-width: $breakpoint-desktop) {
    .container-left {
      margin-right: 15px;
    }
    
    .logo {
      margin-right: 30px;
    }
    
    .language-selector {
      margin-right: 30px;
    }
    
    .dropdown-menu {
      .support-icon {
        display: none !important;
      }
    }
    
    .sections {
      
      
      .inventory {
        .inventory-badge {
          display: none;
        }
      }
    }
    
    .btn {
      margin: 0 10px;
    }
    
    .dropdown-divider {
      display: none;
    }
    
    .balance-button {
      width: 100px;
      
      .amount {
        margin-right: 0;
      }
      
      .deposit-button {
        display: none;
      }
    }
    
    .profile-button {
      margin-left: 10px;
      
      .profile-name {
        display: none;
      }
      
      .arrow {
        margin-left: 15px;
      }
    }
  }
  
  /* DESKTOP */
  @media (min-width: $breakpoint-laptop-large) {
    .dropdown-menu {
      .support-icon {
        display: none !important;
      }
    }
  }
}