.floatbar {
  display: flex;
  align-items: center;
  flex-direction: row;
  width: 100%;
  height: 100%;
  border-radius: 5px;
  position: relative;
  
  .fn {
    width: 7%;
    max-width: 7%;
    height: 100%;
    background: linear-gradient(90deg, #29BF50 0%, #29BF50 100%);
    border-top-left-radius: 5px;
    border-bottom-left-radius: 5px;
  }
  
  .mw {
    width: 8%;
    max-width: 8%;
    height: 100%;
    background: linear-gradient(90deg, #41E687 0%, #41E687 100%);
  }
  
  .ft {
    width: 23%;
    max-width: 23%;
    height: 100%;
    background: linear-gradient(90deg, #FFCD4E 0%, #FFB24E 100%);
  }
  
  .ww {
    width: 7%;
    max-width: 7%;
    height: 100%;
    background: linear-gradient(90deg, #FFA519 0%, #FF8A1A 100%);
  }
  
  .bs {
    width: 55%;
    max-width: 55%;
    height: 100%;
    background: linear-gradient(90deg, #F85335 0%, #ED0D51 100%);
    border-top-right-radius: 5px;
    border-bottom-right-radius: 5px;
  }
  
  .float-arrow {
    position: absolute;
    top: -15px;
    
    svg {
      width: 12px;
      height: 12px;
      fill: #FFFFFF;
      transform: rotate(90deg);
    }
  }
}