@import "./src/assets/styles/colors";

.home-page {
  &.mobile {
    width: 100%;
  
    .carousel {
      position: relative;
      margin-bottom: 50px;
    
      .carousel-indicators {
        position: absolute;
        right: 0;
        bottom: -10px;
        left: 0;
        z-index: 9998;
        display: flex;
        justify-content: center;
        padding-left: 0;
      }
    }
  
    .banner {
      overflow: hidden;
      height: 240px;
      background: $dark-color;
      margin-bottom: 20px;
      position: relative;
      
      img {
        z-index: 0;
        width: 100%;
        height: 100%;
      }
      
      .overlay {
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        z-index: 1;
        width: 100%;
        height: 100%;
        background: rgba(0, 0, 0, 0.15);
      }
      
      .text {
        display: flex;
        align-items: flex-start;
        flex-direction: column;
        justify-content: center;
        width: 100%;
        height: 100%;
        padding: 80px;
        
        .main-text {
          display: flex;
          align-items: flex-start;
          flex-direction: column;
          margin-bottom: 20px;
          
          .main-text-row {
            font-size: 24px;
            font-weight: 700;
          }
        }
        
        .bottom-text {
          display: flex;
          align-items: flex-start;
          flex-direction: column;
          
          .promocode {
            padding: 15px;
            font-size: 18px;
            font-weight: 700;
            background: linear-gradient(90deg, #8439F4, #B43FFF);
            border-radius: 5px;
          }
          
          .subtext {
            margin-top: 15px;
            font-size: 14px;
            font-weight: 500;
            color: $light-color;
          }
        }
      }
    }
    
    .items-block {
      width: 100%;
      margin-bottom: 20px;
      padding: 0 20px;
      
      .items-block-title {
        display: flex;
        align-items: center;
        flex-direction: row;
        margin-bottom: 30px;
        
        & > .col {
          font-size: 24px;
          font-weight: 700;
          color: $light-color;
          
          a {
            margin-top: 15px;
            font-size: 18px;
            font-weight: 500;
            color: $primary-color;
            
            svg {
              margin-left: 10px;
              width: 12px;
              height: 12px;
              fill: $primary-color;
            }
          }
        }
      }
      
      .items {
        display: flex;
        width: 100%;
        overflow-x: scroll;
        
        .item {
          min-width: 260px;
        }
      }
    }
  }
  
  &.non-laptop {
    width: 100%;
  
