          font-size: 24px;
          font-weight: 700;
          color: $light-color;
          
          & > div {
            display: flex;
            flex-direction: row;
            align-items: center;
            justify-content: center;
            
            span.block {
              height: 30px;
              width: 5px;
              background: $primary-color;
              margin-right: 20px;
              
              &.dota {
                background: $danger-color;
              }
            }
          }
          
          a {
            display: flex;
            align-items: center;
            justify-content: center;
            font-size: 18px;
            font-weight: 500;
            
            .arrow-icon {
              margin-left: 20px;
              
              svg {
                width: 12px;
                height: 12px;
                fill: $primary-color;
              }
            }
            
            &:hover {
              color: $primary-color;
            }
          }
        }
      }
      
