}




.swiper-pagination-bullet {
  outline: none;
  width: 80px;
  height: 8px;
  border-radius: 15px;
  margin-right: 25px;
  margin-left: 25px;
  cursor: pointer;
  background: #FFFFFF;
  opacity: .5;
  transition: opacity .6s ease;
  border: none;
  
  &.carousel__dot--selected {
    opacity: 1;
  }
}

.rec-carousel-wrapper {
  width: calc(100% + 100px) !important;
  margin-left: -50px !important;
  
  &.csgo-carousel {
    .rec-carousel {
      .rec-slider-container {
        .rec-swipable {
          .rec-carousel-item {
            .rec-item-wrapper {
              height: 577px;
            }
          }
        }
      }
    }
  }
  
  &.dota-carousel {
    .rec-carousel {
      .rec-slider-container {
        .rec-swipable {
          .rec-carousel-item {
            .rec-item-wrapper {
              height: 564px;
            }
          }
        }
      }
    }
  }
  
  .rec-carousel {
    .rec-arrow {
      box-sizing: border-box;
      transition: 175ms cubic-bezier(0.1, 0.1, 0.25, 1);
      font-size: 32px;
      background: transparent;
      color: $light-color;
      box-shadow: none;
      border-radius: 0;
      border: none;
      padding: 0;
      width: 50px;
      height: 50px;
      min-width: 50px;
      line-height: 50px;
      align-self: center;
      cursor: pointer;
      outline: none;
      
      &:hover {
        color: $primary-color;
        background: transparent;
        box-shadow: none;
      }
      
      &:focus {
        color: $primary-color;
        background: transparent;
        box-shadow: none;
      }
      
      &.rec-arrow-left {
      }
      
      &.rec-arrow-right {
      }
    }
    
    .rec-slider-container {
      margin: 0;
      
      .rec-swipable {
        .rec-carousel-item {
          .rec-item-wrapper {
            height: 100%;
          }
        }
      }
    }
  }
  
  .rec-pagination {
    display: none;
  }
}