@import "src/assets/styles/colors";

.item-sell-by-edit-on-click {
  input {
    width: 90px;
    font-size: 24px;
    font-weight: 700;
    border-bottom: 2px solid $primary-color;
  }
  
  .edit-btn {
    display: flex;
    flex-direction: row;
    align-items: center;
    cursor: pointer;
    color: $gray-light-color;
    
    &:hover {
      color: $primary-color;
      
      svg {
        fill: $primary-color;
      }
    }
    
    svg {
      width: 22px;
      height: 22px;
      margin-right: 5px;
      fill: $gray-light-color;
    }
  }
}