@import "./src/assets/styles/colors";

.seller-page {
  &.mobile {
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: flex-start;
    width: 100%;
    
    &.hide-sidebar {
      .sidebar-filter {
        transform: translateX(-100%);
      }
    }
    
    .sidebar-filter {
      position: fixed;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      z-index: 10000;
      overflow-y: scroll;
      width: 100%;
      max-width: 100%;
      height: 100%;
      min-height: 100vh;
      margin: 0;
      padding: 30px;
      
      .control {
        display: flex;
        align-items: center;
        flex-direction: row;
        justify-content: center;
        width: 100%;
        margin-top: -30px;
        padding: 30px;
        cursor: pointer;
        font-size: 16px;
        font-weight: 600;
        color: $gray-light-color;
        
        &:hover {
          color: $danger-color;
          
          svg {
            fill: $danger-color;
          }
        }
        
        svg {
          width: 16px;
          height: 16px;
          margin-left: 15px;
          fill: $gray-light-color;
        }
      }
      
      .inputs {
        display: flex;
        flex-direction: column;
        
        .price-input-group, .lock-input-group {
          max-width: 100%;
          
          &:first-child {
            margin-bottom: 10px;
          }
        }
      }
      
      .exterior-inputs {
        flex-direction: column;
        
        .exterior-input-group {
          max-width: 100%;
          
          &:first-child {
            margin-bottom: 10px;
          }
        }
      }
      
      .stickers-filter--stickers {
        padding: 0;
        
        .sticker {
          img, svg {
            width: 128px;
            height: 128px;
          }
          
          &:not(:last-child) {
            margin-right: 20px;
          }
        }
      }
    }
    
    .advanced-search-block {
      display: flex;
      align-items: center;
      flex-direction: row;
      width: 100%;
      height: 110px;
      margin-top: 15px;
      margin-bottom: 30px;
      padding: 30px;
      background: $dark-color;
      
      .block {
        width: 100%;
        
        .game-title {
          font-size: 24px;
          font-weight: 700;
          color: $light-color;
        }
      }
      
      .filters {
        display: flex;
        align-items: center;
        flex-direction: row;
        
        .filter {
          display: flex;
          align-items: center;
          flex-direction: row;
          justify-content: center;
          width: 36px;
          height: 36px;
          cursor: pointer;
          border-radius: 5px;
          
          &:not(:last-child) {
            margin-right: 15px;
          }
          
          svg {
            width: 16px;
            height: 16px;
            fill: $gray-light-color;
          }
        }
      }
    }
    
    .seller-info-block {
      display: flex;
      align-items: center;
      flex-direction: column;
      justify-content: center;
      width: 100%;
      padding: 20px;
      background: $dark-color;
      
      .seller-image {
        margin-bottom: 5px;
        
        img {
          width: 80px;
          height: 80px;
          border-radius: 50%;
        }
      }
      
      .seller-nickname {
        display: flex;
        align-items: center;
        flex-direction: row;
        margin-bottom: 15px;
        font-size: 20px;
        font-weight: 600;
        color: $light-color;
        
        .verification-badge {
          position: relative;
          display: flex;
          align-items: center;
          flex-direction: row;
          justify-content: center;
          width: 16px;
          height: 16px;
          margin-left: 10px;
          background: $primary-color;
          border-radius: 50%;
          
          &:after {
            position: absolute;
            top: 3px;
            left: 6px;
            content: "";
            display: block;
            width: 3px;
            height: 7px;
            border-width: 0px 2px 2px 0px;
            border-style: solid;
            border-color: rgb(255, 255, 255);
            transform: rotate(45deg);
          }
        }
      }
      
      .seller-stats-divider {
        width: 1px;
        min-width: 1px;
        height: 12px;
        margin: 0 10px;
        background: $secondary-color;
      }
      
      .seller-stats {
        width: 100%;
        
        .seller-stats-row {
          display: flex;
          align-items: center;
          flex-direction: row;
          justify-content: center;
          font-size: 14px;
          font-weight: 500;
          color: $gray-light-color;
          
          span {
            color: $gray-color;
          }
        }
        
        .seller-stats-col {
          display: flex;
          align-items: center;
          flex-direction: column;
          justify-content: center;
          margin-top: 20px;
        }
        
        .seller-stats-block {
          display: flex;
          align-items: center;
          flex-direction: row;
          flex-wrap: nowrap;
          width: 100%;
          padding: 10px 20px;
          background: $dark-secondary-color;
          border-radius: 5px;
          
          &:not(:last-child) {
            margin-bottom: 10px;
          }
          
          .seller-stat-value {
            width: 30px;
            white-space: nowrap;
          }
          
          .seller-stat-title {
            font-size: 14px;
            font-weight: 500;
            color: $gray-light-color;
          }
        }
      }
    }
    
    .seller-advanced-search {
      display: flex;
      align-items: center;
      flex-direction: row;
      width: 100%;
      margin-top: 15px;
      padding: 30px;
      background: $dark-color;
      
      span {
        font-size: 24px;
        font-weight: 700;
        color: $gray-light-color;
      }
      
      .filters {
        display: flex;
        align-items: center;
        flex-direction: row;
        justify-content: center;
        margin-left: auto;
        
        .filter {
          display: flex;
          align-items: center;
          flex-direction: row;
          justify-content: center;
          width: 36px;
          height: 36px;
          color: $gray-light-color;
          background: $secondary-color;
          border-radius: 5px;
          
          &:not(:last-child) {
            margin-right: 20px;
          }
        }
      }
    }
    
    .seller-content {
      display: flex;
      align-items: center;
      flex-direction: column;
      justify-content: flex-start;
      width: 100%;
      margin-top: 30px;
      padding: 0 10px;
      
      .seller-content-title {
        width: 100%;
        margin-bottom: 20px;
        font-size: 22px;
        font-weight: 700;
      }
    }
    
    .items {
      display: flex;
      align-items: center;
      flex-direction: row;
      flex-wrap: wrap;
      width: calc(100% + 20px);
    }
  }
  
