  &.laptop {
    display: flex;
    align-items: flex-start;
    flex-direction: row;
    justify-content: space-between;
    width: 100%;
    margin-top: 85px;
    
    .sidebar-filter {
      margin-top: 0;
      margin-left: 0;
    }
    
    .seller-page-content {
      display: flex;
      align-items: flex-start;
      flex-direction: column;
      width: calc(100% - 430px);
      margin-top: 20px;
      margin-left: 20px;
      white-space: nowrap;
      transition: 250ms cubic-bezier(0.1, 0.1, 0.25, 1) all;
      
      &.sidebar-toggled {
        width: 100%;
        margin-left: -430px;
        
        .market-filter-block {
          display: flex;
          align-items: center;
          flex-direction: row;
          overflow: hidden;
          width: 100%;
          margin-bottom: 20px !important;
          padding: 20px 30px;
          background: $dark-color;
          border-radius: 5px;
          
          .left, .right {
            display: flex;
            align-items: center;
            flex-direction: row;
            justify-content: center;
            margin: 0;
          }
          
          .left {
            margin-right: auto;
            
            .hide-btn {
              margin-right: 15px;
              color: $gray-light-color;
              
              svg {
                width: 12px;
                height: 12px;
                margin-right: 10px;
                transition: 1000ms cubic-bezier(0.1, 0.1, 0.25, 1) all;
                transform: rotate(180deg);
                fill: $gray-light-color;
              }
              
              &:hover {
                color: $light-color;
                
                svg {
                  fill: $light-color;
                }
              }
            }
            
            .game-filter {
              margin-right: 15px;
            }
            
            .filters-amount {
              display: flex;
              align-items: center;
              flex-direction: row;
              font-size: 18px;
              font-weight: 500;
              
              svg {
                width: 19px;
                height: 19px;
                margin-right: 10px;
                fill: $primary-color;
              }
            }
          }
          
          .right {
            .select {
              display: flex;
              align-items: center;
              justify-content: center;
              width: auto;
              height: 53px;
            }
            
            .view-btns {
              display: flex;
              align-items: center;
              flex-direction: row;
              justify-content: center;
              margin-left: 50px;
              
              .list-btn {
                position: relative;
                display: flex;
                align-items: center;
                justify-content: center;
                margin-right: 20px;
                cursor: pointer;
                
                svg {
                  width: 25px;
                  height: 25px;
                  fill: $gray-light-color;
                }
              }
              
              .card-btn {
                position: relative;
                display: flex;
                align-items: center;
                justify-content: center;
                cursor: pointer;
                
                svg {
                  width: 25px;
                  height: 25px;
                  fill: $gray-light-color;
                }
                
                &.active {
                  &:after {
                    position: absolute;
                    top: -37px;
                    right: 0;
                    left: 0;
                    content: "";
                    height: 3px;
                    background: $primary-color;
                  }
                  
                  svg {
                    fill: $light-color;
                  }
                }
              }
            }
          }
        }
      }
      
      .market-filter-block {
        display: flex;
        flex-direction: column;
        width: 100%;
        margin-bottom: 20px !important;
        padding: 20px 30px;
        background: $dark-color;
        border-radius: 5px;
        
        .left, .right {
          display: flex;
          align-items: center;
          flex-direction: row;
          justify-content: space-between;
          margin: 10px;
          
          & > * {
