              display: flex;
              align-items: center;
              justify-content: center;
              cursor: pointer;
              
              svg {
                width: 25px;
                height: 25px;
                fill: $gray-light-color;
              }
              
              &.active {
                //&:after {
                //  position: absolute;
                //  top: -37px;
                //  right: 0;
                //  left: 0;
                //  content: "";
                //  height: 3px;
                //  background: $primary-color;
                //}
                
                svg {
                  fill: $light-color;
                }
              }
            }
          }
        }
      }
      
      .seller-block {
        display: flex;
        align-items: center;
        flex-direction: column;
        width: 100%;
        margin-bottom: 20px;
        padding: 50px;
        background: $dark-color;
        border-radius: 5px;
        
        .seller-image {
