            height: 100%;
            border-radius: 50%;
          }
        }
        
        .seller-bio {
          display: flex;
          align-items: center;
          flex-direction: column;
          width: 100%;
          height: 100%;
          margin-top: 25px;
          margin-bottom: 25px;
          
          .seller-nickname {
            display: flex;
            align-items: center;
            flex-direction: row;
            margin-bottom: 15px;
            font-size: 36px;
            font-weight: 700;
            color: $light-color;
            
            .verification {
              position: relative;
              display: flex;
              align-items: center;
              justify-content: center;
              width: 29px;
              height: 29px;
              margin-left: 20px;
              background: $primary-color;
              border-radius: 50%;
              
              &:after {
                position: absolute;
                top: 7px;
                left: 11px;
                content: "";
                display: block;
                width: 5px;
                height: 10px;
                border-width: 0 3px 3px 0;
                border-style: solid;
                border-color: #FFFFFF;
                transform: rotate(45deg);
              }
            }
          }
          
          .seller-duration {
            font-size: 21px;
            font-weight: 600;
            color: $gray-light-color;
          }
        }
        
        .seller-stats {
          display: flex;
          align-items: center;
          flex-direction: row;
          justify-content: center;
          height: 100%;
          
