            display: flex;
            align-items: center;
            flex-direction: column;
            justify-content: space-between;
            height: 100%;
            
            &.vertical {
              margin-left: 20px;
              
              .seller-stat {
                display: flex;
                align-items: center;
                flex-direction: column;
                width: 185px;
                padding: 25px;
                background: $dark-secondary-color;
                border-radius: 5px;
                
                .stat-icon {
                  display: flex;
                  align-items: center;
                  flex-direction: row;
                  justify-content: center;
                  width: 55px;
                  height: 55px;
                  margin-right: 0;
                  margin-bottom: 10px;
                  
                  svg {
                    width: 55px;
                    height: 55px;
                    fill: $primary-color;
                  }
                }
                
                .stat-value {
                  font-size: 36px;
                  font-weight: 700;
                  color: $light-color;
                }
                
                .stat-description {
                  font-size: 18px;
                  font-weight: 600;
                  color: $gray-light-color;
                }
              }
            }
            
            .seller-stat {
              display: flex;
              align-items: center;
              flex-direction: row;
              width: 100%;
              height: 100%;
              margin-bottom: 20px;
              
              &:last-child {
                margin-bottom: 0;
              }
              
              .stat-icon {
                display: flex;
                align-items: center;
                justify-content: center;
                width: 40px;
                min-width: 40px;
                height: 40px;
                margin-right: 20px;
                background: $dark-secondary-color;
                border-radius: 50%;
                
                &.danger {
                  background: $danger-color;
                }
                
                &.success {
                  background: $success-color;
                }
                
                svg {
                  width: 20px;
                  height: 20px;
                  fill: $light-color;
                }
              }
              
              .seller-stat-block {
                display: flex;
                align-items: center;
                flex-direction: row;
                justify-content: center;
                width: 100%;
                max-height: 84px;
                padding: 20px 30px;
                background: $dark-secondary-color;
                border-radius: 5px;
                
                .stat-value {
                  display: flex;
                  width: 74px;
                  min-width: 74px;
                  max-width: 74px;
                  margin-right: 15px;
                  font-size: 36px;
                  font-weight: 700;
                  white-space: nowrap;
                }
                
                .stat-description {
