@import "src/assets/styles/colors";

.modal-wrapper {
  position: fixed;
  top: 0;
  
  left: 0;
  z-index: 10000;
  display: none;
  align-items: center;
  
  flex-direction: row;
  justify-content: center;
  width: 100%;
  height: 100%;
  
  background: rgba(0, 0, 0, 0.5);
  
  &.active {
    display: flex;
  }
  
  .modal-block {
    position: relative;
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;
    overflow: hidden;
    width: 700px;
    height: 560px;
    background: #272E3D;
    border-radius: 5px;
    
    @keyframes rotate {
      100% {
        transform: rotate(1turn);
      }
    }
    
    &.modal-block--hover {
      background: transparent;
      
      &:before {
        position: absolute;
        z-index: -2;
        content: "";
        width: 200%;
        height: 200%;
        background-color: #399953;
        background-image: linear-gradient(#399953, #399953), linear-gradient(#FBB300, #FBB300), linear-gradient(#D53E33, #D53E33), linear-gradient(#377AF5, #377AF5);
        background-repeat: no-repeat;
        background-position: 0 0, 100% 0, 100% 100%, 0 100%;
        background-size: 50% 50%, 50% 50%;
        animation: rotate 4s linear infinite;
      }
      
      &:after {
        position: absolute;
        top: 6px;
        left: 6px;
        z-index: -1;
        content: "";
        width: calc(100% - 12px);
        height: calc(100% - 12px);
        background: #272E3D;
        border-radius: 5px;
      }
    }
    
    .close-button {
      position: absolute;
      top: 20px;
      right: 20px;
      cursor: pointer;
      
      svg {
        width: 20px;
        height: 20px;
        fill: #FFFFFF;
      }
    }
    
    .modal-body {
      display: flex;
      align-items: center;
      flex-direction: column;
      
      .icon {
        margin-bottom: 35px;
        
        svg {
          width: 48px;
          height: 60px;
          
          fill: #4A99EB;
        }
      }
      
      .text {
        .main-text {
          margin-bottom: 30px;
          
          font-size: 36px;
          font-weight: 700;
          
          text-align: center;
        }
        
        .sub-text {
          margin-bottom: 30px;
          
          font-size: 18px;
          font-weight: 500;
          
          text-align: center;
          
          color: #6D7681;
        }
      }
      
      .button-open-file {
        width: 114px;
        height: 47px;
        
        margin-bottom: 30px;
        padding: 13px 20px;
      }
      
      #file {
        display: none;
      }
      
      .files-uploaded {
        display: flex;
        align-items: center;
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: center;
        margin: -5px;
        padding: 25px 30px;
        font-size: 14px;
        font-weight: 400;
        
        color: $gray-light-color;
        
        span {
          padding: 5px;
        }
      }
      
      .max-file-size {
        margin-bottom: 10px;
        font-size: 18px;
        font-weight: 500;
        color: #6D7681;
      }
      
      .max-files {
        margin-bottom: 10px;
        font-size: 18px;
        font-weight: 500;
        color: #6D7681;
      }
      
      .supported-file-types {
        font-size: 18px;
        font-weight: 500;
        color: #6D7681;
      }
    }
  }
}