@import "src/assets/styles/colors";

.sidebar-filter-dropdown-menu {
  display: flex;
  flex-direction: column;
  align-items: center;
  color: $light-color;
  font-size: 18px;
  font-weight: 600;
  width: 100%;
  position: relative;
  
  .dropdown-menu--head {
    display: flex;
    flex-direction: row;
    align-items: center;
    cursor: pointer;
    width: 100%;
    padding: 20px;
    border-radius: 5px;
    background: $secondary-color;
    
    .dropdown-menu--title {
      display: flex;
      flex-direction: row;
      align-items: center;
      user-select: none;
      
      span {
        margin-left: 5px;
        color: $gray-light-color;
      }
    }
    
    .dropdown-menu--controls {
      display: flex;
      flex-direction: row;
      align-items: center;
      margin-left: auto;
      
      .dropdown-menu--reset {
        margin-right: 10px;
        display: flex;
        flex-direction: row;
        align-items: center;
        transform: rotate(45deg);
        
        &:hover {
          svg {
            fill: $primary-color;
          }
        }
        
        svg {
          pointer-events: none;
          width: 14px;
          height: 14px;
          fill: $light-color;
        }
      }
      
      .dropdown-menu--arrow {
        border-right: 6px solid transparent;
        border-left: 6px solid transparent;
        border-top: 7px solid;
        border-top-color: $light-color;
        
        &.active {
          border-top-color: $primary-color;
        }
      }
    }
  }
  
  .dropdown-menu--options {
    margin-top: 1px;
    max-height: 310px;
    overflow-y: auto;
    display: flex;
    flex-direction: column;
    width: 100%;
    z-index: 999;
    border-radius: 5px;
    background: $secondary-color;
    
    .dropdown-menu--option {
      display: flex;
      flex-direction: row;
      align-items: center;
      padding: 20px;
      cursor: pointer;
      color: $gray-light-color;
      user-select: none;
      
      &:hover {
        background: #232A37;
      }
    }
  }
  
  label {
    display: flex;
    flex-direction: row;
    align-items: center;
    font-size: 18px;
    font-weight: 500;
    color: $gray-light-color;
    cursor: pointer;
    user-select: none;
    
    input[type="checkbox"] {
      position: relative;
      width: 24px;
      min-width: 24px;
      height: 24px;
      min-height: 24px;
      margin: 0 15px 0 0;
      border-radius: 5px;
      background: $dark-color;
      appearance: none;
      outline: 0;
      cursor: pointer;
      transition: background 175ms cubic-bezier(0.1, 0.1, 0.25, 1);
      
      &:before {
        content: "";
        position: absolute;
        display: block;
        opacity: 0;
      }
      
      &:checked {
        border-color: $primary-color;
        background: $primary-color;
        
        &::before {
          opacity: 1;
          border-style: solid;
          border-color: #FFFFFF;
          border-width: 0 3px 3px 0;
          transform: rotate(45deg);
          top: 4px;
          left: 8px;
          width: 5px;
          height: 10px;
        }
        
        ~ span, ~ label {
          color: $light-color;
        }
      }
      
      &:indeterminate {
        border-color: $primary-color;
        background: $primary-color;
        
        &::before {
          opacity: 1;
          content: "+";
          top: -8px;
          bottom: 0;
          right: 0;
          left: 2px;
          font-size: 32px;
        }
        
        ~ span, ~ label {
          color: $light-color;
        }
      }
    }
  }
}
