@import "src/assets/styles/colors";

.transaction-item {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  height: 125px;
  width: 100%;
  padding: 30px;
  position: relative;
  white-space: nowrap;
  background: #212734;
  border-radius: 5px;
  transition: 375ms cubic-bezier(0.1, 0.1, 0.25, 1), z-index 0s 175ms;
  
  &:not(:last-child) {
    margin-bottom: 15px;
  }
  
  .transaction-item-divider {
    width: 1px;
    min-width: 1px;
    height: 64px;
    margin: 0 40px;
    background: #30394B;
  }
  
  .transaction-visual {
    display: flex;
    flex-direction: row;
    align-items: center;
    margin-right: 50px;
    
    .item-visual-main {
      display: flex;
      align-items: center;
      width: 125px;
      margin-right: 30px;
      
      img {
        width: auto;
        max-width: 125px;
        height: auto;
      }
    }
    
    .item-visual-secondary {
      display: flex;
      flex-direction: row;
      align-items: center;
      flex-wrap: wrap;
      width: 100px;
      
      .item-sticker {
        width: 36px;
        height: 36px;
        cursor: pointer;
        
        img, svg {
          width: 36px;
          height: 36px;
        }
        
        svg {
          fill: $secondary-color;
        }
      }
    }
  }
  
  .transaction-item-info {
    display: flex;
    flex-direction: column;
    width: 190px;
    max-width: 190px;
    
    .item-name-main {
      display: flex;
      flex-direction: row;
      align-items: center;
      margin-bottom: 5px;
      font-size: 14px;
      font-weight: 500;
      color: $gray-light-color;
      
      .item-name-main-additional {
        margin-left: 15px;
      }
    }
    
    .item-name-secondary {
      margin-bottom: 20px;
      font-size: 20px;
      font-weight: 600;
      color: $light-color;
    }
    
    .item-status {
      font-size: 18px;
      font-weight: 600;
      color: $gray-color;
      
      span {
        color: $gray-light-color;
      }
    }
  }
  
  .transaction-price-method {
    display: flex;
    flex-direction: column;
    width: 190px;
    max-width: 190px;
    
    .mb {
      display: block;
      margin-bottom: 39px;
    }
    
    .sell-item {
      display: flex;
      flex-direction: row;
      align-items: center;
      font-size: 24px;
      font-weight: 700;
      cursor: pointer;
      color: $primary-color;
    }
    
    .price {
      display: flex;
      flex-direction: row;
      align-items: center;
      margin-bottom: 20px;
      font-size: 24px;
      font-weight: 700;
      
      span.currency {
        margin-right: 5px;
        color: $gray-light-color;
      }
    }
    
    .method {
      font-size: 18px;
      font-weight: 600;
      color: $gray-color;
      
      span {
        font-weight: 500;
        color: $gray-light-color;
      }
    }
  }
  
  .transaction-information {
    display: flex;
    flex-direction: row;
    width: 410px;
    max-width: 410px;
    
    .transaction-information-block {
      display: flex;
      flex-direction: column;
      min-width: 150px;
      max-width: 220px;
      
      &:not(:last-child) {
        margin-right: 30px;
      }
      
      .item-id {
        display: flex;
        flex-direction: row;
        align-items: center;
        margin-bottom: 20px;
        font-size: 18px;
        font-weight: 600;
        color: $gray-color;
        
        span {
          margin-left: 5px;
          font-weight: 500;
          color: $gray-light-color;
        }
      }
      
      .item-float {
        display: flex;
        flex-direction: row;
        align-items: center;
        font-size: 18px;
        font-weight: 600;
        color: $gray-color;
        
        span {
          margin-left: 5px;
          font-weight: 500;
          color: $gray-light-color;
        }
      }
      
      .item-date {
        display: flex;
        flex-direction: row;
        align-items: center;
        margin-bottom: 20px;
        font-size: 18px;
        font-weight: 600;
        color: $gray-color;
        
        span {
          margin-left: 5px;
          font-weight: 500;
          color: $gray-light-color;
        }
      }
      
      .item-lock, .sell-date, .withdrawn-date {
        display: flex;
        flex-direction: row;
        align-items: center;
        font-size: 18px;
        font-weight: 600;
        color: $gray-color;
        
        span {
          margin-left: 5px;
          font-weight: 500;
        }
        
        svg {
          width: 22px;
          height: 22px;
          margin-right: 10px;
          fill: $gray-color;
        }
      }
      
      .withdrawn-date {
        span {
          color: $gray-light-color;
        }
      }
      
      .sell-date {
        span {
          color: $gray-light-color;
        }
      }
      
      .item-lock {
        display: flex;
        flex-direction: row;
        align-items: center;
        
        span {
          margin-left: 5px;
          font-weight: 500;
          color: $gray-light-color;
        }
        
        &.locked {
          color: $danger-color;
          
          svg {
            fill: $danger-color;
          }
          
          span {
            color: $danger-color;
          }
        }
        
        &.unlocked {
          color: $success-color;
          
          svg {
            fill: $success-color;
          }
          
          span {
            color: $success-color;
          }
        }
      }
    }
  }
  
  .transaction-actions {
    display: flex;
    flex-direction: row;
    align-items: center;
    width: 300px;
    
    .bump {
      display: flex;
      flex-direction: column;
      align-items: center;
      width: 100%;
      margin-right: 40px;
      
      .last-bump-title {
        margin-bottom: 20px;
        font-size: 18px;
        font-weight: 600;
        color: $gray-color;
      }
      
      .last-bump-date {
        font-size: 18px;
        font-weight: 500;
        color: $gray-light-color;
      }
    }
    
    .btn {
      width: 100%;
      
      &.withdraw {
        svg {
          transform: rotate(180deg);
        }
      }
      
      &:disabled {
        span {
          color: $gray-light-color;
        }
        
        svg {
          fill: $gray-light-color;
        }
      }
      
      span {
        color: $light-color;
      }
      
      svg {
        width: 16px;
        margin-right: 10px;
        fill: $light-color;
      }
    }
  }
}