@import "src/assets/styles/colors";

.deposit-card {
  &.mobile-card {
    padding: 30px;
    background: $secondary-color;
    border-radius: 5px;
    
    .deposit-card--title {
      font-size: 18px;
      font-weight: 600;
      color: $light-color;
      margin-bottom: 10px;
    }
    
    .deposit-card--description {
      font-size: 16px;
      font-weight: 500;
      color: $gray-light-color;
      margin-bottom: 25px;
    }
    
    .deposit-card--link {
      font-size: 18px;
      font-weight: 600;
      color: $primary-color;
    }
  }
  
