.item {
  position: relative;
  display: flex;
  align-items: center;
  width: 100%;
  white-space: nowrap;
  background: #212734;
  border-radius: 5px;
  transition: 375ms cubic-bezier(0.1, 0.1, 0.25, 1), z-index 0s 175ms;
}

