.item.large-desktop {
  &.cart {
    flex-direction: row;
    height: 125px;
    margin-bottom: 15px;
    padding: 20px 30px;
    
    .item-block {
      margin: 0 30px;
      
      &:first-child {
        margin-left: 0;
      }
      
      &:last-child {
        margin-right: 0;
        margin-left: auto;
      }
    }
    
    .item-divider {
      width: 1px;
      height: 64px;
      background: #30394B;
    }
    
    .item-images {
      position: relative;
      display: flex;
      align-items: center;
      flex-direction: column;
      justify-content: center;
      width: 125px;
      margin-right: 0;
      
      .item-image {
        display: flex;
        height: 64px;
        
        img {
          max-width: 125px;
          height: 64px;
        }
      }
    }
    
    .stickers-container {
      display: flex;
      align-items: center;
      flex-direction: row;
      justify-content: center;
      
      .sticker-container {
        display: flex;
        align-items: center;
        justify-content: center;
        
        &:hover {
          img, svg {
            transform: scale(2.2);
          }
        }
        
        img, svg {
          width: 28px;
          height: 28px;
        }
        
      }
    }
    
    .item-information {
      width: 150px;
      
      .item-type {
        margin-bottom: 5px;
        font-size: 14px;
        font-weight: 500;
        color: $gray-light-color;
      }
      
      .item-name {
        font-size: 20px;
        font-weight: 600;
        color: $light-color;
      }
      
      .item-lock {
        display: flex;
        align-items: center;
        flex-direction: row;
        margin-top: 15px;
        font-size: 18px;
        font-weight: 600;
        
        .lock-icon {
          width: 20px;
          height: 20px;
          margin-right: 10px;
        }
        
        &.unlocked {
          color: $success-color;
          
          svg {
            fill: $success-color;
          }
        }
        
        &.locked {
          color: $danger-color;
          
          svg {
            fill: $danger-color;
          }
          
        }
      }
    }
    
    .float {
      font-size: 18px;
      font-weight: 700;
      color: $light-color;
    }
    
    
    .item-prices {
      display: flex;
      flex-direction: column;
      width: 170px;
      
      .currency {
        margin-right: 4px;
      }
      
      .item-price {
        display: flex;
        align-items: center;
        flex-direction: row;
        font-size: 30px;
        font-weight: 700;
        color: $light-color;
        
        .discount {
          display: flex;
          align-items: center;
          justify-content: center;
          margin-left: 20px;
          padding: 3px 6px;
          font-size: 14px;
          background: $danger-color;
          border-radius: 5px;
        }
      }
      
      .float {
        margin-top: 10px;
        font-size: 18px;
        font-weight: 600;
        color: $gray-light-color;
        
        .exterior {
          margin-right: 5px;
        }
      }
    }
    
    .item-additional {
      display: flex;
      align-items: center;
      flex-direction: column;
      
      .item-inspect-link {
        display: flex;
        align-items: center;
        flex-direction: row;
        
        svg {
          width: auto;
          height: 18px;
          margin-right: 10px;
          fill: $gray-light-color;
        }
        
        span {
          font-size: 18px;
          font-weight: 600;
          color: $gray-light-color;
        }
        
        &:hover {
          svg {
            fill: $primary-color;
          }
          
          span {
            color: $primary-color;
          }
        }
      }
    }
    
    .delete-item {
      svg {
        fill: $light-color;
      }
      
      &:hover {
        background: $danger-color;
      }
      
      &:active {
        background: darken($danger-color, 5%);
      }
    }
  }
}

