  &.tablet-desktop {
    display: flex;
    align-items: flex-start;
    flex-direction: column;
    flex-basis: 260px;
    width: 100%;
    flex-grow: 1;
    margin-bottom: 10px;
    margin-right: 10px;
    background: $dark-color;
    border-radius: 5px;
    z-index: 0;
    transition: 375ms cubic-bezier(0.1, 0.1, 0.25, 1);
    
    .item-top {
      display: flex;
      align-items: center;
      flex-direction: row;
      justify-content: center;
      width: 100%;
      padding: 20px 20px 0 20px;
      
      .item-game {
        font-size: 18px;
        font-weight: 800;
        color: $gray-light-color;
        
        svg {
          width: 20px;
          height: 20px;
          fill: $gray-light-color;
        }
      }
      
      .inspect-link {
        margin-right: 20px;
        
        svg {
          width: 20px;
          height: 20px;
          fill: $gray-light-color;
        }
      }
      
      .search-link {
        svg {
          width: 20px;
          height: 20px;
          fill: $gray-light-color;
        }
      }
    }
    
    .item-images {
      display: flex;
      align-items: center;
      flex-direction: column;
      width: 100%;
      padding: 20px;
      
      .item-image {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 156px;
        height: 80px;
        
        img {
          width: auto;
          max-width: 156px;
          height: auto;
        }
      }
      
      .item-stickers {
        display: flex;
