.item.transaction {
  .item-divider {
    width: 1px;
    min-width: 1px;
    height: 64px;
    margin: 0 40px;
    background: #30394B;
  }
  
  &.mobile {
    display: flex;
    align-items: center;
    flex-direction: row;
    margin-bottom: 15px;
    padding: 20px;
    justify-content: space-between;
    
    &.mobile-small {
      overflow: hidden;
      flex-direction: row;
      flex-wrap: wrap;
      
      .transaction-top {
        display: flex;
        flex-direction: column;
        width: 100%;
        margin-bottom: 20px;
        
        .item-images {
          margin: 0;
          position: relative;
          
          .item-image {
            width: 100px;
            
            img {
              max-width: 100px;
            }
          }
          
          .item-stickers {
            flex-direction: column;
            top: 0;
            right: 0;
            bottom: auto;
          }
        }
      }
      
      
    }
    
    &.mobile-medium {
      overflow: hidden;
      flex-direction: row;
      flex-wrap: wrap;
      
      .item-transaction-col {
        width: 100%;
        
        &:first-child {
          margin-bottom: 20px;
        }
        
        &:last-child {
          justify-content: space-between;
          align-items: center;
        }
        
        .item-images {
          position: relative;
          
          .item-stickers {
            flex-wrap: nowrap;
            bottom: 0;
          }
        }
      }
      
    }
    
    &.mobile-large {
      overflow: hidden;
      flex-direction: row;
      flex-wrap: wrap;
      
      .item-images {
        position: relative;
        margin-bottom: 20px;
        
        .item-image {
          width: 100px;
          
          img {
            max-width: 100px;
          }
        }
        
        .item-stickers {
          flex-wrap: nowrap;
          bottom: 0;
          width: 100%;
        }
      }
      
      .item-information {
        margin-bottom: 20px;
      }
    }
    
    .item-transaction-col {
      display: flex;
      flex-direction: row;
    }
    
    .item-small-divider {
      height: 12px;
      min-width: 1px;
      width: 1px;
      background: $gray-color;
      margin: 0 7px;
    }
    
    .item-images {
      display: flex;
      flex-direction: row;
      margin-right: 30px;
      
      .item-image {
        display: flex;
        align-items: center;
        width: 85px;
        //height: 64px;
        
        img {
          width: auto;
          max-width: 85px;
          height: auto;
        }
      }
      
      .item-stickers {
        display: flex;
        align-items: center;
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: space-between;
        position: absolute;
        bottom: 20px;
        
        .item-sticker {
          width: 24px;
          height: 24px;
          
          img, svg {
            width: 24px;
            height: 24px;
          }
          
          svg {
            fill: $secondary-color;
          }
        }
      }
    }
    
    .item-information {
      display: flex;
      flex-direction: column;
      width: 170px;
      max-width: 170px;
      
      .item-type {
        margin-bottom: 5px;
        font-size: 14px;
        font-weight: 500;
        color: $gray-light-color;
        display: flex;
        flex-direction: row;
        align-items: center;
      }
      
      .item-name {
        margin-bottom: 5px;
        font-size: 18px;
        font-weight: 600;
        color: $light-color;
      }
      
      .item-status {
        font-size: 14px;
        font-weight: 600;
        color: $gray-color;
        margin-bottom: 5px;
        
        span {
          margin-left: 3px;
          color: $gray-light-color;
        }
      }
      
      .item-id {
        display: flex;
        align-items: center;
        flex-direction: row;
        margin-bottom: 5px;
        font-size: 14px;
        font-weight: 600;
        color: $gray-color;
        
        span {
          margin-left: 3px;
          
          color: $gray-light-color;
        }
      }
      
      .item-float {
        display: flex;
        align-items: center;
        flex-direction: row;
        font-size: 14px;
        font-weight: 600;
        color: $gray-color;
        
        span {
          margin-left: 3px;
          color: $gray-light-color;
        }
      }
    }
    
    .item-transaction-info {
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      width: 175px;
      max-width: 175px;
      
      .item-date {
        display: flex;
        align-items: center;
        flex-direction: row;
        font-size: 14px;
        font-weight: 600;
        color: $gray-color;
        margin-bottom: 5px;
        
        span {
          margin-left: 3px;
          color: $gray-light-color;
        }
      }
      
      .item-lock {
        display: flex;
        align-items: center;
        flex-direction: row;
        font-size: 14px;
        font-weight: 600;
        color: $gray-color;
        
        &.locked {
          svg {
            fill: $danger-color;
          }
        }
        
        &.unlocked {
          color: $success-color;
          
          svg {
            fill: $success-color;
          }
        }
        
        &.sell-date {
          color: $gray-color;
        }
        
        svg {
          width: 14px;
          height: 14px;
          margin-right: 5px;
          fill: $gray-color;
        }
        
        span {
          margin-left: 3px;
          font-weight: 500;
          color: $light-color;
        }
        
        span.item-sell-date {
          color: $gray-light-color;
        }
      }
      
      .price {
        display: flex;
        align-items: center;
        flex-direction: row;
        margin-bottom: 10px;
        
        .price-value {
          margin-right: 10px;
          font-size: 18px;
          font-weight: 600;
          color: $light-color;
        }
        
        .edit-btn {
          font-size: 14px;
          font-weight: 500;
          color: $gray-light-color;
          display: flex;
          flex-direction: row;
          align-items: center;
          justify-content: center;
          
          svg {
            width: 12px;
            height: 12px;
            margin-right: 5px;
            fill: $gray-light-color;
          }
          
          &:hover {
            color: $light-color;
            
            svg {
              fill: $light-color;
            }
          }
        }
      }
      
      .method {
        font-size: 14px;
        font-weight: 600;
        color: $gray-color;
        margin-bottom: 5px;
        
        span {
          margin-left: 3px;
          
          color: $gray-light-color;
        }
      }
    }
    
    .item-bump-info {
      display: flex;
      flex-direction: column;
      
      .action-btn {
        .btn {
          span {
            color: $gray-light-color;
          }
          
          svg {
            width: 12px;
            height: 12px;
            fill: $gray-light-color;
          }
        }
      }
    }
  }
  
