      width: 220px;
      
      .currency {
        margin-right: 4px;
      }
      
      .item-price {
        display: flex;
        align-items: center;
        flex-direction: row;
        font-size: 30px;
        font-weight: 700;
        color: $light-color;
        
        .discount {
          display: flex;
          align-items: center;
          justify-content: center;
          margin-left: 20px;
          padding: 3px 6px;
          font-size: 14px;
          background: $danger-color;
          border-radius: 5px;
        }
      }
      
      .item-steam-price {
        display: flex;
        align-items: center;
        margin-top: 12px;
        font-size: 18px;
        font-weight: 600;
        color: $gray-light-color;
        
        svg {
          margin-right: 5px;
          
          .st0 {
            fill: $gray-light-color !important;
          }
          
          .st1 {
            fill: $dark-color;
          }
        }
        
        .currency {
          margin-right: 2px;
          margin-left: 10px;
        }
      }
    }
    
    .item-additional {
      display: flex;
      flex-direction: row;
      align-items: center;
      
      svg {
        width: 18px;
        height: 18px;
        color: $gray-light-color;
      }
      
      .item-inspect-link {
        display: flex;
        align-items: center;
        flex-direction: row;
        margin-right: 5px;
        
        svg {
          width: auto;
          height: 18px;
          fill: $gray-light-color;
        }
        
        &:hover {
          svg {
            fill: $primary-color;
          }
        }
      }
      
      .delete-item {
        svg {
          fill: $light-color;
        }
        
        &:hover {
          background: $danger-color;
        }
        
        &:active {
          background: darken($danger-color, 5%);
        }
      }
    }
    
  }
  
  &.fullscreen {
    
    flex-direction: row;
    height: 125px;
    margin-bottom: 15px;
    padding: 20px 30px;
    
    .item-block {
      margin: 0 40px;
      
      &:first-child {
        margin-left: 0;
      }
      
      &:last-child {
        margin-right: 0;
        margin-left: auto;
      }
    }
    
    .item-divider {
      width: 1px;
      height: 64px;
      background: #30394B;
    }
    
    .item-images {
      position: relative;
      display: flex;
      align-items: center;
      flex-direction: column;
      justify-content: center;
      width: 125px;
      margin-right: 0;
      
      .item-image {
        display: flex;
        height: 64px;
        
        img {
          max-width: 125px;
          height: 64px;
        }
      }
    }
    
    .stickers-container {
      display: flex;
      align-items: center;
      flex-direction: row;
      justify-content: center;
      
      .sticker-container {
        
        
        display: flex;
        align-items: center;
        justify-content: center;
        
        &:hover {
          img, svg {
            transform: scale(2.2);
          }
        }
        
        img, svg {
          width: 28px;
          height: 28px;
        }
        
      }
    }
    
    .item-information {
      width: 150px;
      
      .item-type {
        margin-bottom: 5px;
        font-size: 14px;
        font-weight: 500;
        color: $gray-light-color;
      }
      
      .item-name {
        font-size: 20px;
        font-weight: 600;
        color: $light-color;
      }
      
      .item-lock {
        display: flex;
        align-items: center;
        flex-direction: row;
        margin-top: 15px;
        font-size: 18px;
        font-weight: 600;
        
        .lock-icon {
          width: 20px;
          height: 20px;
          margin-right: 10px;
        }
        
        &.unlocked {
          color: $success-color;
          
          svg {
            fill: $success-color;
          }
        }
        
        &.locked {
          color: $danger-color;
          
          svg {
            fill: $danger-color;
          }
          
        }
      }
    }
    
    .item-prices {
      display: flex;
      flex-direction: column;
      width: 220px;
      
      .currency {
        margin-right: 4px;
      }
      
      .item-price {
        display: flex;
        align-items: center;
        flex-direction: row;
        font-size: 30px;
        font-weight: 700;
        color: $light-color;
        
        .discount {
          display: flex;
          align-items: center;
          justify-content: center;
          margin-left: 20px;
          padding: 3px 6px;
          font-size: 14px;
          background: $danger-color;
          border-radius: 5px;
        }
      }
      
      .item-steam-price {
        display: flex;
        align-items: center;
        margin-top: 12px;
        font-size: 18px;
        font-weight: 600;
        color: $gray-light-color;
        
        svg {
          margin-right: 5px;
          
          .st0 {
            fill: $gray-light-color !important;
          }
          
          .st1 {
            fill: $dark-color;
          }
        }
        
        .currency {
          margin-right: 2px;
          margin-left: 10px;
        }
      }
    }
    
    .item-additional {
      display: flex;
      align-items: center;
      flex-direction: column;
      width: 290px;
      
      & > .row {
        align-items: center;
        justify-content: space-between;
        
        &:first-child {
          margin-bottom: 20px;
        }
      }
      
      .item-exterior {
        font-size: 18px;
        font-weight: 600;
        color: $gray-light-color;
      }
      
      .item-inspect-link {
        display: flex;
        align-items: center;
        flex-direction: row;
        
        svg {
          width: auto;
          height: 18px;
          margin-right: 10px;
          fill: $gray-light-color;
        }
        
        span {
          font-size: 18px;
          font-weight: 600;
          color: $gray-light-color;
        }
        
        &:hover {
          svg {
            fill: $primary-color;
          }
          
          span {
            color: $primary-color;
          }
        }
      }
      
      .float {
        font-size: 18px;
        font-weight: 700;
        color: $light-color;
      }
      
      .floatbar {
        display: flex;
        align-items: center;
        flex-direction: row;
        width: 225px;
        height: 10px;
        border-radius: 5px;
        
        
        .fn {
          width: 7%;
          height: 100%;
          background: linear-gradient(90deg, #29BF50 0%, #29BF50 100%);
          border-top-left-radius: 5px;
          border-bottom-left-radius: 5px;
        }
        
        .mw {
          width: 8%;
          height: 100%;
          background: linear-gradient(90deg, #41E687 0%, #41E687 100%);
        }
        
        .ft {
          width: 23%;
          height: 100%;
          background: linear-gradient(90deg, #FFCD4E 0%, #FFB24E 100%);
        }
        
        .ww {
          width: 7%;
          height: 100%;
          background: linear-gradient(90deg, #FFA519 0%, #FF8A1A 100%);
        }
        
        .bs {
          width: 65%;
          height: 100%;
          background: linear-gradient(90deg, #F85335 0%, #ED0D51 100%);
          border-top-right-radius: 5px;
          border-bottom-right-radius: 5px;
        }
      }
    }
    
    .delete-item {
      svg {
        fill: $light-color;
      }
      
      &:hover {
        background: $danger-color;
      }
      
      &:active {
        background: darken($danger-color, 5%);
      }
    }
  }
  
  @media (max-width: 1200px) {
    &.tablet {
      .item-additional {
        display: none !important;
      }
    }
  }
}