@import "src/assets/styles/colors";

.item-chart-tooltip {
  padding: 10px 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: 18px;
  font-weight: 600;
  color: $light-color;
  
  span {
    color: $gray-light-color;
    font-size: 18px;
    font-weight: 600;
  }
  
  .date {
    font-size: 20px;
    font-weight: 600;
    margin-bottom: 20px;
  }
  
  .price {
    margin-bottom: 10px;
  }
  
  .volume {
  }
}

.visx-axis-label {
  color: $gray-light-color;
  font-size: 16px;
  font-weight: 500;
}

.visx-bar {
  border-radius: 50%;
}

.visx-axis-tick {
  transform: translateY(10px);
  
  text {
    font-size: 14px;
    font-weight: 500;
    fill: $gray-light-color;
  }
}