.input {
  width: 100%;
  height: 100%;

  position: relative;

  &.input-focus {
    box-shadow: 0 0 0 1.5px inset $primary-color;
  }

  &.input-error {
    box-shadow: 0 0 0 1.5px inset $danger-color;
  }

  &.input-success {
    box-shadow: 0 0 0 1.5px inset $success-color
  }

  input {
    width: 100%;
    height: 100%;
  }
  
  &.large-input {
    input {
      padding: 15px;
    }
  }
}

.input-prepend {
  margin-right: 20px;

  display: flex;
}

.input-append {
  margin-left: 20px;

  display: flex;
}