@import "src/assets/styles/colors";

.cart-item.full {
  position: relative;
  display: flex;
  align-items: center;
  width: 100%;
  white-space: nowrap;
  border-radius: 5px;
  flex-direction: row;
  height: 125px;
  margin-bottom: 15px;
  padding: 20px 30px;
  background: #212734;
  transition: 375ms cubic-bezier(0.1, 0.1, 0.25, 1), z-index 0s 175ms;
  
  .cart-item--divider {
    width: 1px;
    height: 64px;
    background: #30394B;
    margin: 0 40px;
  }
  
  .cart-item--visual {
    margin: 0 40px 0 0;
    
    .cart-item--image {
      position: relative;
      display: flex;
      align-items: center;
      flex-direction: column;
      justify-content: center;
      width: 125px;
      margin-right: 0;
      
      img {
        max-width: 125px;
        height: 64px;
      }
    }
    
    .cart-item--stickers {
      display: flex;
      flex-direction: row;
      align-items: center;
      justify-content: center;
      
      .item-sticker {
        display: flex;
        
        img, svg {
          width: 28px;
          height: 28px;
        }
        
        svg {
          fill: $gray-light-color;
        }
        
        .__react_component_tooltip {
          display: flex;
          flex-direction: column;
          justify-content: center;
          align-items: center;
          max-width: 280px;
          min-width: 150px;
          padding: 15px;
          border-radius: 5px;
          background: $secondary-color;
          font-size: 14px;
          font-weight: 600;
          white-space: normal;
          opacity: 1;
          
          &.place-top {
            &:after {
              border-top-color: $secondary-color;
            }
          }
          
          &.place-left {
            &:after {
              border-left-color: $secondary-color;
            }
          }
          
          &.place-bottom {
            &:after {
              border-bottom-color: $secondary-color;
            }
          }
          
          &.place-right {
            &:after {
              border-right-color: $secondary-color;
            }
          }
          
          .sticker-title {
            font-size: 18px;
            font-weight: 600;
            color: $light-color;
            margin-bottom: 10px;
          }
          
          .sticker-wear {
            font-size: 18px;
            font-weight: 600;
            color: $gray-light-color;
          }
        }
      }
    }
  }
  
  .cart-item--info {
    width: 150px;
    
    .cart-item--main-name {
      margin-bottom: 5px;
      font-size: 14px;
      font-weight: 500;
      color: $gray-light-color;
    }
    
    .cart-item--secondary-name {
      font-size: 20px;
      font-weight: 600;
      color: $light-color;
    }
    
    .cart-item--lock-status {
      display: flex;
      flex-direction: row;
      align-items: center;
      margin-top: 15px;
      font-size: 18px;
      font-weight: 600;
      
      svg {
        min-width: 20px;
        width: 20px;
        height: 20px;
        margin-right: 10px;
      }
      
      &.unlocked {
        color: $success-color;
        
        svg {
          fill: $success-color;
        }
      }
      
      &.locked {
        color: $danger-color;
        
        svg {
          fill: $danger-color;
        }
      }
    }
  }
  
  .cart-item--price {
    display: flex;
    flex-direction: column;
    width: 220px;
    
    .cart-item--our-price {
      display: flex;
      align-items: center;
      flex-direction: row;
      font-size: 30px;
      font-weight: 700;
      color: $light-color;
      
      span.cart-item--discount {
        display: flex;
        align-items: center;
        justify-content: center;
        margin-left: 20px;
        padding: 3px 6px;
        font-size: 14px;
        background: $danger-color;
        border-radius: 5px;
      }
    }
    
    .cart-item--steam-price {
      display: flex;
      align-items: center;
      margin-top: 12px;
      font-size: 18px;
      font-weight: 600;
      color: $gray-light-color;
      
      svg {
        margin-right: 5px;
        width: 24px;
        height: 24px;
        border-radius: 100%;
        
        .st0 {
          fill: $gray-light-color !important;
        }
        
        .st1 {
          fill: $dark-color;
        }
      }
      
      span {
        margin-right: 5px;
      }
    }
  }
  
  .cart-item--additional {
    display: flex;
    align-items: center;
    flex-direction: column;
    width: 290px;
    
    .row {
      align-items: center;
      justify-content: space-between;
      
      &:first-child {
        margin-bottom: 20px;
      }
      
      .cart-item--exterior {
        font-size: 18px;
        font-weight: 600;
        color: $gray-light-color;
      }
      
      .cart-item--steam-inspect {
        display: flex;
        flex-direction: row;
        align-items: center;
        font-size: 18px;
        font-weight: 600;
        color: $gray-light-color;
        
        &:hover {
          color: $primary-color;
          
          svg {
            fill: $primary-color;
          }
        }
        
        &:hover {
          color: $primary-color;
          
          svg {
            
            fill: $primary-color;
          }
        }
        
        svg {
          min-width: 20px;
          width: 20px;
          height: 20px;
          margin-right: 10px;
          fill: $gray-light-color;
        }
      }
      
      .cart-item--float {
        font-size: 18px;
        font-weight: 700;
        color: $light-color;
      }
      
      .floatbar {
        display: flex;
        align-items: center;
        flex-direction: row;
        width: 225px;
        height: 10px;
        border-radius: 5px;
        
        
        .fn {
          width: 7%;
          height: 100%;
          background: linear-gradient(90deg, #29BF50 0%, #29BF50 100%);
          border-top-left-radius: 5px;
          border-bottom-left-radius: 5px;
        }
        
        .mw {
          width: 8%;
          height: 100%;
          background: linear-gradient(90deg, #41E687 0%, #41E687 100%);
        }
        
        .ft {
          width: 23%;
          height: 100%;
          background: linear-gradient(90deg, #FFCD4E 0%, #FFB24E 100%);
        }
        
        .ww {
          width: 7%;
          height: 100%;
          background: linear-gradient(90deg, #FFA519 0%, #FF8A1A 100%);
        }
        
        .bs {
          width: 65%;
          height: 100%;
          background: linear-gradient(90deg, #F85335 0%, #ED0D51 100%);
          border-top-right-radius: 5px;
          border-bottom-right-radius: 5px;
        }
      }
    }
  }
  
  .cart-item--actions {
    margin-left: auto;
    
    .delete-item {
      display: flex;
      flex-direction: row;
      align-items: center;
      width: 55px;
      height: 55px;
      
      svg {
        fill: $light-color;
        height: 20px;
        width: 20px;
        min-width: 20px;
      }
      
      &:hover {
        background: $danger-color;
        
        svg {
          fill: $light-color;
        }
      }
    }
  }
}