@import "src/assets/styles/colors";

.item-card {
  &.mobile {
    position: relative;
    z-index: 0;
    display: flex;
    align-items: flex-start;
    flex: 1 1 275px;
    flex-direction: column;
    width: 100%;
    min-width: 275px;
    margin: 10px;
    padding: 20px;
    background: $dark-color url("light.png") no-repeat center center;
    border-radius: 5px;
    transition: 175ms cubic-bezier(0.1, 0.1, 0.25, 1);
    
    &:hover {
      z-index: 9998 !important;
      background: lighten($dark-color, 5%) url("light.png") no-repeat center center;
      border-radius: 5px 5px 0 0;
    }
  
    .item-top-container {
      display: flex;
      align-items: center;
      flex-direction: row;
      justify-content: space-between;
      width: 100%;
      margin-bottom: 20px;
      user-select: none;
    
      .item-game {
        font-size: 18px;
        font-weight: 800;
        text-transform: uppercase;
        color: $gray-light-color;
      }
    
      .item-exterior {
        font-size: 18px;
        font-weight: 600;
        color: $gray-light-color;
      }
    
      .item-links {
        display: flex;
        align-items: center;
        flex-direction: row;
      
        .inspect-link {
          margin-right: 20px;
        
          &:hover {
            svg {
              fill: $primary-color;
            }
          }
        
          svg {
            width: 20px;
            height: 20px;
            fill: $gray-light-color;
          }
        }
      
        .search-link {
          &:hover {
            svg {
              fill: $primary-color;
            }
          }
        
          svg {
            width: 20px;
            height: 20px;
            fill: $gray-light-color;
          }
        }
      }
    }
  
    .item-visual {
      display: flex;
      align-items: center;
      flex-direction: column;
      width: 100%;
      margin-bottom: 20px;
    
      .item-image {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 156px;
        height: 80px;
        margin-bottom: 20px;
      
        img {
          width: auto;
          max-width: 156px;
          height: auto;
        }
      }
    
      .item-stickers {
        display: flex;
        align-items: center;
        flex-direction: row;
        height: 36px;
      
        .item-sticker {
          display: flex;
          align-items: center;
          flex-direction: row;
          justify-content: center;
          cursor: pointer;
        
          &:not(:last-child) {
            margin-right: 20px;
          }
        
          img {
            width: 36px;
            height: 36px;
          }
        
          .__react_component_tooltip {
            display: flex;
            align-items: center;
            flex-direction: column;
            justify-content: center;
            min-width: 150px;
            max-width: 280px;
            padding: 15px;
            font-size: 14px;
            font-weight: 600;
            white-space: normal;
            background: $secondary-color;
            border-radius: 5px;
            opacity: 1;
          
            &.place-top {
              &:after {
                border-top-color: $secondary-color;
              }
            }
          
            &.place-left {
              &:after {
                border-left-color: $secondary-color;
              }
            }
          
            &.place-bottom {
              &:after {
                border-bottom-color: $secondary-color;
              }
            }
          
            &.place-right {
              &:after {
                border-right-color: $secondary-color;
              }
            }
          
            .sticker-title {
              margin-bottom: 10px;
              font-size: 18px;
              font-weight: 600;
              color: $light-color;
            }
          
            .sticker-wear {
              font-size: 18px;
              font-weight: 600;
              color: $gray-light-color;
            }
          }
        
        }
      }
    }
  
    .item-info-container {
      position: relative;
      display: flex;
      flex-direction: column;
      width: calc(100% + 40px);
      margin: 0 -20px;
      margin-bottom: 20px;
      padding: 20px;
      background: #171B24;
    
      .item-tag {
        position: absolute;
        top: 20px;
        right: 20px;
        display: flex;
        align-items: center;
        flex-direction: row;
        height: 40px;
      
        &:hover {
          .icon {
            border-radius: 0 5px 5px 0;
          }
        
          .tag {
            width: auto;
            padding: 10px 10px 10px 20px;
            border-radius: 5px 0 0 5px;
          }
        }
      
        .icon {
          display: flex;
          align-items: center;
          flex-direction: row;
          justify-content: center;
          width: 40px;
          height: 40px;
          cursor: pointer;
          background: $secondary-color;
          border-radius: 5px;
          transition: 175ms cubic-bezier(0.1, 0.1, 0.25, 1);
        
        
          svg {
            width: 20px;
            height: 20px;
            fill: $gray-light-color;
          }
        }
      
        .tag {
          display: flex;
          align-items: center;
          flex-direction: row;
          overflow: hidden;
          width: 0;
          height: 40px;
          font-size: 18px;
          font-weight: 500;
          color: $gray-light-color;
          background: $secondary-color;
          border-radius: 5px;
          transition: 175ms cubic-bezier(0.1, 0.1, 0.25, 1);
        }
      }
    
      .item-name-info {
        display: flex;
        align-items: flex-start;
        flex-direction: column;
        margin-bottom: 15px;
      
        .main-name {
          margin-bottom: 5px;
          font-size: 14px;
          font-weight: 500;
          color: $gray-light-color;
        }
      
        .secondary-name {
          font-size: 18px;
          font-weight: 600;
          color: $light-color;
        }
      }
    
      .item-price-info {
        display: flex;
        align-items: flex-start;
        flex-direction: column;
      
        .price {
          display: flex;
          align-items: center;
          flex-direction: row;
          margin-bottom: 15px;
          font-size: 24px;
          font-weight: 700;
          color: $light-color;
        
          .currency {
            margin-right: 5px;
            font-size: 24px;
            color: $gray-light-color;
          }
        
          span {
            display: flex;
            align-items: center;
            justify-content: center;
            margin-left: 20px;
            padding: 3px 6px;
            font-size: 14px;
            background: $danger-color;
            border-radius: 5px;
          }
        }
      
        .steam-price {
          display: flex;
          align-items: center;
          flex-direction: row;
          font-size: 16px;
          font-weight: 600;
          color: $gray-light-color;
        
          span {
            margin-right: 5px;
          }
        
          svg {
            width: 24px;
            height: 24px;
            margin-right: 10px;
            border-radius: 50%;
          
            .st0 {
              fill: $gray-light-color;
            }
          
            .st1 {
              fill: $secondary-color;
            }
          }
        }
      }
    }
  
    .item-float-container {
      display: flex;
      align-items: center;
      flex-direction: row;
      width: 100%;
      height: 22px;
      margin-bottom: 15px;
    
      .float {
        width: 70px;
        font-size: 18px;
        font-weight: 700;
        color: $light-color;
      }
    
      .floatbar {
        display: flex;
        align-items: center;
        flex-direction: row;
        width: 100%;
        height: 10px;
        border-radius: 5px;
      
        .float-arrow {
          top: -18px;
        }
      
        .fn {
          width: 7%;
          height: 100%;
          background: linear-gradient(90deg, #29BF50 0%, #29BF50 100%);
          border-top-left-radius: 5px;
          border-bottom-left-radius: 5px;
        }
      
        .mw {
          width: 8%;
          height: 100%;
          background: linear-gradient(90deg, #41E687 0%, #41E687 100%);
        }
      
        .ft {
          width: 23%;
          height: 100%;
          background: linear-gradient(90deg, #FFCD4E 0%, #FFB24E 100%);
        }
      
        .ww {
          width: 7%;
          height: 100%;
          background: linear-gradient(90deg, #FFA519 0%, #FF8A1A 100%);
        }
      
        .bs {
          width: 65%;
          height: 100%;
          background: linear-gradient(90deg, #F85335 0%, #ED0D51 100%);
          border-top-right-radius: 5px;
          border-bottom-right-radius: 5px;
        }
      }
    }
  
    .item-lock-status-container {
      display: flex;
      align-items: center;
      flex-direction: row;
      justify-content: space-between;
      width: 100%;
    
      span {
        font-size: 18px;
        font-weight: 600;
        color: $gray-light-color;
      }
    
      div {
        display: flex;
        align-items: center;
        flex-direction: row;
        font-size: 18px;
        font-weight: 600;
        color: $light-color;
      
        &.unlocked {
          svg {
            fill: $success-color;
          }
        }
      
        &.locked {
          svg {
            fill: $danger-color;
          }
        }
      
        svg {
          width: 20px;
          height: 20px;
          margin-right: 10px;
          fill: $gray-light-color;
        }
      }
    }
    
    .action-buttons {
      margin: 0 -20px;
      display: flex;
      flex-direction: row;
      width: calc(100% + 40px);
      padding: 20px 20px 0 20px;
      transition: 175ms cubic-bezier(0.1, 0.1, 0.25, 1);
      
      .buy-btn {
        width: 100%;
      }
      
      .cart-btn {
        margin-left: 15px;
        
        &:active {
          svg {
            fill: $light-color;
          }
        }
        
        svg {
          width: 30px;
          height: 30px;
          fill: $gray-light-color;
        }
      }
    }
  }
  
