@import "src/assets/styles/colors";

.no-items-found {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  color: $gray-light-color;
  
  &.mobile {
    .title {
      text-align: center;
      margin-bottom: 30px;
      font-size: 20px;
      font-weight: 600;
    }
    
    .description {
      text-align: center;
      font-size: 16px;
      font-weight: 500;
      margin-bottom: 30px;
    }
    
    .buttons {
      display: flex;
      flex-direction: column;
      align-items: center;
      
      a {
        color: $primary-color;
        margin-right: 30px;
        
        &:not(:last-child) {
          margin-bottom: 15px;
        }
      }
    }
    
    
  }
  
  &.tablet {
    .title {
      text-align: center;
      margin-bottom: 30px;
