@import "./assets/styles/breakpoints.scss";
@import "./assets/styles/colors.scss";

* {
  box-sizing: border-box;
}

html {
  width: 100%;
  height: 100%;
}

body {
  width: 100%;
  //height: 100%;
  margin: 0;
  font-family: "Gilroy", sans-serif;
  color: #FFFFFF;
  //background: #0F141B;
  background: $dark-secondary-color;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
  monospace;
}

a {
  text-decoration: none;
  color: #FFFFFF;
}

ul {
  margin: 0;
  padding: 0;
}

::-webkit-scrollbar {
  width: 6px;
  height: 6px;
}

::-webkit-scrollbar-track {
  background: $dark-color;
}

::-webkit-scrollbar-thumb {
  background: $gray-light-color;
  border-radius: 5px;
  
  &:hover {
    background: lighten($gray-light-color, 5%);
  }
}

input {
  padding: 0;
  font-size: 18px;
  font-weight: 500;
  font-family: "Gilroy", sans-serif;
  color: #FFFFFF;
  background: none;
  border: none;
  
  outline: none;
  
  &::placeholder {
    color: #6D7681;
  }
  
  &[type=number] {
    -moz-appearance: textfield;
  }
  
  &[type=number]:-moz-ui-invalid {
    box-shadow: none;
  }
  
  &[type=number]::-webkit-inner-spin-button,
  &[type=number]::-webkit-outer-spin-button {
    margin: 0;
    -webkit-appearance: none;
  }
}

.content {
  z-index: 999999999 !important;
  //align-items: center;
  display: flex;
  flex-direction: row;
  justify-content: center;
  //padding: 50px !important;
  width: 100%;
  //min-height: calc(100vh - 470px) !important;
  max-width: 2020px;
  
  min-height: calc(100vh - 286px) !important; // by footer height
  margin: 0 auto;
  //margin-top: 170px !important;
  //padding: 50px 0;
}

@media (max-width: $breakpoint-laptop-small) {
  .content {
    min-height: calc(100vh - 482px) !important;
    margin-top: 90px !important;
    padding: 0;
  }
}

.container {
  display: flex;
  justify-content: center;
  width: 100%;
  max-width: 1920px;
  height: 100%;
  margin: 0 auto;
  padding: 0 50px;
}

.page-spacing {
  margin-top: 170px;
  padding: 50px !important;
}

@media (max-width: 1024px) {
  .page-spacing {
    margin-top: 0;
    padding: 0 !important;
    
    & > div {
      padding: 30px !important;
    }
  }
}

@media (max-width: 1200px) {
  .page-spacing {
    padding: 25px 0 !important;
    
    & > div {
      //padding: 35px !important;
      padding: 0;
    }
  }
}

.row {
  display: flex;
  
  flex-direction: row;
  width: 100%;
}

.col {
  display: flex;
  flex-direction: column;
}

