}

.dev-menu {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 9999 !important;
  display: flex;
  align-items: center;
  flex-direction: row;
  justify-content: center;
  width: 100%;
  
  .dev-menu-toggle {
    position: absolute;
    top: 0px;
    left: -5px;
    display: flex;
    align-items: center;
    flex-direction: row;
    justify-content: flex-end;
    width: 40px;
    height: 40px;
    padding: 10px;
    cursor: pointer;
    background: $primary-color;
    border-radius: 0 50% 50% 0;
  
    &.active {
      svg {
        transform: rotate(0deg);
      }
    }
    
    svg {
      transition: 0.25s ease-in-out;
      width: 24px;
      height: 24px;
      fill: $light-color;
      transform: rotate(180deg);
  
    }
  }
  
  .dev-menu-links {
    position: absolute;
    top: 5px;
    left: 50px;
    display: none;
    align-items: flex-start;
    flex-direction: column;
    padding: 20px;
    color: $light-color;
    background: #171C25;
    border: 2px solid #232a38;
    border-radius: 5px;
    
    .done {
      color: $success-color;
      font-weight: 600;
    }
    
    .features {
      color: $warning-color;
    }
    
    .layout {
      color: $danger-color;
    }
    
    .adaptives {
      color: $violet-color;
    }
    
    .done, .layout, .features, .adaptives {
      &:hover {
        color: $primary-color !important;
      }
    }
    
    &.active {
      display: flex;
    }
  }
  
  a {
    font-size: 100%;
    font-weight: 500;
    color: $gray-light-color;
    
    &.active {
      color: $primary-color;
    }
    
    &:hover {
      color: $primary-color;
    }
    
    &:not(:last-child) {
      margin-bottom: 5px;
    }
  }
  
  .link-divider {
    margin: 0 10px;
  }
}

.Toastify {
  .Toastify__toast-container {
    z-index: 10001;
    width: auto;
    max-width: 360px;
    min-height: 56px;
    padding: 0;
    
    &.Toastify__toast-container--top-right {
      top: 25px;
      right: 25px;
    }
    
    .Toastify__toast {
      display: flex;
      align-items: center;
      flex-direction: row;
      margin-bottom: 15px;
      padding: 0;
      font-family: "Gilroy", sans-serif;
      background: $gray-light-color;
      border-radius: 5px;
      
      .Toastify__progress-bar {
        z-index: 1;
        height: 100%;
        background: rgba(39, 46, 61, 0.4);
      }
      
      .Toastify__close-button {
        z-index: 101;
        align-self: center;
        padding-right: 20px;
        padding-left: 20px;
        
        svg {
          width: 16px;
          height: 16px;
        }
      }
      
      .Toastify__toast-body {
        z-index: 100;
        padding: 16px 20px;
        font-size: 16px;
        font-weight: 500;
      }
      
      &.Toastify__toast--error {
        background: $danger-color;
      }
      
      &.Toastify__toast--success {
        background: darken($success-color, 15%);
      }
      
      &.Toastify__toast--warning {
        background: $warning-color;
      }
      
      &.Toastify__toast--info {
        background: $primary-color;
      }
    }
  }
}