@import "./src/assets/styles/colors";

.mobile-footer {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  background: $dark-color;
  width: 100%;
  height: 100%;
  min-height: 250px;
  padding: 30px;
  overflow: hidden;
  margin-top: 30px;
  
  .footer-links {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;
    margin-bottom: 50px;
    
    a {
      font-size: 16px;
      font-weight: 500;
      color: $gray-light-color;
      margin: 12px;
      white-space: nowrap;
    }
  }
  
  .footer-block {
    display: flex;
    flex-direction: column;
    width: 100%;
    margin-bottom: 50px;
    
    .footer-block-header {
      font-size: 18px;
      font-weight: 500;
      margin-bottom: 15px;
      display: flex;
      flex-direction: row;
      align-items: center;
      justify-content: flex-start;
    }
    
    .footer-block-content {
      padding-bottom: 5px; // scroll adjustment
      display: flex;
      flex-direction: row;
      align-items: center;
      flex-wrap: nowrap;
      width: 100%;
      overflow-x: scroll;
      
      .social {
        width: 40px;
        height: 40px;
        margin-right: 15px;
        display: flex;
        align-items: center;
        justify-content: center;
        background: $secondary-color;
        border-radius: 12.5px;
        
        svg {
          width: 40px;
          height: 40px;
          
          .st0 {
            fill: $secondary-color;
          }
          
          .st1 {
            fill: $gray-light-color;
          }
        }
      }
      
      .payment {
        width: 70px;
        min-width: 70px;
        height: 30px;
        margin-right: 15px;
        display: flex;
        align-items: center;
        justify-content: center;
        background: $secondary-color;
        border-radius: 5px;
        
        svg {
          height: 20px;
          
          .st0 {
            fill: $secondary-color;
          }
          
          .st1 {
            fill: $gray-light-color;
          }
          
          .crypto-1 {
            fill: $secondary-color;
          }
        }
      }
    }
  }
  
  .footer-logo {
    display: flex;
    flex-direction: column;
    width: 100%;
    align-items: center;
    justify-content: center;
    
    .logo {
      width: 100%;
      text-align: center;
      margin-bottom: 15px;
      font-size: 24px;
      font-family: "TT Lakes Condensed Bold Italic", sans-serif;
      letter-spacing: 0.12em;
      
      & > span {
        font-size: inherit;
        color: #4A99EB;
      }
    }
    
    span {
      font-size: 14px;
      font-weight: 500;
      color: $gray-color;
    }
  }
}