@import "src/assets/styles/colors.scss";

.profile-page {
  &.no-scroll {
    height: calc(100vh - 86px);
    overflow: hidden;
  }
  
  &.mobile {
    width: 100%;
    
    .profile-sidebar {
      width: 100%;
      height: 100%;
      min-width: 100vw;
      min-height: 100vh;
      background: $dark-secondary-color;
      position: fixed;
      z-index: 10000;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      transform: translateX(0%);
      transition: 0.5s all;
      
      &.hide {
        transform: translateX(100%);
      }
      
      .profile-sidebar-back {
        display: flex;
        flex-direction: row;
        align-items: center;
        height: 80px;
        background: $dark-color;
        padding: 30px;
        font-size: 16px;
        font-weight: 600;
        color: $gray-light-color;
        margin-bottom: 10px;
        
        svg {
          width: 16px;
          height: 16px;
          margin-right: 10px;
          transform: rotate(180deg);
          fill: $gray-light-color;
        }
      }
      
      .profile-sidebar-content {
        width: 100%;
        height: 100%;
        
        .profile-sidebar-tab {
          width: 100%;
          height: calc(100% - 80px);
          overflow-y: scroll;
          
          &.hide {
            display: none;
          }
          
          .profile-sidebar-container {
            padding: 30px;
            background: $dark-color;
            
            &:not(:last-child) {
              margin-bottom: 10px;
            }
            
            &.fee {
              font-size: 18px;
              font-weight: 500;
              color: $gray-light-color;
              display: flex;
              flex-direction: row;
              align-items: center;
              
              span {
                margin-left: 10px;
                font-weight: 600;
                color: $light-color;
              }
              
              a {
                margin-left: auto;
                
                .question-icon {
                  color: $gray-light-color;
                }
              }
            }
            
            &.chart-container {
              
              .chart-data {
                display: flex;
                flex-direction: column;
                
                .chart-data-group {
                  display: flex;
                  flex-direction: column;
                  margin-bottom: 15px;
                  
                  .chart-data-title {
                    font-size: 18px;
                    font-weight: 500;
                    color: $gray-light-color;
                    margin-bottom: 5px;
                  }
                  
                  .chart-data-number {
                    display: flex;
                    flex-direction: row;
                    align-items: center;
                    
                    font-size: 20px;
                    font-weight: 600;
                    color: $light-color;
                    
                    span {
                      margin-left: 5px;
                    }
                  }
                }
              }
              
              .chart {
              
              }
            }
            
            .profile-sidebar-tab-title {
              display: flex;
              flex-direction: row;
              align-items: center;
              justify-content: center;
              width: 100%;
              text-align: center;
              font-size: 24px;
              font-weight: 700;
              color: $light-color;
              margin-bottom: 30px;
            }
            
            .profile-sidebar-input-group {
              display: flex;
              flex-direction: column;
              
              &:not(:last-child) {
                margin-bottom: 20px;
              }
              
              .input-group-title {
                display: flex;
                flex-direction: row;
                align-items: center;
                width: 100%;
                font-size: 18px;
                font-weight: 600;
                color: $light-color;
                margin-bottom: 15px;
                
                a, span {
                  margin-left: auto;
                  display: flex;
                  flex-direction: row;
                  align-items: center;
                  justify-content: center;
                  font-size: 14px;
                  font-weight: 500;
                  color: $gray-light-color;
                  
                  .question-icon {
                    margin-left: 15px;
                  }
                  
                  .switch input {
                    margin: 0;
                  }
                }
              }
              
              .input {
                padding: 15px;
                width: 100%;
                background: $dark-secondary-color;
                border-radius: 5px;
                font-size: 16px;
                
                &:not(:last-child) {
                  margin-bottom: 10px;
                }
                
                input {
                  font-size: 16px;
                }
              }
            }
            
            .store-link {
              display: flex;
              flex-direction: row;
              align-items: center;
              width: 100%;
              padding: 15px;
              font-size: 16px;
              font-weight: 500;
              border-radius: 5px;
              color: $gray-light-color;
              background: $dark-secondary-color;
              position: relative;
              
              .copy-icon {
                background: $primary-color;
                position: absolute;
                top: 7px;
                right: 7px;
                bottom: 7px;
                display: flex;
                flex-direction: row;
                align-items: center;
                justify-content: center;
                border-radius: 5px;
                height: 36px;
                width: 36px;
                
                svg {
                  width: 20px;
                  height: 20px;
                  fill: $light-color;
                }
              }
            }
            
            .question-icon {
              display: flex;
              flex-direction: row;
              align-items: center;
              justify-content: center;
              width: 24px;
              min-width: 24px;
              height: 24px;
              min-height: 24px;
              border-radius: 50%;
              background: $secondary-color;
              color: $gray-light-color;
              font-size: 14px;
              font-weight: 600;
            }
            
            .referral-cards {
              display: flex;
              flex-direction: column;
              align-items: center;
              margin-bottom: 30px;
              
              .referral-card {
                display: flex;
                flex-direction: row;
                align-items: center;
                width: 100%;
                height: 98px;
                padding: 20px;
                background: $secondary-color;
                border-radius: 5px;
                
                &:not(:last-child) {
                  margin-bottom: 10px;
                }
                
                .referral-card-data {
                  display: flex;
                  flex-direction: column;
                  justify-content: space-between;
                  height: 100%;
                  
                  & > span {
                    font-size: 18px;
                    font-weight: 600;
                    color: $gray-light-color;
                  }
                  
                  div {
                    font-size: 24px;
                    font-weight: 600;
                    color: $light-color;
                    
                    span {
                      font-size: 24px;
                      font-weight: 600;
                    }
                  }
                }
                
                .icon {
                  margin-left: auto;
                  width: 45px;
                  height: 45px;
                  border-radius: 15px;
                  display: flex;
                  flex-direction: row;
                  align-items: center;
                  justify-content: center;
                  
                  svg {
                    width: 24px;
                    height: 24px;
                    fill: $light-color;
                  }
                  
                  &.icon-bg-primary {
                    background: transparentize($primary-color, 0.25);
                    
                    svg {
                      fill: $light-color;
                    }
                  }
                  
                  &.icon-bg-success {
                    background: transparentize($success-color, 0.25);
                    
                    svg {
                      fill: $light-color;
                    }
                  }
                  
                  &.icon-bg-violet {
                    background: transparentize($violet-color, 0.25);
                    
                    svg {
                      fill: $light-color;
                    }
                  }
                }
              }
            }
            
            .referral-link-container {
              margin-bottom: 30px;
              
              .referral-link-title {
                font-size: 18px;
                font-weight: 500;
                color: $light-color;
                margin-bottom: 15px;
              }
              
              .referral-link {
                display: flex;
                flex-direction: row;
                align-items: center;
                width: 100%;
                padding: 15px;
                font-size: 16px;
                font-weight: 500;
                border-radius: 5px;
                color: $gray-light-color;
                background: $dark-secondary-color;
                position: relative;
                
                .copy-icon {
                  background: $primary-color;
                  position: absolute;
                  top: 7px;
                  right: 7px;
                  bottom: 7px;
                  display: flex;
                  flex-direction: row;
                  align-items: center;
                  justify-content: center;
                  border-radius: 5px;
                  height: 36px;
                  width: 36px;
                  
                  svg {
                    width: 20px;
                    height: 20px;
                    fill: $light-color;
                  }
                }
              }
            }
            
            .session-cards {
              display: flex;
              flex-direction: row;
              flex-wrap: nowrap;
              overflow-x: auto;
              margin: 30px 0;
              
              .session-card {
                width: 100%;
                min-width: 260px;
                background: $secondary-color;
                padding: 20px;
                border-radius: 5px;
                position: relative;
                
                &:not(:last-child) {
                  width: calc(100% - 30px);
                  margin-right: 10px;
                }
                
                .browser-icon {
                  position: absolute;
                  top: 20px;
                  right: 20px;
                  width: 32px;
                  height: 32px;
                  z-index: 999;
                  
                  svg, img {
                    width: 32px;
                    height: 32px;
                  }
                }
                
                .browser-name {
                  font-size: 18px;
                  font-weight: 600;
                  color: $gray-light-color;
                  margin-bottom: 10px;
                }
                
                .os-name {
                  font-size: 16px;
                  font-weight: 500;
                  color: $gray-light-color;
                  margin-bottom: 10px;
                }
                
                .activity {
                  display: flex;
                  flex-direction: row;
                  align-items: center;
                  font-size: 16px;
                  font-weight: 500;
                  color: $gray-light-color;
                  margin-bottom: 10px;
                  
                  span {
                    margin-left: 5px;
                    color: $light-color;
                  }
                }
                
                .ip-location {
                  font-size: 16px;
                  font-weight: 700;
                  color: $gray-light-color;
                  margin-bottom: 15px;
                }
                
                .close-session {
                  font-size: 18px;
                  font-weight: 700;
                  color: $danger-color;
                }
              }
            }
            
            .session-close {
              width: 100%;
              margin-bottom: 30px;
            }
            
            .subscription-cards {
              display: flex;
              flex-direction: column;
              
              .subscription-card {
                display: flex;
                flex-direction: column;
                background: $secondary-color;
                border-radius: 5px;
                padding: 20px;
                
                &:not(:last-child) {
                  margin-bottom: 15px;
                }
                
                .subscription-top {
                  display: flex;
                  flex-direction: row;
                  
                  .subscription-info {
                    margin-bottom: 15px;
                    
                    .subscription-name {
                      font-size: 18px;
                      font-weight: 700;
                      color: $light-color;
                      margin-bottom: 5px;
                    }
                    
                    .subscription-description {
                      font-size: 16px;
                      font-weight: 500;
                      color: $gray-light-color;
                    }
                  }
                  
                  .subscription-icon {
                    margin-left: auto;
                    display: flex;
                    flex-direction: row;
                    align-items: center;
                    justify-content: center;
                    width: 45px;
                    height: 45px;
                    background: $gray-light-color;
                    border-radius: 15px;
                    
                    &.discount {
                      background: linear-gradient(45deg, #3C855A, #7BBA73);
                    }
                    
                    svg {
                      width: 24px;
                      height: 24px;
                      fill: $light-color;
                    }
                  }
                }
                
                .subscription-bottom {
                  display: flex;
                  flex-direction: row;
                  align-items: center;
                  margin-bottom: 30px;
                  
                  .subscription-price {
                    font-size: 18px;
                    font-weight: 700;
                    color: $primary-color;
                  }
                  
                  .subscription-time {
                    font-size: 16px;
                    font-weight: 600;
                    margin-left: auto;
                    color: $gray-light-color;
                  }
                }
                
                .subscription-status {
                  display: flex;
                  flex-direction: row;
                  align-items: center;
                  font-size: 16px;
                  font-weight: 600;
                  color: $danger-color;
                  
                  &.active {
                    color: $primary-color;
                    
                    svg {
                      fill: $primary-color;
                    }
                  }
                  
                  svg {
                    width: 16px;
                    height: 16px;
                    margin-right: 10px;
                    fill: $danger-color;
                  }
                }
              }
            }
          }
        }
      }
    }
    
    .profile-container {
      border-radius: 5px;
      background: $dark-color;
      padding: 30px;
      width: 100%;
      min-height: 1px;
      
      &:not(:last-child) {
        margin-bottom: 10px;
      }
    }
    
    .user-block {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      
      .user-image {
        width: 120px;
        height: 120px;
        margin-bottom: 15px;
        
        img {
          width: auto;
          max-width: 120px;
          height: auto;
          border-radius: 50%;
        }
      }
      
      .user-name {
        display: flex;
        flex-direction: row;
        align-items: center;
        font-size: 24px;
        font-weight: 600;
        color: $light-color;
        margin-bottom: 15px;
        
        .verification {
          margin-left: 10px;
          width: 20px;
          height: 20px;
          background: $primary-color;
          border-radius: 50%;
        }
      }
      
      .user-steam-id {
        display: flex;
        flex-direction: row;
        align-items: center;
        font-size: 16px;
        font-weight: 500;
        color: $gray-light-color;
        
        .steam-icon {
          width: 16px;
          height: 16px;
          background: $gray-light-color;
          border-radius: 50%;
          margin-right: 5px;
          
          svg {
            width: 16px;
            height: 16px;
            
            .st0 {
              border-radius: 50%;
              fill: transparent;
            }
          }
        }
      }
    }
    
    .profile-navigation {
      .navigation-link {
        display: flex;
        flex-direction: row;
        align-items: center;
        font-size: 18px;
        font-weight: 500;
        color: $gray-light-color;
        padding-top: 20px;
        padding-bottom: 20px;
        
        &:not(:last-child) {
          //padding-bottom: 0;
          //margin-bottom: 30px;
        }
        
        svg {
          width: 18px;
          height: 18px;
          margin-right: 15px;
          fill: $gray-light-color;
        }
      }
    }
    
    .profile-logout {
      display: flex;
      flex-direction: row;
      align-items: center;
      font-size: 18px;
      font-weight: 500;
      color: $danger-color;
      
      svg {
        width: 18px;
        height: 18px;
        margin-right: 15px;
        fill: $danger-color;
      }
    }
    
    .table-transactions {
      width: 100%;
      overflow-x: auto;
      
      table {
        width: 100%;
        border-collapse: collapse;
        white-space: nowrap;
        
        th, td {
          padding: 15px 30px;
          text-align: left;
          
          &:first-child {
            padding-left: 15px;
          }
          
          &:last-child {
            padding-right: 15px;
          }
          
          &:last-child {
            text-align: right;
          }
        }
        
        thead {
          th {
            color: $gray-light-color;
            font-size: 16px;
            font-weight: 600;
          }
        }
        
        tbody {
          tr {
            background: #171C25;
          }
          
          tr:nth-child(even) {
            background-color: #151922;
          }
        }
      }
    }
    
    .table {
      display: flex;
      flex-direction: column;
      overflow: auto;
      
      .table-head {
        display: flex;
        flex-direction: column;
      }
      
      .table-body {
        display: flex;
        flex-direction: column;
      }
      
      .table-row {
        display: flex;
        flex-direction: row;
        align-items: center;
        
        .table-cell {
          display: flex;
          flex-direction: row;
          align-items: center;
          white-space: nowrap;
        }
      }
    }
  }
  
  &.tablet {
    width: 100%;
    background: $dark-color;
    padding: 30px;
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    position: relative;
    
    .profile-sidebar {
      width: 74px;
      min-width: 74px;
      margin-right: 30px;
      position: absolute;
      
      &.show {
        width: 350px;
        height: 100%;
        margin: -30px;
        position: absolute !important;
        top: 30px !important;
        right: 30px !important;
        bottom: 30px !important;
        left: 30px !important;
        z-index: 9998;
        padding: 30px;
        background: #191E28;
        border-radius: 0 5px 5px 0;
        box-shadow: 4px 4px 5px 0px rgba(21, 25, 34, 0.25);
        
        .user-block {
          width: 290px;
          
          .user-info {
            display: flex;
            
            .user-name {
            }
            
            .user-steam-id {
            }
          }
        }
        
        .navigation-block {
          .navigation-link {
            padding: 25px;
            width: 100%;
            justify-content: flex-start;
            
            span {
              display: block;
            }
            
            &.active {
              span {
                color: $light-color;
              }
            }
          }
        }
      }
      
      .user-block {
        width: 74px;
        height: 74px;
        border-radius: 20px;
        background: $secondary-color;
        margin-bottom: 50px;
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: center;
        padding: 25px;
        
        .user-image {
          display: flex;
          flex-direction: row;
          align-items: center;
          justify-content: center;
          height: 48px;
          width: 48px;
          position: relative;
          
          img {
            width: auto;
            max-width: 48px;
            height: auto;
            border-radius: 50%;
          }
          
          .verification {
            position: absolute;
            bottom: -5px;
            right: -5px;
            height: 24px;
            width: 24px;
            background: $primary-color;
            border-radius: 50%;
            border: 5px solid $secondary-color;
          }
        }
        
        .user-info {
          display: none;
          flex-direction: column;
          justify-content: space-between;
          margin-left: 20px;
          
          .user-name {
            font-size: 18px;
            font-weight: 700;
            color: $light-color;
            margin-bottom: 10px;
          }
          
          .user-steam-id {
            font-size: 14px;
            font-weight: 500;
            color: $gray-light-color;
            white-space: nowrap;
          }
        }
      }
      
      .navigation-block {
        .navigation-link {
          width: 74px;
          height: 74px;
          border-radius: 5px;
          display: flex;
          flex-direction: row;
          align-items: center;
          justify-content: center;
          padding: 25px;
          
          &:not(:last-child) {
            margin-bottom: 15px;
          }
          
          &:hover {
            background: $secondary-color;
            
            svg {
              fill: $primary-color;
            }
          }
          
          &.active {
            background: $primary-color;
            
            svg {
              fill: $light-color;
            }
          }
          
          &.navigation-sidebar-toggle {
            background: $secondary-color;
            
            svg {
              fill: $light-color;
            }
            
            &:hover {
              background: $light-color;
              
              svg {
                fill: $primary-color;
              }
            }
          }
          
          svg {
            min-width: 24px;
            width: 24px;
            height: 24px;
            fill: $gray-light-color;
          }
          
          span {
            margin-left: 20px;
            white-space: nowrap;
            font-size: 16px;
            font-weight: 600;
            color: $gray-light-color;
            display: none;
          }
        }
      }
    }
    
    .profile-page-content {
      background: #151922;
      width: calc(100% - 74px - 30px);
      height: 100%;
      border-radius: 5px;
      
      &.sidebar-active {
        width: calc(100% - 30px);
        margin-left: 104px;
      }
      
      .content-container {
        padding: 30px;
      }
      
      .table-transactions {
        width: 100%;
        overflow-x: auto;
        
        table {
          width: 100%;
          border-collapse: collapse;
          white-space: nowrap;
          
          th, td {
            padding: 15px 30px;
            text-align: left;
            
            &:first-child {
              padding-left: 15px;
            }
            
            &:last-child {
              padding-right: 15px;
            }
            
            &:last-child {
              text-align: right;
            }
          }
          
          thead {
            th {
              color: $gray-light-color;
              font-size: 16px;
              font-weight: 600;
            }
          }
          
          tbody {
            tr {
              background: #171C25;
            }
            
            tr:nth-child(even) {
              background-color: #151922;
            }
          }
        }
      }
      
      .profile-page-content-input {
        
        .input-group-title {
          display: flex;
          flex-direction: row;
          align-items: center;
          font-size: 20px;
          color: $light-color;
          font-weight: 700;
          margin-bottom: 15px;
          
          a, span {
            margin-left: auto;
            display: flex;
            flex-direction: row;
            align-items: center;
            font-size: 16px;
            font-weight: 500;
            color: $gray-light-color;
          }
          
          .switch {
            height: 24px;
            
            input {
              margin-right: 0;
            }
          }
        }
        
        .input {
          position: relative;
          padding: 15px;
          border-radius: 5px;
          background: #0F141B;
          height: auto;
          
          .icons {
            position: absolute;
            display: flex;
            flex-direction: row;
            align-items: center;
            top: 10px;
            right: 10px;
            bottom: 10px;
          }
          
          .input-icon {
            display: flex;
            flex-direction: row;
            align-items: center;
            justify-content: center;
            border-radius: 5px;
            height: 40px;
            width: 40px;
            
            &:not(:last-child) {
              margin-right: 10px;
            }
            
            svg {
              width: 24px;
              height: 24px;
              fill: $light-color;
            }
            
            span {
              display: none;
              margin-left: 10px;
              font-size: 14px;
              font-weight: 500;
              color: $light-color;
            }
          }
          
          .copy-icon {
            position: absolute;
            display: flex;
            flex-direction: row;
            align-items: center;
            justify-content: center;
            top: 10px;
            right: 10px;
            bottom: 10px;
            background: $primary-color;
            border-radius: 5px;
            height: 40px;
            width: 40px;
            
            svg {
              width: 24px;
              height: 24px;
              fill: $light-color;
            }
          }
        }
      }
      
      .question-icon {
        margin-left: 15px !important;
        font-weight: 700;
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: center;
        width: 24px;
        min-width: 24px;
        height: 24px;
        border-radius: 50%;
        background: $secondary-color;
        color: $gray-light-color;
      }
      
      .content-profile {
        &.hide {
          display: none;
        }
        
        .verification-block {
          position: relative;
          border: 1.5px solid $secondary-color;
          border-radius: 5px;
          padding: 30px;
          margin-bottom: 50px;
          
          .verification-block-close {
            position: absolute;
            top: 20px;
            right: 20px;
            
            &:hover {
