            padding: 12px;
            
            &:not(:last-child) {
              margin-right: 10px;
            }
            
            svg {
              width: 24px;
              height: 24px;
              fill: $light-color;
            }
            
            span {
              display: none;
              margin-left: 10px;
              font-size: 14px;
              font-weight: 500;
              color: $light-color;
            }
          }
          
          .copy-icon {
            position: absolute;
            display: flex;
            flex-direction: row;
            align-items: center;
            justify-content: center;
            top: 10px;
            right: 10px;
            bottom: 10px;
            background: $primary-color;
            border-radius: 5px;
            height: 40px;
            width: 60px;
            
            svg {
              width: 24px;
              height: 24px;
              fill: $light-color;
            }
          }
        }
