                width: 22px;
                height: 22px;
              }
              
              &.primary {
                background: transparentize($primary-color, 0.95);
                
                svg {
                  fill: $primary-color;
                }
              }
              
              &.success {
                background: transparentize($success-color, 0.95);
                
                svg {
                  fill: $success-color;
                }
              }
              
              &.violet {
                background: transparentize($violet-color, 0.95);
                
                svg {
                  fill: $violet-color;
                }
              }
            }
            
            .referral-data {
              display: flex;
              flex-direction: column;
              
              .referral-card-title {
                font-size: 16px;
                font-weight: 500;
                color: $gray-light-color;
                margin-bottom: 5px;
                white-space: nowrap;
              }
              
              .referral-card-value {
                font-size: 22px;
                font-weight: 700;
                color: $light-color;
                white-space: nowrap;
              }
            }
          }
        }
        
      }
      
