                  flex-direction: row;
                  align-items: center;
                  justify-content: center;
                  width: 40px;
                  height: 40px;
                  border-radius: 10px;
                  background: linear-gradient(45deg, #3C855A, #7BBA73);
                  
                  svg {
                    width: 22px;
                    height: 22px;
                    fill: $light-color;
                  }
                }
                
                .subscription-price-info {
                  display: flex;
                  flex-direction: column;
                  
                  .subscription-price-value {
                    color: $dark-color;
                    font-size: 22px;
                    font-weight: 700;
                    margin-bottom: 5px;
