              align-items: center;
              width: 100%;
              
              .switch {
                display: flex;
                flex-direction: row;
                align-items: center;
                white-space: nowrap;
                
                input {
                  width: 52px;
                  height: 24px;
                  
                  &:checked {
                    &:before {
                      transform: translateX(26px);
                    }
                    
                    ~ label {
                      color: $light-color;
                    }
                  }
                  
                  &:before {
