  &.desktop {
    margin-top: 170px;
    padding: 50px;
    width: 100%;
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    position: relative;
    
    .profile-page-inner {
      padding: 50px;
      background: $dark-color;
      border-radius: 5px;
      display: flex;
      flex-direction: row;
      align-items: flex-start;
      width: 100%;
    }
    
    .profile-sidebar {
      margin-right: 30px;
      width: 360px;
      height: 643px;
      
      .user-block {
        width: 290px;
      }
      
      .user-block {
        width: 100%;
        height: 134px;
        border-radius: 20px;
        background: $secondary-color;
        margin-bottom: 50px;
        display: flex;
        flex-direction: row;
        align-items: center;
        padding: 30px;
        
        .user-image {
          display: flex;
          flex-direction: row;
          align-items: center;
          justify-content: center;
          height: 74px;
          width: 74px;
          position: relative;
          
          img {
            width: auto;
            max-width: 74px;
            height: auto;
            border-radius: 50%;
          }
          
          .verification {
            position: absolute;
            bottom: -5px;
            right: -5px;
            height: 28px;
            width: 28px;
            background: $primary-color;
            border-radius: 50%;
            border: 5px solid $secondary-color;
          }
        }
        
        .user-info {
          display: flex;
          flex-direction: column;
          justify-content: center;
          height: 100%;
          margin-left: 25px;
          
          .user-name {
            font-size: 20px;
            font-weight: 700;
            color: $light-color;
            margin-bottom: 10px;
          }
          
          .user-steam-id {
            font-size: 16px;
            font-weight: 500;
            color: $gray-light-color;
            white-space: nowrap;
          }
        }
      }
      
      .navigation-block {
        .navigation-link {
          width: 100%;
          border-radius: 5px;
          display: flex;
          flex-direction: row;
          align-items: center;
          padding: 25px;
          cursor: pointer;
          
          &:not(:last-child) {
            margin-bottom: 15px;
          }
          
          &:hover {
            background: $secondary-color;
            
            span {
              color: $primary-color;
            }
            
            svg {
              fill: $primary-color;
            }
          }
          
          &.active {
            background: $primary-color;
            
            span {
              color: $light-color;
            }
            
            svg {
              fill: $light-color;
            }
          }
          
          svg {
            width: 24px;
            height: 24px;
            fill: $gray-light-color;
          }
          
          span {
            margin-left: 20px;
            white-space: nowrap;
            font-size: 16px;
            font-weight: 600;
            color: $gray-light-color;
          }
        }
      }
    }
    
    .profile-page-content {
      background: #151922;
      width: calc(100% - 358px - 30px);
      height: 100%;
      border-radius: 5px;
      
      .content-container {
        padding: 50px;
      }
      
      .table-transactions {
        width: 100%;
        overflow-x: auto;
        padding-bottom: 25px;
        
        table {
          width: 100%;
          border-collapse: collapse;
          white-space: nowrap;
          
          th, td {
            padding: 15px 30px;
            text-align: left;
            
            &:first-child {
              padding-left: 15px;
            }
            
            &:last-child {
              padding-right: 15px;
            }
            
            &:last-child {
              text-align: right;
            }
          }
          
          thead {
            th {
              color: $gray-light-color;
              font-size: 16px;
              font-weight: 600;
            }
          }
          
          tbody {
            tr {
              background: #171C25;
            }
            
            tr:nth-child(even) {
              background-color: #151922;
            }
          }
        }
      }
      
      .pagination {
        width: 100%;
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: center;
        margin-top: 15px;
        
        .pagination-item {
          width: 40px;
          height: 40px;
          border-radius: 5px;
          background: #1A1E29;
          display: flex;
          flex-direction: row;
          align-items: center;
          justify-content: center;
          font-size: 16px;
          font-weight: 700;
          color: $light-color;
          
          svg {
            fill: $light-color;
            width: 16px;
            height: 16px;
          }
          
          &:not(:last-child) {
            margin-right: 15px;
          }
          
          &.active {
            background: $primary-color;
          }
          
