            flex-direction: row;
            align-items: center;
            font-size: 16px;
            font-weight: 500;
            color: $gray-light-color;
            
            &:hover {
              color: $primary-color;
              
              span {
                color: $gray-light-color;
              }
            }
          }
          
          .switch {
            height: 24px;
            
            input {
              margin-right: 0;
            }
          }
        }
        
        .input {
          position: relative;
          padding: 15px;
          border-radius: 5px;
          background: #0F141B;
          height: 60px;
          
