          }
          
          &.active {
            background: $primary-color;
            
            span {
              color: $light-color;
            }
            
            svg {
              fill: $light-color;
            }
          }
          
          svg {
            width: 28px;
            height: 28px;
            fill: $gray-light-color;
          }
          
          span {
            margin-left: 20px;
            white-space: nowrap;
            font-size: 18px;
            font-weight: 600;
            color: $gray-light-color;
          }
        }
      }
    }
    
    .profile-page-content {
      background: #151922;
      width: calc(100% - 358px - 30px);
      height: 100%;
      border-radius: 5px;
      
      .content-container {
        padding: 50px;
      }
      
      .table-transactions {
        width: 100%;
        overflow-x: auto;
        padding-bottom: 25px;
        
        table {
          width: 100%;
          border-collapse: collapse;
          white-space: nowrap;
          
          th, td {
            padding: 15px 30px;
            text-align: left;
            
            &:first-child {
              padding-left: 15px;
            }
            
            &:last-child {
              padding-right: 15px;
            }
            
            &:last-child {
              text-align: right;
            }
          }
          
          thead {
            th {
              color: $gray-color;
              font-size: 16px;
              font-weight: 600;
            }
          }
          
          tbody {
            td {
              font-size: 16px;
              font-weight: 500;
            }
            
            tr {
              background: #171C25;
            }
            
            tr:nth-child(even) {
              background-color: #151922;
            }
          }
        }
      }
      
      .pagination {
        width: 100%;
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: center;
        margin-top: 15px;
        
        .pagination-item {
          width: 48px;
          height: 48px;
          border-radius: 5px;
          background: #1A1E29;
          display: flex;
          flex-direction: row;
          align-items: center;
          justify-content: center;
          font-size: 16px;
          font-weight: 700;
          color: $light-color;
          
          svg {
            fill: $light-color;
            width: 18px;
            height: 18px;
          }
          
          &:not(:last-child) {
            margin-right: 15px;
          }
          
          &.active {
            background: $primary-color;
          }
          
          &:first-child {
            margin-right: 30px;
            
            svg {
              transform: rotate(180deg);
            }
          }
          
          &:last-child {
            margin-left: 15px;
          }
        }
      }
      
      .profile-page-content-input {
        .input-group-title {
          display: flex;
          flex-direction: row;
          align-items: center;
          font-size: 20px;
          color: $light-color;
          font-weight: 700;
          margin-bottom: 15px;
          
          a, span {
            margin-left: auto;
            display: flex;
            flex-direction: row;
            align-items: center;
            font-size: 16px;
            font-weight: 500;
            color: $gray-light-color;
            
            &:hover {
              color: $primary-color;
              
              span {
                color: $gray-light-color;
              }
            }
          }
          
          .switch {
            height: 24px;
            
            input {
              margin-right: 0;
            }
          }
        }
        
        .input {
          position: relative;
          padding: 15px;
          border-radius: 5px;
          background: #0F141B;
          height: 60px;
          
          .icons {
            position: absolute;
            display: flex;
            flex-direction: row;
            align-items: center;
            top: 10px;
            right: 10px;
            bottom: 10px;
          }
          
          .input-icon {
            display: flex;
            flex-direction: row;
            align-items: center;
            justify-content: center;
            border-radius: 5px;
            height: 40px;
            padding: 12px;
            
            &:not(:last-child) {
              margin-right: 10px;
            }
            
            svg {
              width: 24px;
              height: 24px;
              fill: $light-color;
            }
            
            span {
              display: none;
              margin-left: 10px;
              font-size: 14px;
              font-weight: 500;
              color: $light-color;
            }
          }
          
          .copy-icon {
            position: absolute;
            display: flex;
            flex-direction: row;
            align-items: center;
            justify-content: center;
            top: 10px;
            right: 10px;
            bottom: 10px;
            background: $primary-color;
            border-radius: 5px;
            height: 40px;
            width: 60px;
            
            svg {
              width: 24px;
              height: 24px;
              fill: $light-color;
            }
          }
        }
      }
      
      .question-icon {
        margin-left: 15px !important;
        font-weight: 700;
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: center;
        width: 24px;
        min-width: 24px;
        height: 24px;
        border-radius: 50%;
        background: $secondary-color;
        color: $gray-light-color;
      }
      
      .content-profile {
        &.hide {
          display: none;
        }
        
