                font-size: 24px;
                font-weight: 700;
                color: $light-color;
                margin-bottom: 15px;
              }
              
              .verification-description {
                font-size: 18px;
                font-weight: 600;
                color: $gray-light-color;
                line-height: 130%;
              }
            }
            
