                  flex-direction: row;
                  align-items: center;
                  justify-content: center;
                  width: 40px;
                  height: 40px;
                  border-radius: 5px;
                  
                  &.primary {
                    background: transparentize($primary-color, 0.9);
                  }
                  
                  &.success {
                    background: transparentize($success-color, 0.9);
                  }
                  
                  svg {
                    width: 22px;
                    height: 22px;
                    fill: $light-color;
                  }
                }
                
                span {
                  font-size: 18px;
                  font-weight: 600;
                  color: $gray-light-color;
                  margin-left: 15px;
                }
              }
              
              .profile-chart-value {
                margin-bottom: 10px;
                font-size: 36px;
                font-weight: 700;
                color: $light-color;
              }
              
