            display: flex;
            flex-direction: row;
            align-items: center;
            padding: 30px;
            background: $dark-color;
            border-radius: 5px;
            
            .icon {
              display: flex;
              flex-direction: row;
              align-items: center;
              justify-content: center;
              width: 24px;
              height: 24px;
              border-radius: 50%;
              margin-right: 20px;
              
              svg {
                width: 24px;
                height: 24px;
                fill: $primary-color;
              }
            }
            
            .title {
              font-size: 16px;
              font-weight: 500;
              color: $light-color;
              
              span {
                margin-left: 5px;
                font-weight: 700;
              }
            }
            
            .question-icon {
              margin-left: auto !important;
            }
          }
        }
      }
      
      .content-transactions {
