  &.full {
    margin-top: 170px;
    padding: 50px;
    width: 100%;
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    position: relative;
    
    .profile-page-inner {
      padding: 50px;
      background: $dark-color;
      border-radius: 5px;
      display: flex;
      flex-direction: row;
      align-items: flex-start;
      width: 100%;
    }
    
    .profile-sidebar {
      margin-right: 30px;
      width: 410px;
      min-width: 410px;
      
      .user-block {
        width: 410px;
      }
      
      .user-block {
        width: 100%;
        height: 134px;
        border-radius: 20px;
        background: $secondary-color;
        margin-bottom: 50px;
        display: flex;
        flex-direction: row;
        align-items: center;
        padding: 30px;
        
        .user-image {
          display: flex;
          flex-direction: row;
          align-items: center;
          justify-content: center;
          height: 74px;
          width: 74px;
          position: relative;
          
          img {
            width: auto;
            max-width: 74px;
            height: auto;
            border-radius: 50%;
          }
          
          .verification {
            position: absolute;
            bottom: -5px;
            right: -5px;
            height: 28px;
            width: 28px;
            border-radius: 50%;
            background: $light-color;
            border: 5px solid $secondary-color;
            display: flex;
            flex-direction: row;
            align-items: center;
            justify-content: center;
            
            svg {
              position: absolute;
              border: 5px solid $secondary-color;
              border-radius: 50%;
              width: 32px;
              height: 32px;
              fill: $primary-color;
            }
          }
        }
        
        .user-info {
          display: flex;
          flex-direction: column;
          justify-content: center;
          height: 100%;
          margin-left: 25px;
          
          .user-name {
            font-size: 24px;
            font-weight: 700;
            color: $light-color;
            margin-bottom: 10px;
          }
          
          .user-steam-id {
            font-size: 16px;
            font-weight: 500;
            color: $gray-light-color;
            white-space: nowrap;
          }
        }
      }
      
      .navigation-block {
        .navigation-link {
          width: 100%;
          border-radius: 5px;
          display: flex;
          flex-direction: row;
          align-items: center;
          padding: 30px;
          cursor: pointer;
          
          &:not(:last-child) {
            margin-bottom: 15px;
          }
          
          &:hover {
            background: $secondary-color;
            
            span {
              color: $primary-color;
            }
            
            svg {
              fill: $primary-color;
            }
          }
          
          &.active {
            background: $primary-color;
            
            span {
              color: $light-color;
            }
            
            svg {
              fill: $light-color;
            }
          }
          
          svg {
            width: 28px;
            height: 28px;
            fill: $gray-light-color;
          }
          
          span {
            margin-left: 20px;
            white-space: nowrap;
            font-size: 18px;
            font-weight: 600;
            color: $gray-light-color;
          }
        }
      }
    }
    
    .profile-page-content {
      background: #151922;
      width: calc(100% - 358px - 80px);
      height: 100%;
      border-radius: 5px;
      
      .content-container {
        padding: 50px;
      }
      
      .table-transactions {
        width: 100%;
        overflow-x: auto;
        padding-bottom: 25px;
        
        table {
          width: 100%;
          border-collapse: collapse;
          white-space: nowrap;
          
          th, td {
            padding: 15px 30px;
            text-align: left;
            
            &:first-child {
              padding-left: 15px;
            }
            
            &:last-child {
              padding-right: 15px;
            }
            
            &:last-child {
              text-align: right;
            }
          }
          
          thead {
            th {
              color: $gray-color;
              font-size: 16px;
              font-weight: 600;
            }
          }
          
          tbody {
            td {
              font-size: 16px;
              font-weight: 500;
              
              &.transaction-id {
              }
              
              &.transaction-type {
              }
              
              &.transaction-amount {
                span {
                  color: $primary-color;
                }
              }
              
              &.transaction-method {
              }
              
              &.transaction-card {
              }
              
              &.transaction-date {
                color: $gray-light-color;
              }
              
              &.transaction-message {
                position: relative;
                
                .transaction-message-block {
                  display: flex;
                  flex-direction: row;
                  align-items: center;
                  justify-content: center;
                  cursor: pointer;
                  
                  span {
                    display: flex;
                    flex-direction: row;
                    align-items: center;
                    justify-content: center;
                    width: 38px;
                    height: 38px;
                    
                    &.message-active {
                      width: 38px;
                      height: 38px;
                      border-radius: 5px;
                      background: $secondary-color;
                      
                      svg {
                        fill: $primary-color;
                      }
                    }
                    
                    svg {
                      width: 20px;
                      height: 20px;
                      fill: $gray-light-color;
                    }
                  }
                }
                
                .transaction-message-popover {
                  display: flex;
                  flex-direction: row;
                  align-items: center;
                  justify-content: center;
                  
                  .transaction-message-icon {
                    display: flex;
                    flex-direction: row;
                    align-items: center;
                    justify-content: center;
                    width: 38px;
                    height: 38px;
                    border-radius: 5px;
                    cursor: pointer;
                    
                    &.has-message {
                      background: $secondary-color;
                      
                      svg {
                        fill: $primary-color;
                      }
                    }
                    
                    svg {
                      width: 20px;
                      height: 20px;
                      fill: $gray-light-color
                    }
                  }
  
                  .__react_component_tooltip {
                    display: flex;
                    flex-direction: row;
                    justify-content: center;
                    align-items: center;
                    max-width: 280px;
                    min-width: 150px;
                    padding: 15px;
                    border-radius: 5px;
                    background: $secondary-color;
                    color: $gray-light-color;
                    font-size: 14px;
                    font-weight: 600;
                    white-space: normal;
                    
                    &:after {
                      border-left-color: $secondary-color;
                    }
                  }
                  
                  .transaction-message-text {
                    display: flex;
                    flex-direction: row;
                    justify-content: center;
                    align-items: center;
                    max-width: 280px;
                    min-width: 150px;
                    padding: 15px;
                    border-radius: 5px;
                    background: $secondary-color;
                    color: $gray-light-color;
                    font-size: 14px;
                    font-weight: 600;
                    position: absolute;
                    bottom: -75px;
                    white-space: normal;
                  }
                }
              }
              
              &.transaction-status {
                .transaction-status-block {
                  display: flex;
                  flex-direction: row;
                  align-items: center;
                  
                  span.success {
                    display: block;
                    width: 14px;
                    height: 14px;
                    border-radius: 50%;
                    background: $success-color;
                    margin-right: 10px;
                  }
                  
                  span.failed {
                    display: block;
                    width: 14px;
                    height: 14px;
                    border-radius: 50%;
                    background: $danger-color;
                    margin-right: 10px;
                  }
                  
                  span.in-progress {
                    display: block;
                    width: 14px;
                    height: 14px;
                    border-radius: 50%;
                    background: $warning-color;
                    margin-right: 10px;
                  }
                  
                  span:last-child {
                    font-size: 16px;
                    font-weight: 500;
                    color: $light-color;
                  }
                }
              }
            }
            
            tr {
              background: #171C25;
            }
            
            tr:nth-child(even) {
              background-color: #151922;
            }
          }
        }
      }
      
      .pagination {
        width: 100%;
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: center;
        margin-top: 15px !important;
        user-select: none;
        
        .pagination-item {
          width: 48px;
          height: 48px;
          border-radius: 5px;
          background: #1A1E29;
          display: flex;
          flex-direction: row;
          align-items: center;
          justify-content: center;
          font-size: 16px;
          font-weight: 700;
          color: $light-color;
          cursor: pointer;
          transition: 0.25s;
          
          &:hover {
            background: lighten(#1A1E29, 5%);
          }
          
          &:active {
            background: darken(#1A1E29, 5%);
          }
          
          &.pagination-item-arrow {
          
          }
          
          &.active {
            background: $primary-color;
            
            &:hover {
              background: darken($primary-color, 10%);
            }
            
            &:active {
              background: darken($primary-color, 10%);
            }
          }
          
          &.pagination-item-arrow-active {
            background: $primary-color;
            
            &:hover {
              background: darken($primary-color, 10%);
            }
            
            &:active {
              background: darken($primary-color, 5%);
            }
          }
          
          svg {
            fill: $light-color;
            width: 18px;
            height: 18px;
          }
          
          &:not(:last-child) {
            margin-right: 15px;
          }
          
          &:first-child {
            margin-right: 30px;
            
            svg {
              transform: rotate(180deg);
            }
          }
          
          &:last-child {
            margin-left: 15px;
          }
        }
      }
      
      .profile-page-content-input {
        .input-group-title {
          display: flex;
          flex-direction: row;
          align-items: center;
          justify-content: space-between;
          font-size: 20px;
          color: $light-color;
          font-weight: 700;
          margin-bottom: 15px;
          
          a, span {
            margin-left: auto;
            display: flex;
            flex-direction: row;
            align-items: center;
            font-size: 16px;
            font-weight: 500;
            color: $gray-light-color;
            
            &:hover {
              color: $primary-color;
              
              span {
                color: $gray-light-color;
              }
            }
          }
          
          label {
            cursor: pointer;
            font-size: 14px;
            font-weight: 500;
            color: $gray-light-color;
            
            span {
              margin-right: 10px;
            }
          }
        }
        
        .input {
          display: flex;
          flex-direction: row;
          align-items: center;
        }
        
        .input, .text-block {
          position: relative;
          padding: 15px;
          border-radius: 5px;
          background: #0F141B;
          height: 74px;
          display: flex;
          
          .text {
            display: flex;
            flex-direction: row;
            align-items: center;
            width: 100%;
            font-size: 18px;
            font-weight: 500;
            color: $gray-light-color;
          }
          
          .icons {
            display: flex;
            flex-direction: row;
            align-items: center;
            padding-left: 15px;
          }
          
          .input-icon {
            position: absolute;
            display: flex;
            flex-direction: row;
            align-items: center;
            justify-content: center;
            top: 10px;
            right: 10px;
            bottom: 10px;
            background: $primary-color;
            border-radius: 5px;
            height: 48px;
            width: 60px;
            cursor: pointer;
            padding: 12px;
            user-select: none;
            
            &:hover {
              background: lighten($primary-color, 5%);
            }
            
            &:active {
              background: darken($primary-color, 5%);
            }
            
            &:not(:last-child) {
              margin-right: 10px;
            }
            
            svg {
              width: 24px;
              height: 24px;
              fill: $light-color;
            }
            
            span {
              display: none;
              margin-left: 10px;
              font-size: 14px;
              font-weight: 500;
              color: $light-color;
            }
          }
          
          .input-btn {
            display: flex;
            flex-direction: row;
            align-items: center;
            justify-content: center;
            background: $primary-color;
            border-radius: 5px;
            height: 48px;
            width: 60px;
            cursor: pointer;
            padding: 12px;
            user-select: none;
            
            &:hover {
              background: lighten($primary-color, 5%);
            }
            
            &:active {
              background: darken($primary-color, 5%);
            }
            
            &:not(:last-child) {
              margin-right: 10px;
            }
            
            svg {
              width: 24px;
              height: 24px;
              fill: $light-color;
            }
            
            span {
              display: none;
              margin-left: 10px;
              font-size: 14px;
              font-weight: 500;
              color: $light-color;
            }
          }
        }
      }
      
      .question-icon {
        margin-left: 15px !important;
        font-weight: 700;
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: center;
        width: 24px;
        min-width: 24px;
        height: 24px;
        border-radius: 50%;
        background: $secondary-color;
        color: $gray-light-color;
      }
      
      .content-profile {
        &.hide {
          display: none;
        }
        
        .verification-block {
          position: relative;
          border: 1.5px solid $secondary-color;
          border-radius: 5px;
          padding: 30px;
          margin-bottom: 50px;
          display: none;
          
          &.show {
            display: block;
          }
          
          .verification-block-close {
            position: absolute;
            top: 20px;
            right: 20px;
            cursor: pointer;
            
            &:hover {
              svg {
                fill: $danger-color;
              }
            }
            
            svg {
              width: 16px;
              height: 16px;
              fill: $gray-color;
            }
          }
          
          .verification-info {
            display: flex;
            flex-direction: row;
            margin-bottom: 30px;
            
            .verification-right {
              display: flex;
              flex-direction: column;
              margin-left: 30px;
              
              .verification-title {
                font-size: 24px;
                font-weight: 700;
                color: $light-color;
                margin-bottom: 15px;
              }
              
              .verification-description {
                font-size: 18px;
                font-weight: 600;
                color: $gray-light-color;
                line-height: 130%;
              }
            }
            
            .verification-badge {
              width: 48px;
              min-width: 48px;
              height: 48px;
              background: $light-color;
              border-radius: 50%;
              display: flex;
              flex-direction: row;
              align-items: center;
              justify-content: center;
              
              svg {
                position: absolute;
                width: 64px;
                height: 64px;
                fill: $primary-color;
              }
            }
          }
          
          .verification-bottom {
            color: $gray-color;
            font-size: 14px;
            font-weight: 600;
          }
          
        }
        
        .inputs-block {
          display: flex;
          flex-direction: row;
          flex-wrap: wrap;
          margin-bottom: 30px !important;
          margin: -25px;
          
          .profile-page-content-input {
            flex: 1 1 auto;
            margin: 25px;
          }
        }
        
        .charts-block {
          display: flex;
          flex-direction: row;
          flex-wrap: nowrap;
          margin-bottom: 30px !important;
          margin: -25px;
          
          .profile-page-content-chart {
            display: flex;
            flex: 1 1 auto;
            flex-direction: row;
            align-items: center;
            padding: 40px;
            background: $dark-color;
            border-radius: 5px;
            margin: 25px;
            white-space: nowrap;
            
            .chart-left {
              display: flex;
              flex-direction: column;
              
              .profile-chart-title {
                display: flex;
                flex-direction: row;
                align-items: center;
                margin-bottom: 25px;
                
                .icon {
                  display: flex;
                  flex-direction: row;
                  align-items: center;
                  justify-content: center;
                  width: 40px;
                  height: 40px;
                  border-radius: 5px;
                  
                  &.primary {
                    background: transparentize($primary-color, 0.9);
                  }
                  
                  &.success {
                    background: transparentize($success-color, 0.9);
                  }
                  
                  svg {
                    width: 22px;
                    height: 22px;
                    fill: $light-color;
                  }
                }
                
                span {
                  font-size: 18px;
                  font-weight: 600;
                  color: $gray-light-color;
                  margin-left: 15px;
                }
              }
              
              .profile-chart-value {
                margin-bottom: 10px;
                font-size: 36px;
                font-weight: 700;
                color: $light-color;
              }
              
              .profile-chart-secondary-value {
                font-size: 16px;
                font-weight: 600;
                color: $gray-light-color;
              }
            }
            
            .chart-container {
              margin-left: auto;
              width: 174px;
              height: 120px;
            }
          }
        }
        
        .fees-block {
          .fee-container {
            display: flex;
            flex-direction: row;
            align-items: center;
            padding: 30px;
            background: $dark-color;
            border-radius: 5px;
            
            .icon {
              display: flex;
              flex-direction: row;
              align-items: center;
              justify-content: center;
              width: 24px;
              height: 24px;
              border-radius: 50%;
              margin-right: 20px;
              
              svg {
                width: 24px;
                height: 24px;
                fill: $primary-color;
              }
            }
            
            .title {
              font-size: 16px;
              font-weight: 500;
              color: $light-color;
              
              span {
                margin-left: 5px;
                font-weight: 700;
              }
            }
            
            .question-icon {
              margin-left: auto !important;
            }
          }
        }
      }
      
      .content-transactions {
        min-height: 683px;
        display: flex;
        flex-direction: column;
        
        &.hide {
          display: none;
        }
        
        .pagination {
          margin-top: auto;
        }
      }
      
      .content-referrals {
        &.hide {
          display: none;
        }
        
        .referral-link {
          margin-bottom: 50px;
        }
        
        .referral-cards {
          display: flex;
          flex-direction: row;
          align-items: center;
          justify-content: space-between;
          margin-bottom: 50px;
          
          .referral-card {
            flex: 1 1 auto;
            display: flex;
            flex-direction: row;
            align-items: center;
            background: #1A1E29;
            padding: 25px 30px;
            border-radius: 5px;
            
            &:not(:last-child) {
              margin-right: 20px;
            }
            
            .referral-icon {
              width: 60px;
              min-width: 60px;
              height: 60px;
              border-radius: 20px;
              display: flex;
              flex-direction: row;
              align-items: center;
              justify-content: center;
              margin-right: 20px;
              
              svg {
                width: 30px;
                height: 30px;
              }
              
              &.primary {
                background: transparentize($primary-color, 0.95);
                
                svg {
                  fill: $primary-color;
                }
              }
              
              &.success {
                background: transparentize($success-color, 0.95);
                
                svg {
                  fill: $success-color;
                }
              }
              
              &.violet {
                background: transparentize($violet-color, 0.95);
                
                svg {
                  fill: $violet-color;
                }
              }
            }
            
            .referral-data {
              display: flex;
              flex-direction: column;
              
              .referral-card-title {
                font-size: 18px;
                font-weight: 500;
                color: $gray-light-color;
                margin-bottom: 5px;
                white-space: nowrap;
              }
              
              .referral-card-value {
                font-size: 30px;
                font-weight: 700;
                color: $light-color;
                white-space: nowrap;
              }
            }
          }
        }
        
      }
      
      .content-security {
        &.hide {
          display: none;
        }
        
        .security-inputs {
          margin: -10px;
          
          .profile-page-content-input {
            flex: 1 1 auto;
            
            margin: 10px;
            
            &.security {
              max-width: 350px;
              width: 350px;
            }
          }
        }
        
        @media (min-width: 1400px) {
          .profile-page-content-input {
            .input-icon {
              min-width: 120px !important;
              width: auto !important;
              
              span {
                display: block;
              }
            }
            
            &.password {
              max-width: 250px;
            }
            
            &.api-key {
              width: 100%;
            }
          }
        }
        
        .security-inputs {
          display: flex;
          flex-direction: row;
          flex-wrap: wrap;
          margin-bottom: 50px;
          
          .input-icon {
            position: relative;
            display: flex;
            flex-direction: row;
            align-items: center;
            justify-content: center;
            top: 0px;
            right: 0px;
            bottom: 0px;
            background: $primary-color;
            border-radius: 5px;
            height: 100%;
            width: 60px;
            cursor: pointer;
            padding: 12px 24px;
            
            &:hover {
              background: lighten($primary-color, 5%);
            }
            
            &:active {
              background: darken($primary-color, 5%);
            }
            
            &:not(:last-child) {
              margin-right: 10px;
            }
            
            svg {
              width: 24px;
              height: 24px;
              fill: $light-color;
            }
            
            span {
              margin-left: 10px;
              font-size: 14px;
              font-weight: 500;
              color: $light-color;
            }
          }
          
          .input, .btn {
            height: 74px;
            width: 100%;
          }
          
          .api-key {
            .text {
              width: 100%;
            }
            
            .icons {
              margin-left: auto;
              
              .remove-icon {
                background: $secondary-color;
                
                &:hover {
                  background: lighten($secondary-color, 5%);
                }
                
                &:active {
                  background: darken($secondary-color, 5%);
                }
                
                span {
                  font-size: 18px;
                  font-weight: 500;
                  color: $gray-light-color;
                }
              }
              
              .view-icon {
                background: $primary-color;
                
                &:hover {
                  background: lighten($primary-color, 5%);
                }
                
                &:active {
                  background: darken($primary-color, 5%);
                }
                
                span {
                  font-size: 18px;
                  font-weight: 600;
                  color: $light-color;
                }
              }
              
              .input-icon {
                display: flex;
                flex-direction: row;
                align-items: center;
                justify-content: center;
                width: 14px;
                
                &:not(:last-child) {
                  margin-right: 15px;
                }
                
                svg {
                  width: 14px;
                  height: 14px;
                }
              }
            }
          }
          
          .security {
            .security-container {
              border: 2px solid $secondary-color;
              border-radius: 5px;
              display: flex;
              flex-direction: row;
              align-items: center;
              padding: 15px;
              height: 74px;
              
              span.security-status {
                display: flex;
                flex-direction: row;
                align-items: center;
                justify-content: center;
                margin-right: 50px;
                color: $gray-light-color;
                font-size: 18px;
                white-space: nowrap;
                font-weight: 600;
                
                &.status-on {
                  color: $primary-color;
                }
                
                &.status-off {
                  color: $danger-color;
                }
              }
              
              span.security-status-active, span.security-status-non-active {
                position: relative;
                margin-right: 30px;
                
                svg {
                  width: 32px;
                  height: 32px;
                  fill: $secondary-color;
                }
                
                &.security-status-active {
                  .security-status-text {
                    color: $primary-color;
                  }
                  
                  svg {
                    fill: $primary-color;
                  }
                }
                
                &.security-status-non-active {
                  .security-status-text {
                    color: $danger-color;
                  }
                  
                  svg {
                    fill: $danger-color;
                  }
                }
              }
              
              .security-status-text {
                width: 32px;
                height: 32px;
                position: absolute;
                top: 0;
                right: 0;
                bottom: 0;
                left: 0;
                font-size: 10px;
                font-weight: 900;
                display: flex;
                flex-direction: row;
                align-items: center;
                justify-content: center;
              }
              
              .btn {
                margin-left: auto;
                width: 80px;
                height: 48px;
              }
            }
          }
        }
        
        .active-sessions {
          display: flex;
          flex-direction: column;
          margin-bottom: 50px;
          
          .container-title {
            display: flex;
            flex-direction: row;
            align-items: center;
            font-size: 22px;
            font-weight: 700;
            color: $light-color;
            margin-bottom: 20px;
            width: 100%;
            
            span {
              margin-left: auto;
              font-size: 16px;
              font-weight: 600;
              cursor: pointer;
            }
          }
          
          .session-cards {
            display: flex;
            flex-direction: row;
            align-items: center;
            flex-wrap: nowrap;
            overflow-x: auto;
            width: 100%;
            padding-bottom: 15px;
            
            .session-card {
              min-width: 430px;
              padding: 30px;
              background: $dark-color;
              display: flex;
              flex-direction: row;
              align-items: center;
              
              &:not(:last-child) {
                margin-right: 15px;
              }
              
              .session-info {
                .session-title {
                  margin-bottom: 15px;
                  display: flex;
                  flex-direction: row;
                  align-items: center;
                  
                  .session-browser-icon {
                    margin-right: 15px;
                    max-width: 28px;
                    height: 28px;
                    
                    svg, img {
                      width: 28px;
                      min-width: 28px;
                      height: 28px;
                    }
                    
                    svg {
                      fill: $light-color;
                    }
                  }
                  
                  .session-browser-name {
                    font-size: 18px;
                    font-weight: 500;
                    color: $light-color;
                  }
                }
                
                .session-activity {
                  margin-bottom: 10px;
                  font-size: 16px;
                  font-weight: 500;
                  color: $gray-light-color;
                  
                  span {
                    color: $light-color;
                  }
                }
                
                .session-location {
                  font-size: 16px;
                  font-weight: 500;
                  color: $gray-light-color;
                }
              }
              
              .close-icon {
                display: flex;
                flex-direction: row;
                align-items: center;
                justify-content: center;
                border-radius: 5px;
                margin-left: 40px;
                height: 85px;
                width: 42px;
                background: $secondary-color;
                padding: 12px;
                cursor: pointer;
                
                svg {
                  width: 18px;
                  height: 18px;
                  fill: $light-color;
                }
                
                &:hover {
                  background: $danger-color;
                }
              }
            }
          }
        }
      }
      
      .content-subscriptions {
        &.hide {
          display: none;
        }
        
        .content-container-title {
          font-size: 22px;
          font-weight: 700;
          color: $light-color;
          margin-bottom: 20px;
        }
        
        .subscription-cards {
          display: flex;
          flex-direction: row;
          align-items: center;
          width: 100%;
          overflow-x: auto;
          margin-bottom: 50px;
          padding-bottom: 25px;
          
          .subscription-card {
            width: 345px;
            border-radius: 25px;
            padding: 50px;
            display: flex;
            flex-direction: column;
            background: linear-gradient(45deg, #3C855B, #7ABA72);
            transition: 0.5s ease-in-out all;
            cursor: pointer;
            
            &:hover {
              background: linear-gradient(45deg, darken(#3C855B, 5), darken(#7ABA72, 5));
            }
            
            &:not(:last-child) {
              margin-right: 15px;
            }
            
            .best-offer {
              display: flex;
              flex-direction: row;
              align-items: center;
              margin-bottom: 30px;
              
              .icon {
                display: flex;
                flex-direction: row;
                align-items: center;
                justify-content: center;
                width: 14px;
                height: 14px;
                
                svg {
                  width: 26px;
                  height: 26px;
                  fill: #A7F49F;
                }
              }
              
              span {
                color: #A7F49F;
                margin-left: 10px;
                font-size: 18px;
                font-weight: 500;
              }
            }
            
            .subscription-title {
              font-size: 36px;
              font-weight: 700;
              color: $light-color;
              margin-bottom: 15px;
            }
            
            .subscription-description {
              font-size: 14px;
              font-weight: 500;
              color: #A7F49F;
              line-height: 120%;
              margin-bottom: 30px;
            }
            
            .subscription-price {
              margin-bottom: 15px;
              display: flex;
              flex-direction: row;
              align-items: center;
              cursor: pointer;
              
              &:hover {
                .subscription-price-block {
                  background: darken($light-color, 5%);
                }
                
                .subscription-price-arrow {
                  svg {
                    fill: darken($light-color, 5%);
                  }
                }
              }
              
              .subscription-price-block {
                padding: 15px;
                display: flex;
                flex-direction: row;
                align-items: center;
                background: $light-color;
                border-radius: 10px;
                width: 210px;
                
                .subscription-icon {
                  margin-right: 15px;
                  display: flex;
                  flex-direction: row;
                  align-items: center;
                  justify-content: center;
                  width: 40px;
                  height: 40px;
                  border-radius: 10px;
                  background: linear-gradient(45deg, #3C855A, #7BBA73);
                  
                  svg {
                    width: 22px;
                    height: 22px;
                    fill: $light-color;
                  }
                }
                
                .subscription-price-info {
                  display: flex;
                  flex-direction: column;
                  
                  .subscription-price-value {
                    color: $dark-color;
                    font-size: 22px;
                    font-weight: 700;
                    margin-bottom: 5px;
                  }
                  
                  .subscription-price-duration {
                    font-size: 14px;
                    font-weight: 500;
                    color: $gray-light-color;
                  }
                }
              }
              
              .subscription-price-arrow {
                margin-left: 30px;
                
                svg {
                  width: 24px;
                  height: 24px;
                  fill: $light-color;
                }
              }
            }
            
            .subscription-additional-info {
              font-size: 14px;
              font-weight: 500;
              color: #66C281;
            }
          }
        }
        
        .active-subscription-cards {
          display: flex;
          flex-direction: row;
          align-items: center;
          overflow-x: auto;
          width: 100%;
          padding-bottom: 25px;
          
          .active-subscription-card {
            width: 345px;
            border-radius: 5px;
            padding: 20px;
            background: $dark-color;
            
            &:not(:last-child) {
              margin-right: 10px;
            }
            
            .active-subscription-card-top {
              display: flex;
              flex-direction: row;
              align-items: center;
              margin-bottom: 30px;
              
              .active-subscription-icon {
                display: flex;
                flex-direction: row;
                align-items: center;
                justify-content: center;
                width: 44px;
                height: 44px;
                border-radius: 10px;
                margin-right: 20px;
                background: linear-gradient(45deg, #3C855B, #7ABA72);
                
                svg {
                  width: 24px;
                  height: 24px;
                  fill: $light-color;
                }
              }
              
              .active-subscription-info {
                display: flex;
                flex-direction: column;
                
                .active-subscription-title {
                  font-size: 22px;
                  font-weight: 700;
                  color: $light-color;
                  margin-bottom: 5px;
                }
                
                .active-subscription-duration {
                  font-size: 14px;
                  font-weight: 500;
                  color: $gray-light-color;
                }
              }
            }
            
            .active-subscription-card-switch {
              display: flex;
              flex-direction: row;
              align-items: center;
              width: 100%;
              
              .switch {
                display: flex;
                flex-direction: row;
                align-items: center;
                white-space: nowrap;
                
                input {
                  width: 52px;
                  height: 24px;
                  
                  &:checked {
                    &:before {
                      transform: translateX(26px);
                    }
                    
                    ~ label {
                      color: $light-color;
                    }
                  }
                  
                  &:before {
                    width: 15px;
                    height: 15px;
                  }
                }
                
                label {
                  font-size: 14px;
                  color: $gray-light-color;
                }
              }
            }
          }
        }
      }
    }
  }
}