header {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  z-index: 9999;
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 100%;
  max-width: 100% !important;
  height: auto !important;
  padding: 0 50px;
  color: #6D7681;
  background: #191E28;
  box-shadow: 0 0 5px 0 #151921;
}

header > div {
  display: flex;
  align-items: center;
  flex-direction: row;
  width: 100%;
  max-width: 1920px;
  height: 85px;
  margin: 0 auto;
}