@import "./src/assets/styles/colors";

.item-page {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
  background: $dark-secondary-color;
  border-radius: 5px;
  
  &.mobile {
    overflow: hidden;
    height: 100%;
    
    .return-link {
      display: flex;
      align-items: center;
      flex-direction: row;
      width: 100%;
      padding: 10px 30px 20px 30px;
      color: $gray-light-color;
      background: $dark-secondary-color;
      
      svg {
        width: 12px;
        height: 12px;
        margin-right: 10px;
        transform: rotate(180deg);
        fill: $gray-light-color;
      }
    }
    
    .item-container {
      display: flex;
      flex-direction: row;
      height: 100%;
      padding: 30px;
      background: $dark-color;
      border-radius: 5px;
      
      &:not(:last-child) {
        margin-bottom: 20px;
      }
      
      .item-container-title {
        display: flex;
        align-items: center;
        flex-direction: row;
        width: 100%;
        margin-bottom: 30px;
        font-size: 20px;
        font-weight: 700;
        color: $light-color;
      }
      
      &.item-data {
        position: relative;
        align-items: flex-start !important;
        margin-right: 0;
      }
      
      &.item-chart {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        width: 100%;
        padding: 0;
        
        .item-container-title {
          display: flex;
          flex-direction: column;
          align-items: center;
          padding: 30px 30px 0 30px;
          margin-bottom: 0;
        }
        
        .left {
          width: 100%;
          display: flex;
          flex-direction: column;
          margin-bottom: 30px;
          
          .title {
            margin-bottom: 10px;
          }
          
          .date-selector {
            display: flex;
            flex-direction: row;
            align-items: center;
            justify-content: flex-start;
            user-select: none;
            cursor: pointer;
            
            span {
              font-size: 18px;
              font-weight: 500;
              color: $gray-light-color;
              
              &.active {
                color: $primary-color;
              }
              
              &:not(:last-child) {
                margin-right: 20px;
              }
            }
          }
        }
        
        .right {
          width: 100%;
          display: flex;
          flex-direction: column;
          align-items: flex-start;
          font-size: 18px;
          font-weight: 500;
          color: $gray-light-color;
          
          .top {
            margin-bottom: 10px;
          }
          
          .bottom {
            display: flex;
            flex-direction: row;
            align-items: center;
          }
          
          input {
            width: 124px;
            height: 54px;
            background: $dark-secondary-color;
            color: $light-color;
            margin-right: 30px;
            margin-left: 20px;
          }
        }
        
        svg {
          user-select: none;
        }
      }
      
      &.item-images {
        display: flex;
        flex-direction: column;
        width: 100%;
        height: 275px;
        
        .item-images-slider {
          display: flex;
          align-items: center;
          flex-direction: row;
          flex-wrap: wrap;
          overflow-y: scroll;
          height: 100%;
          padding-right: 20px;
          
          .item-image-slider-image {
            padding: 10px;
            cursor: pointer;
            width: 350px;
            position: relative;
            user-select: none;
            
            &:hover {
              .cover {
                display: flex;
              }
            }
            
            .cover {
              width: calc(100% - 20px);
              height: calc(100% - 24px);
              position: absolute;
              border-radius: 5px;
              margin: 10px;
              top: 0;
              left: 0;
              bottom: 0;
              right: 0;
              display: none;
              flex-direction: row;
              align-items: center;
              justify-content: center;
              background: rgba($secondary-color, 0.7);
              
              svg {
                width: 50px;
                height: 50px;
                fill: $light-color;
              }
            }
            
            img {
              max-width: 100%;
              height: auto;
              border-radius: 5px;
            }
          }
        }
      }
      
      &.item-sales {
        flex-direction: column;
        overflow: hidden;
        width: 100%;
        height: 705px;
        padding: 0;
        
        .item-container-title {
          padding: 30px;
        }
        
        .item-sales-tabs {
          position: relative;
          display: flex;
          align-items: center;
          flex-direction: row;
          margin-left: auto;
          
          .content-tab {
            font-size: 20px;
            font-weight: 700;
            color: $gray-light-color;
            
            &:not(:last-child) {
              margin-right: 20px;
            }
            
            &.active {
              color: $light-color;
              position: relative;
              
              &:before {
                position: absolute;
                top: -30px;
                left: -5px;
                content: "";
                width: 30px;
                height: 4px;
                background: $primary-color;
                border-radius: 5px;
              }
            }
          }
        }
        
        .item-sales-table {
          display: flex;
          flex-direction: column;
          width: 100%;
          height: 100%;
          
          .item-sales-table-row {
            display: flex;
            align-items: center;
            flex-direction: row;
            flex-wrap: nowrap;
            width: 100%;
            padding: 15px 30px;
            
            &:last-child {
              border-radius: 0 0 5px 5px;
            }
          }
          
          .item-sales-table-cell {
            flex: 1;
            font-size: 16px;
            font-weight: 500;
            text-align: center;
            white-space: nowrap;
            color: $light-color;
            
            &:first-child {
              text-align: left;
            }
            
            &:last-child {
              text-align: right;
            }
            
            span {
              color: $gray-light-color;
            }
          }
          
          .item-sales-table-head {
            display: flex;
            align-items: center;
            flex-direction: row;
            
            .item-sales-table-cell {
              font-size: 16px;
              font-weight: 600;
              text-align: center;
              white-space: nowrap;
              color: $gray-light-color;
              
              &:first-child {
                text-align: left;
              }
              
              &:last-child {
                text-align: right;
              }
            }
          }
          
          .item-sales-table-body {
            display: flex;
            align-items: center;
            flex-direction: column;
            overflow-y: scroll;
            height: 100%;
            
            .item-sales-table-row {
              background: $dark-color;
              
              &:nth-child(odd) {
                background: #171C25;
              }
            }
          }
        }
      }
    }
  }
  
