@import "src/assets/styles/colors";

.phone-number-input {
  display: flex;
  flex-direction: column;
  position: relative;
  width: 100%;
  border-radius: 5px;
  
  .input-focus {
    border-radius: 5px;
    box-shadow: 0 0 0 1.5px $primary-color;
  }
  
  .input-error {
    border-radius: 5px;
    box-shadow: 0 0 0 1.5px $danger-color;
  }
  
  .phoe-number-input--prepend {
    display: flex;
    position: absolute;
    top: 20px;
    left: 20px;
    bottom: 20px;
    font-size: 18px;
    font-weight: 500;
    color: $gray-light-color;
  }
  
  input {
    border-radius: 5px;
    width: 100%;
    padding: 20px;
    background: $dark-secondary-color;
    font-size: 18px;
    font-weight: 600;
    color: $light-color;
  }
}