@import "./src/assets/styles/colors.scss";
@import "./src/assets/styles/breakpoints.scss";

.cart-page {
  .btn {
    &:disabled {
      background: rgba($primary-color, 0.55);
    }
  }
  
  &.mobile {
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: space-between;
    width: 100%;
    height: 100%;
    padding: 30px;
    background: $dark-color;
    
    .cart-page-title {
      display: flex;
      align-items: center;
      flex-direction: row;
      justify-content: center;
      width: 100%;
      margin-bottom: 30px;
      font-size: 24px;
      font-weight: 700;
      
      span {
        margin-left: 10px;
        color: $gray-light-color;
      }
    }
    
    .cart-page-divider {
      width: 100%;
      height: 1px;
      min-height: 1px;
      margin: 30px 0;
      background: $secondary-color;
    }
    
    .cart-page-block {
      width: 100%;
      
      &.items {
        display: flex;
        align-items: center;
        flex-direction: row;
        flex-wrap: wrap;
        width: 100%;
        margin: 0 -5px -5px -5px;
        
        & > div {
          flex: 1 1 250px;
          margin: 5px;
        }
      }
      
      .cart-info-row {
        display: flex;
        align-items: center;
        flex-direction: row;
        justify-content: space-between;
        font-size: 20px;
        font-weight: 600;
        color: $light-color;
        
        &:not(:last-child) {
          margin-bottom: 15px;
        }
        
        &.items-count {
          color: $gray-light-color;
        }
        
        &.buy-btn {
          .btn {
            width: 100%;
          }
        }
        
        &.checkbox-container {
          display: flex;
          flex-direction: row;
          align-items: center;
          width: 100%;
          
          label {
            padding: 20px 0;
            width: 100%;
            margin: 10px 0;
            display: flex;
            flex-direction: row;
            align-items: center;
            font-size: 16px;
            font-weight: 500;
            color: $gray-light-color;
            cursor: pointer;
            user-select: none;
            
            input[type="checkbox"] {
              position: relative;
              width: 24px;
              min-width: 24px;
              height: 24px;
              min-height: 24px;
              margin: 0 15px 0 0;
              border-radius: 5px;
              border: 1px solid $gray-light-color;
              background: $dark-color;
              appearance: none;
              outline: 0;
              cursor: pointer;
              transition: background 175ms cubic-bezier(0.1, 0.1, 0.25, 1);
              
              &:before {
                content: "";
                position: absolute;
                display: block;
                opacity: 0;
              }
              
              &:checked {
                border-color: $primary-color;
                background: $primary-color;
                
                &::before {
                  opacity: 1;
                  border-style: solid;
                  border-color: #FFFFFF;
                  border-width: 0 3px 3px 0;
                  transform: rotate(45deg);
                  top: 3px;
                  left: 7px;
                  width: 5px;
                  height: 10px;
                }
                
                ~ span, ~ label {
                  color: $light-color;
                }
              }
              
              &:indeterminate {
                border-color: $primary-color;
                background: $primary-color;
                
                &::before {
                  opacity: 1;
                  content: "+";
                  top: -8px;
                  bottom: 0;
                  right: 0;
                  left: 2px;
                  font-size: 32px;
                }
                
                ~ span, ~ label {
                  color: $light-color;
                }
              }
            }
          }
        }
      }
    }
  }
  
