              width: 22px;
              height: 22px;
              fill: $gray-light-color;
            }
            
            span {
              margin-left: 12px;
              font-size: 18px;
              font-weight: 500;
              color: $gray-light-color;
            }
            
            &:hover {
              svg {
                fill: $danger-color;
              }
              
              span {
                color: $danger-color;
              }
            }
            
          }
        }
      }
    }
    
    .cart-page-block-right {
      display: flex;
      align-items: center;
      flex-direction: column;
      justify-content: space-between;
      min-width: 465px;
      padding: 50px;
      background: $dark-secondary-color;
      border-radius: 5px;
      
      & > .col {
        width: 100%;
      }
      
      .col.controls {
        width: 100%;
        //margin-top: 180px;
        margin-top: 50px;
        
        .check-container {
          display: flex;
          align-items: center;
          flex-direction: row;
          justify-content: space-between;
          margin-bottom: 40px;
          
          label {
            display: flex;
            flex-direction: row;
            align-items: center;
            font-size: 18px;
            font-weight: 500;
            color: $gray-light-color;
            cursor: pointer;
            user-select: none;
            
            input[type="checkbox"] {
              position: relative;
              width: 24px;
              min-width: 24px;
              height: 24px;
              min-height: 24px;
              margin: 0 15px 0 0;
              border-radius: 5px;
              border: 1px solid $gray-light-color;
              background: $dark-color;
              appearance: none;
              outline: 0;
              cursor: pointer;
              transition: background 175ms cubic-bezier(0.1, 0.1, 0.25, 1);
              
              &:before {
                content: "";
                position: absolute;
                display: block;
                opacity: 0;
              }
              
              &:checked {
                border-color: $primary-color;
                background: $primary-color;
                
                &::before {
                  opacity: 1;
                  border-style: solid;
                  border-color: #FFFFFF;
                  border-width: 0 3px 3px 0;
                  transform: rotate(45deg);
                  top: 3px;
                  left: 7px;
                  width: 5px;
                  height: 10px;
                }
                
                ~ span, ~ label {
                  color: $light-color;
                }
              }
              
              &:indeterminate {
                border-color: $primary-color;
                background: $primary-color;
                
                &::before {
                  opacity: 1;
                  content: "+";
                  top: -8px;
                  bottom: 0;
                  right: 0;
                  left: 2px;
                  font-size: 32px;
                }
                
                ~ span, ~ label {
                  color: $light-color;
                }
              }
            }
          }
          
          .info-btn {
            display: flex;
            align-items: center;
            justify-content: center;
            width: 27px;
            min-width: 27px;
            height: 27px;
            cursor: pointer;
            user-select: none;
            font-size: 18px;
            font-weight: 700;
            color: $light-color;
            background: $secondary-color;
            border-radius: 50%;
          }
        }
        
        .btn {
          &:disabled {
            background: rgba($primary-color, 0.55);
          }
        }
      }
      
      .block-title {
        font-size: 36px;
        font-weight: 700;
        
        color: $light-color;
      }
      
      .cart-description {
        .cart-description-row {
          display: flex;
          align-items: center;
          flex-direction: row;
          justify-content: space-between;
          width: 100%;
          margin-bottom: 20px;
          
          &:first-child {
            margin-top: 50px;
          }
          
          .cart-description-row-left {
            font-size: 18px;
            font-weight: 500;
            text-align: left;
            color: $gray-light-color;
          }
          
          .cart-description-row-right {
            font-size: 18px;
            font-weight: 600;
            text-align: right;
            
            .currency {
              margin-right: 5px;
            }
          }
          
          &.sum {
            margin-top: 40px;
            
            .cart-description-row-left {
              font-size: 24px;
              font-weight: 500;
              color: $light-color;
            }
            
            .cart-description-row-right {
              font-size: 36px;
              font-weight: 700;
              color: $light-color;
              
              .currency {
                color: $primary-color;
              }
            }
            
          }
        }
      }
      
      .divider-h {
        width: 100%;
        margin-top: 30px;
        margin-bottom: 50px;
      }
    }
    
    .item.cart .delete-item svg {
      width: 18px;
      height: 18px;
    }
  }
  
