@import "src/assets/styles/colors";

.error-page {
  &.from-large-desktop {
    display: flex;
    align-items: center;
    flex-direction: row;
    justify-content: center;
    width: 100vw;
    margin: 230px 50px 50px 50px;
    padding: 50px;
    background: url("bg.jpg") center no-repeat no-repeat;
    border-radius: 5px;
    
    .left-image {
      width: 320px;
      height: 150px;
    }
    
    .right-image {
      width: 320px;
      height: 150px;
    }
    
    img {
      width: auto;
      height: 150px;
    }
    
    .error {
      display: flex;
      align-items: center;
      flex-direction: column;
      justify-content: center;
      
      .title {
        margin-bottom: 15px;
        font-size: 48px;
        font-weight: 600;
        color: $light-color;
      }
      
      .status-code {
        margin-bottom: 15px;
        font-size: 36px;
        font-weight: 500;
        color: $gray-light-color;
      }
      
      .error-description {
        width: 400px;
        margin-bottom: 30px;
        font-size: 25px;
        font-weight: 500;
        text-align: center;
        color: $light-color;
      }
      
      .buttons {
        display: flex;
        align-items: center;
        flex-direction: row;
        justify-content: center;
        width: 100%;
        
        a {
          &:first-child {
            margin-right: 30px;
          }
        }
      }
    }
  }
  
  &.mobile {
    display: flex;
    align-items: center;
    flex-direction: row;
    justify-content: center;
    width: 100%;
    background: url("bg.jpg") center no-repeat no-repeat;
    border-radius: 5px;
    padding: 20px;
    
    .error {
      display: flex;
      align-items: center;
      flex-direction: column;
      justify-content: center;
      width: 100%;
      
      .title {
        margin-bottom: 15px;
        font-size: 24px;
        font-weight: 600;
        color: $light-color;
      }
      
      .status-code {
        margin-bottom: 15px;
        font-size: 16px;
        font-weight: 500;
        color: $gray-light-color;
      }
      
      .error-description {
        width: 100%;
        margin-bottom: 30px;
        font-size: 16px;
        font-weight: 500;
        text-align: center;
        color: $light-color;
      }
      
      .buttons {
        display: flex;
        align-items: center;
        flex-direction: column;
        justify-content: center;
        width: 100%;
        
        a {
          width: 100%;
          
          .btn {
            width: 100%;
          }
          
          &:first-child {
            margin-bottom: 15px;
          }
        }
      }
    }
  }
  
