@import "src/assets/styles/colors";

.range-slider {
  width: 100%;
  height: 10px;
  position: relative;
  border-radius: 5px;
  user-select: none;
  
  .range-slider-rail {
    display: block;
    width: 100%;
    height: 10px;
    position: relative;
    border-radius: inherit;
    background: linear-gradient(90deg, rgba(41, 191, 80, 1) 0%, rgba(41, 191, 80, 1) 7%, rgba(65, 230, 135, 1) 8%, rgba(255, 206, 78, 1) 15%, rgba(255, 206, 78, 1) 16%, rgba(255, 166, 25, 1) 38%, rgba(255, 166, 25, 1) 39%, rgba(255, 137, 26, 1) 45%, rgba(248, 83, 53, 1) 46%, rgba(237, 13, 81, 1) 100%);
  }
  
  .range-slider-component {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    
    .range-slider-track-box {
      width: 0;
      height: 10px;
      background: $secondary-color;
      transition: 175ms cubic-bezier(0.1, 0.1, 0.25, 1);
      
      &.box-1 {
        border-bottom-left-radius: 5px;
        border-top-left-radius: 5px;
      }
      
      &.box-2 {
        border-bottom-right-radius: 5px;
        border-top-right-radius: 5px;
      }
    }
    
    .range-slider-track {
      position: absolute;
      left: 10%;
      width: 100%;
      height: 10px;
      background: transparent;
      transition: 175ms cubic-bezier(0.1, 0.1, 0.25, 1);
    }
  }
  
  .range-slider-handle {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    position: absolute;
    width: 20px;
    height: 20px;
    cursor: pointer;
    transition: 175ms cubic-bezier(0.1, 0.1, 0.25, 1);
    outline: none !important;
    transform: translate3d(-50%, 20px, 0);
    
    svg {
      position: absolute;
      width: 20px;
      height: 20px;
    }
  }
}