@import "src/assets/styles/colors";

.autocomplete-input {
  position: relative;
  
  .remove-input-value {
    position: absolute;
    top: 22px;
    right: 30px;
    bottom: 0;
    cursor: pointer;
    
    &:hover {
      svg {
        fill: $light-color;
      }
    }
    
    svg {
      width: 18px;
      height: 18px;
      fill: $danger-color;
    }
  }
  
  .autocomplete-input--selected-item {
    height: 60px;
    width: 370px;
    margin-right: 15px;
    background: $secondary-color;
    padding: 15px 20px;
    border-radius: 5px;
    font-size: 18px;
    font-weight: 500;
    display: flex;
    flex-direction: row;
    align-items: center;
    
    img {
      width: 54px;
      height: auto;
      margin-right: 20px;
    }
  }
  
  .autocomplete-input--input {
    height: 60px;
    width: 370px;
    margin-right: 15px;
    background: #151921;
    padding: 15px 20px;
    border-radius: 5px;
    font-size: 18px;
    font-weight: 500;
    display: flex;
    flex-direction: row;
    align-items: center;
    
    &.input-focus {
      box-shadow: 0 0 0 1.5px inset $primary-color;
    }
    
    &.input-error {
      box-shadow: 0 0 0 1.5px inset $danger-color;
    }
    
    &.input-success {
      box-shadow: 0 0 0 1.5px inset $success-color
    }
  }
  
  .suggestions-container {
    position: absolute;
    box-shadow: 0 0 1px 0 $dark-secondary-color;
    font-size: 18px;
    font-weight: 500;
    border-radius: 5px;
    top: 70px;
    max-height: 180px;
    overflow-y: scroll;
    width: 370px;
    
    .suggestions-not-found {
      display: flex;
      flex-direction: row;
      align-items: center;
      color: $gray-light-color;
      cursor: pointer;
      padding: 15px 20px;
      background: $secondary-color;
    }
    
    .suggestions {
      display: flex;
      flex-direction: row;
      align-items: center;
      color: $gray-light-color;
      cursor: pointer;
      padding: 15px 20px;
      background: $secondary-color;
      
      img {
        width: 54px;
        max-height: 30px;
        margin-right: 20px;
      }
      
      &:hover {
        color: $light-color;
        background: lighten($secondary-color, 5%);
      }
    }
  }
}