@import "src/assets/styles/colors";

.stickers-filter {
  .sticker-search-input {
    position: relative;
    display: flex;
    flex-direction: column;
    width: 100%;
    
    &.input-focus {
      box-shadow: 0 0 0 1.5px inset $primary-color;
    }
    
    .input-container {
      display: flex;
      align-items: center;
      flex-direction: row;
      user-select: none;
      background: $dark-secondary-color;
      border-radius: 5px;
      
      svg {
        width: 20px;
        min-width: 20px;
        height: 20px;
        margin-left: 20px;
        fill: $light-color;
      }
      
      input {
        width: 100%;
        padding: 20px;
        color: $light-color;
        
        &.input-focus {
          box-shadow: 0 0 0 1.5px inset $primary-color;
        }
      }
      
      .remove-input-value {
        display: flex;
        align-items: center;
        flex-direction: row;
        padding-right: 20px;
        cursor: pointer;
        
        &:active {
          svg {
            fill: darken($danger-color, 5%);
          }
        }
        
        &:active {
          svg {
            fill: lighten($danger-color, 5%);
          }
        }
        
        svg {
          width: 16px;
          height: 16px;
          margin-left: 0;
          fill: $danger-color;
        }
      }
    }
    
    .suggestions-container {
      position: relative;
      right: 0;
      //top: 82px;
      left: 0;
      z-index: 5;
      display: flex;
      flex-direction: column;
      overflow-y: auto;
      width: 100%;
      max-height: 185px;
      margin-top: 20px;
      background: $secondary-color;
      border-radius: 5px;
      
      .suggestion {
        display: flex;
        align-items: center;
        flex-direction: row;
        padding: 20px;
        cursor: pointer;
        -webkit-user-select: none;
        user-select: none;
        font-size: 18px;
        font-weight: 500;
        color: #6D7681;
      }
    }
  }
  
  .stickers-filter--stickers {
    display: flex;
    align-items: center;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: space-between;
    margin-top: 30px;
    padding: 0 20px;
    
    .sticker {
      display: flex;
      align-items: center;
      flex-direction: row;
      justify-content: center;
      cursor: pointer;
      user-select: none;
      
      &:not(:last-child) {
        margin-right: 40px;
      }
      
      img {
        width: 50px;
        height: 50px;
      }
      
      svg {
        width: 50px;
        height: 50px;
        fill: $secondary-color;
      }
      
      .placeholder {
        position: relative;
        
        span {
          position: absolute;
          top: 0;
          right: 0;
          bottom: 0;
          left: 0;
          display: flex;
          align-items: center;
          flex-direction: row;
          justify-content: center;
          font-size: 20px;
          font-weight: 800;
          color: $gray-light-color;
        }
      }
      
      .icon {
        position: relative;
        display: flex;
        align-items: center;
        flex-direction: row;
        
        .remove-icon {
          position: absolute;
          top: 0;
          right: 0;
          display: flex;
          align-items: center;
          flex-direction: row;
          justify-content: center;
          background: $danger-color;
          box-shadow: 0 0 0 1px $light-color;
          border-radius: 50%;
          width: 20px;
          height: 20px;
          padding: 5px;
          
          svg {
            width: 16px;
            height: 16px;
            fill: $light-color;
          }
        }
      }
    }
  }
}