@import "src/assets/styles/colors";

.modal-wrapper-item-sell {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 10000;
  display: none;
  align-items: center;
  flex-direction: row;
  justify-content: center;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  
  &.fullscreen {
    &.active {
      display: flex;
    }
    
    .modal-block {
      position: relative;
      display: flex;
      overflow: hidden;
      width: 1540px;
      min-width: 1540px;
      height: 755px;
      min-height: 755px;
      padding: 50px;
      white-space: nowrap;
      background: #1A1E29;
      border-radius: 5px;
      
      @keyframes rotate {
        100% {
          transform: rotate(1turn);
        }
      }
      
      .close-button {
        position: absolute;
        top: 20px;
        right: 20px;
        cursor: pointer;
        
        svg {
          width: 20px;
          height: 20px;
          fill: #FFFFFF;
        }
      }
      
      .modal-body {
        display: flex;
        flex-direction: row;
        width: 100%;
        
        .block {
          display: flex;
          align-items: flex-start;
          flex-direction: column;
          
          &.first {
            width: 315px;
            margin-right: 50px;
            
            .item-info {
              display: flex;
              flex-direction: column;
              width: 100%;
              
              .item-info-top {
                display: flex;
                align-items: center;
                flex-direction: row;
                width: 100%;
                
                .item-name-info {
                  display: flex;
                  align-items: flex-start;
                  flex-direction: column;
                  width: 100%;
                  
                  .item-name {
                    margin-bottom: 5px;
                    font-size: 14px;
                    font-weight: 500;
                    color: $gray-light-color;
                  }
                  
                  .item-item {
                    font-size: 24px;
                    font-weight: 600;
                    color: $light-color;
                  }
                }
                
                .item-exterior {
                  margin-left: auto;
                  font-size: 18px;
                  font-weight: 600;
                  text-transform: uppercase;
                  color: $gray-light-color;
                }
              }
              
              .item-img {
                width: 315px;
                height: 160px;
                
                img {
                  width: 315px;
                  height: auto;
                }
              }
            }
            
            .item-screenshots {
              display: flex;
              align-items: center;
              flex-direction: column;
              justify-content: center;
              height: 310px;
              margin-top: auto;
              
              .block-title {
                display: flex;
                align-items: center;
                flex-direction: row;
                width: 100%;
                margin-bottom: 30px;
                font-size: 18px;
                font-weight: 700;
                
                a {
                  display: flex;
                  align-items: center;
                  flex-direction: row;
                  margin-left: auto;
                  font-size: 14px;
                  font-weight: 500;
                  color: $gray-light-color;
                  
                  &:hover {
                    color: $primary-color;
                    
                    span {
                      background: $primary-color;
                      
                      svg {
                        fill: $light-color;
                      }
                    }
                  }
                  
                  span {
                    display: flex;
                    align-items: center;
                    flex-direction: row;
                    justify-content: center;
                    width: 24px;
                    min-width: 24px;
                    height: 24px;
                    min-height: 24px;
                    margin-left: 10px;
                    background: $secondary-color;
                    border-radius: 100%;
                    
                    
                    svg {
                      width: 10px;
                      height: 10px;
                      fill: $gray-light-color;
                    }
                  }
                }
              }
              
              .screenshots {
                padding: 50px;
                border: 5px dashed $secondary-color;
                border-radius: 5px;
                
                
                .modal-body-screenshots {
                  display: flex;
                  align-items: center;
                  flex-direction: column;
                  justify-content: center;
                }
                
                .text {
                  .main-text {
                    display: flex;
                    align-items: center;
                    flex-direction: row-reverse;
                    margin-bottom: 30px;
                    font-size: 16px;
                    font-weight: 700;
                    text-align: center;
                    
                    svg {
                      width: 28px;
                      height: 28px;
                      margin-right: 20px;
                      fill: #4A99EB;
                    }
                  }
                  
                  .sub-text {
                    margin-bottom: 30px;
                    font-size: 18px;
                    font-weight: 500;
                    text-align: center;
                    color: #6D7681;
                  }
                }
                
                .button-open-file {
                  width: 114px;
                  height: 47px;
                  
                  margin-bottom: 30px;
                  padding: 13px 20px;
                }
                
                #file {
                  display: none;
                }
                
                .files-uploaded {
                  display: flex;
                  align-items: center;
                  flex-direction: row;
                  flex-wrap: wrap;
                  justify-content: center;
                  margin: -5px;
                  padding: 25px 30px;
                  font-size: 14px;
                  font-weight: 400;
                  color: $gray-light-color;
                  
                  span {
                    padding: 5px;
                  }
                }
                
                .max-file-size {
                  margin-bottom: 10px;
                  font-size: 18px;
                  font-weight: 500;
                  color: #6D7681;
                }
                
                .max-files {
                  margin-bottom: 10px;
                  font-size: 18px;
                  font-weight: 500;
                  color: #6D7681;
                }
                
                .supported-file-types {
                  font-size: 18px;
                  font-weight: 500;
                  color: #6D7681;
                }
              }
            }
          }
          
          &.second {
            width: 374px;
            margin-right: 50px;
            
            .item-info {
              display: flex;
              flex-direction: column;
              width: 375px;
              height: 245px;
              margin-bottom: 50px;
              padding: 30px;
              background: #171B24;
              border-radius: 5px;
              
              .prices {
                display: flex;
                align-items: center;
                flex-direction: row;
                margin-bottom: 30px;
                
                .price {
                  font-size: 20px;
                  font-weight: 700;
                  color: $light-color;
                }
                
                .steam-price {
                  margin-left: auto;
                  font-size: 14px;
                  font-weight: 600;
                  color: $gray-light-color;
                }
              }
              
              .item-float {
                display: flex;
                align-items: center;
                flex-direction: column;
                width: 100%;
                margin-bottom: 30px;
                
                .float {
                  display: flex;
                  align-items: center;
                  flex-direction: row;
                  width: 100%;
                  font-size: 18px;
                  font-weight: 700;
                  color: $gray-light-color;
                  
                  span:last-child {
                    margin-left: auto;
                    color: $light-color;
                  }
                }
                
                .floatbar {
                  display: flex;
                  align-items: center;
                  flex-direction: row;
                  width: 100%;
                  height: 10px;
                  margin-bottom: 15px;
                  border-radius: 5px;
                  
                  .float-arrow {
                    top: -18px;
                  }
                  
                  .fn {
                    width: 7%;
                    height: 100%;
                    background: linear-gradient(90deg, #29BF50 0%, #29BF50 100%);
                    border-top-left-radius: 5px;
                    border-bottom-left-radius: 5px;
                  }
                  
                  .mw {
                    width: 8%;
                    height: 100%;
                    background: linear-gradient(90deg, #41E687 0%, #41E687 100%);
                  }
                  
                  .ft {
                    width: 23%;
                    height: 100%;
                    background: linear-gradient(90deg, #FFCD4E 0%, #FFB24E 100%);
                  }
                  
                  .ww {
                    width: 7%;
                    height: 100%;
                    background: linear-gradient(90deg, #FFA519 0%, #FF8A1A 100%);
                  }
                  
                  .bs {
                    width: 65%;
                    height: 100%;
                    background: linear-gradient(90deg, #F85335 0%, #ED0D51 100%);
                    border-top-right-radius: 5px;
                    border-bottom-right-radius: 5px;
                  }
                }
              }
              
              .item-stickers {
                display: flex;
                align-items: center;
                flex-direction: row;
                justify-content: space-between;
                height: 50px;
                
                .item-sticker {
                  display: flex;
                  align-items: center;
                  flex-direction: row;
                  justify-content: center;
                  cursor: pointer;
                  
                  &:not(:last-child) {
                    margin-right: 35px;
                  }
                  
                  img {
                    width: 50px;
                    height: 50px;
                  }
                  
                  .__react_component_tooltip {
                    display: flex;
                    align-items: center;
                    flex-direction: column;
                    justify-content: center;
                    min-width: 150px;
                    max-width: 280px;
                    padding: 15px;
                    font-size: 14px;
                    font-weight: 600;
                    white-space: normal;
                    background: $secondary-color;
                    border-radius: 5px;
                    opacity: 1;
                    
                    &.place-top {
                      &:after {
                        border-top-color: $secondary-color;
                      }
                    }
                    
                    &.place-left {
                      &:after {
                        border-left-color: $secondary-color;
                      }
                    }
                    
                    &.place-bottom {
                      &:after {
                        border-bottom-color: $secondary-color;
                      }
                    }
                    
                    &.place-right {
                      &:after {
                        border-right-color: $secondary-color;
                      }
                    }
                    
                    .sticker-title {
                      margin-bottom: 10px;
                      font-size: 18px;
                      font-weight: 600;
                      color: $light-color;
                    }
                    
                    .sticker-wear {
                      font-size: 18px;
                      font-weight: 600;
                      color: $gray-light-color;
                    }
                  }
                }
              }
            }
            
            .additionals {
              display: flex;
              flex-direction: column;
              width: 100%;
              height: 120px;
              margin-bottom: 50px;
              
              .additionals-title {
                display: flex;
                align-items: center;
                flex-direction: row;
                margin-bottom: 40px;
                font-size: 18px;
                font-weight: 700;
                color: $light-color;
                
                span {
                  margin-left: auto;
                  font-size: 14px;
                  font-weight: 600;
                  color: $gray-light-color;
                  
                  label {
                    display: flex;
                    flex-direction: row-reverse;
                    
                    input {
                      margin-right: 0;
                    }
                    
                    span {
                      margin-right: 10px;
                      margin-left: 0;
                    }
                  }
                }
              }
            }
            
            .additionals-price {
              display: flex;
              align-items: center;
              flex-direction: row;
              width: 100%;
              padding: 20px 30px;
              font-size: 16px;
              font-weight: 500;
              color: $gray-light-color;
              background: $secondary-color;
              border-radius: 5px;
              
              .icon {
                display: flex;
                align-items: center;
                flex-direction: row;
                justify-content: center;
                width: 22px;
                height: 22px;
                margin-right: 20px;
                
                svg {
                  width: 22px;
                  min-width: 22px;
                  height: 22px;
                  min-height: 22px;
                  fill: $primary-color;
                }
              }
              
              span:last-child {
                margin-left: auto;
                font-weight: 600;
                color: $light-color;
              }
            }
            
            .item-price {
              width: 100%;
              margin-bottom: 40px;
              
              input {
                display: flex;
                align-items: center;
                flex-direction: row;
                width: 100%;
                margin-bottom: 30px;
                padding: 15px;
                font-size: 18px;
                font-weight: 500;
                background: $dark-secondary-color;
                border-radius: 5px;
                
                &.input-focus {
                  box-shadow: 0 0 0 1.5px inset $primary-color;
                }
              }
              
              .price-info {
                display: flex;
                align-items: center;
                flex-direction: row;
                
                .price-info-block-divider {
                  display: flex;
                  flex-direction: row;
                  width: 1px;
                  height: 30px;
                  margin: 0 30px;
                  background: $secondary-color;
                  border-radius: 5px;
                }
                
                .price-info-block {
                  display: flex;
                  align-items: flex-start;
                  flex-direction: column;
                  
                  span {
                    .gray {
                      color: $gray-light-color !important;
                    }
                    
                    &:first-child {
                      margin-bottom: 5px;
                      font-size: 14px;
                      font-weight: 500;
                      color: $gray-light-color;
                    }
                    
                    &:last-child {
                      font-size: 16px;
                      font-weight: 600;
                      color: $light-color;
                    }
                  }
                }
              }
            }
            
            .buttons {
              display: flex;
              align-items: center;
              flex-direction: row;
              
              .btn {
                width: 100%;
                
                &:first-child {
                  margin-right: 15px;
                }
              }
            }
          }
          
          &.third {
            width: 653px;
            
            .item-container {
              display: flex;
              align-items: center;
              flex-direction: column;
              height: 100%;
              
              &:not(:last-child) {
                margin-bottom: 50px;
              }
              
              .item-container-title {
                display: flex;
                align-items: center;
                flex-direction: row;
                width: 100%;
                margin-bottom: 40px;
                font-size: 20px;
                font-weight: 700;
                color: $light-color;
                
                .item-history-view-switcher {
                  display: flex;
                  align-items: center;
                  flex-direction: row;
                  justify-content: flex-start;
                  margin-left: auto;
                  
                  .item-history-view {
                    position: relative;
                    display: flex;
                    align-items: center;
                    flex-direction: row;
                    justify-content: center;
                    cursor: pointer;
                    font-size: 24px;
                    font-weight: 700;
                    color: $gray-light-color;
                    
                    &:not(:last-child) {
                      margin-right: 30px;
                    }
                    
                    &.active {
                      font-weight: 700;
                      color: $light-color;
                      
                      &:before {
                        position: absolute;
                        top: -48px;
                        content: "";
                        width: 30px;
                        height: 4px;
                        background: $primary-color;
                      }
                    }
                  }
                }
              }
              
              .item-sales-table {
                display: flex;
                flex-direction: column;
                width: 100%;
                
                .item-sales-table-row {
                  display: flex;
                  align-items: center;
                  flex-direction: row;
                  flex-wrap: nowrap;
                  width: 100%;
                  padding: 15px 50px;
                  
                  &:last-child {
                    border-radius: 0 0 5px 5px;
                  }
                }
                
                .item-sales-table-cell {
                  flex: 1;
                  font-size: 16px;
                  font-weight: 500;
                  text-align: center;
                  white-space: nowrap;
                  color: $light-color;
                  
                  &:first-child {
                    text-align: left;
                  }
                  
                  &:last-child {
                    text-align: right;
                  }
                  
                  span {
                    color: $gray-light-color;
                  }
                }
                
                .item-sales-table-head {
                  display: flex;
                  align-items: center;
                  flex-direction: row;
                  
                  .item-sales-table-cell {
                    font-size: 16px;
                    font-weight: 600;
                    text-align: center;
                    white-space: nowrap;
                    color: $gray-light-color;
                    
                    &:first-child {
                      text-align: left;
                    }
                    
                    &:last-child {
                      text-align: right;
                    }
                  }
                }
                
                .item-sales-table-body {
                  display: flex;
                  align-items: center;
                  flex-direction: column;
                  
                  .item-sales-table-row {
                    background: $dark-color;
                    
                    &:nth-child(odd) {
                      background: #171C25;
                    }
                  }
                }
              }
              
              &.item-chart {
                position: relative;
                z-index: 10001;
                align-items: normal;
                width: 100%;
                height: 100%;
                
                .item-container-title {
                  display: flex;
                  align-items: flex-start;
                  flex-direction: column;
                  width: 100%;
                  
                  .title {
                    display: flex;
                    width: 100%;
                  }
                }
                
                .date-selector {
                  display: flex;
                  align-items: center;
                  flex-direction: row;
                  cursor: pointer;
                  user-select: none;
                  
                  span {
                    font-size: 18px;
                    font-weight: 500;
                    color: $gray-light-color;
                    
                    &.active {
                      color: $primary-color;
                    }
                    
                    &:not(:last-child) {
                      margin-right: 20px;
                    }
                  }
                }
                
                
                svg {
                  z-index: 10001;
                  user-select: none;
                  
                  svg {
                    z-index: 45945459459 !important;
                  }
                }
              }
            }
          }
        }
      }
    }
  }
  
