                flex-direction: column;
                width: 100%;
                margin-bottom: 30px;
                
                .float {
                  display: flex;
                  align-items: center;
                  flex-direction: row;
                  width: 100%;
                  font-size: 18px;
                  font-weight: 700;
                  color: $gray-light-color;
                  
                  span:last-child {
                    margin-left: auto;
                    color: $light-color;
                  }
                }
                
                .floatbar {
                  display: flex;
                  align-items: center;
                  flex-direction: row;
                  width: 100%;
                  height: 10px;
                  margin-bottom: 15px;
                  border-radius: 5px;
                  
                  .float-arrow {
                    top: -18px;
                  }
                  
                  .fn {
                    width: 7%;
                    height: 100%;
                    background: linear-gradient(90deg, #29BF50 0%, #29BF50 100%);
                    border-top-left-radius: 5px;
                    border-bottom-left-radius: 5px;
                  }
                  
                  .mw {
                    width: 8%;
                    height: 100%;
                    background: linear-gradient(90deg, #41E687 0%, #41E687 100%);
                  }
                  
                  .ft {
                    width: 23%;
                    height: 100%;
                    background: linear-gradient(90deg, #FFCD4E 0%, #FFB24E 100%);
                  }
                  
                  .ww {
                    width: 7%;
                    height: 100%;
                    background: linear-gradient(90deg, #FFA519 0%, #FF8A1A 100%);
                  }
                  
                  .bs {
                    width: 65%;
                    height: 100%;
                    background: linear-gradient(90deg, #F85335 0%, #ED0D51 100%);
                    border-top-right-radius: 5px;
                    border-bottom-right-radius: 5px;
                  }
                }
              }
              
              .item-stickers {
                display: flex;
                align-items: center;
                flex-direction: row;
                justify-content: space-between;
                height: 50px;
                
                .item-sticker {
                  display: flex;
                  align-items: center;
                  flex-direction: row;
                  justify-content: center;
                  cursor: pointer;
                  
                  img {
                    width: 60px;
                    height: 60px;
                  }
                  
                  .__react_component_tooltip {
                    display: flex;
                    align-items: center;
                    flex-direction: column;
                    justify-content: center;
                    min-width: 150px;
                    max-width: 280px;
                    padding: 15px;
                    font-size: 14px;
                    font-weight: 600;
                    white-space: normal;
                    background: $secondary-color;
                    border-radius: 5px;
                    opacity: 1;
                    
                    &.place-top {
                      &:after {
                        border-top-color: $secondary-color;
                      }
                    }
                    
                    &.place-left {
                      &:after {
                        border-left-color: $secondary-color;
                      }
                    }
                    
                    &.place-bottom {
                      &:after {
                        border-bottom-color: $secondary-color;
                      }
                    }
                    
                    &.place-right {
                      &:after {
                        border-right-color: $secondary-color;
                      }
                    }
                    
                    .sticker-title {
                      margin-bottom: 10px;
                      font-size: 18px;
                      font-weight: 600;
                      color: $light-color;
                    }
                    
                    .sticker-wear {
                      font-size: 18px;
                      font-weight: 600;
                      color: $gray-light-color;
                    }
                  }
                }
              }
            }
            
            .item-screenshots {
              display: flex;
              align-items: center;
              flex-direction: column;
              justify-content: center;
              width: 100%;
              margin-bottom: 10px;
              padding: 30px;
              
              .block-title {
                display: flex;
                align-items: center;
                flex-direction: row;
                width: 100%;
                margin-bottom: 30px;
                font-size: 18px;
                font-weight: 700;
                
                a {
                  display: flex;
                  align-items: center;
                  flex-direction: row;
                  margin-left: auto;
                  font-size: 14px;
                  font-weight: 500;
                  color: $gray-light-color;
                  
                  &:hover {
                    color: $primary-color;
                    
                    span {
                      background: $primary-color;
                      
                      svg {
                        fill: $light-color;
                      }
                    }
                  }
                  
                  span {
                    display: flex;
                    align-items: center;
                    flex-direction: row;
                    justify-content: center;
                    width: 24px;
                    min-width: 24px;
                    height: 24px;
                    min-height: 24px;
                    margin-left: 10px;
                    background: $secondary-color;
                    border-radius: 100%;
                    
                    
                    svg {
                      width: 10px;
                      height: 10px;
                      fill: $gray-light-color;
                    }
                  }
                }
              }
              
              .button-open-file {
                width: 100%;
                height: 47px;
                padding: 13px 20px;
              }
            }
            
            .additionals {
              display: flex;
              flex-direction: column;
              width: 100%;
              margin-bottom: 10px;
              padding: 0 30px;
              
              .additionals-title {
                display: flex;
                align-items: center;
                flex-direction: row;
                margin-bottom: 20px;
                font-size: 18px;
                font-weight: 700;
                color: $light-color;
                
                span {
                  margin-left: auto;
                  font-size: 14px;
                  font-weight: 600;
                  color: $gray-light-color;
                  
                  label {
                    display: flex;
                    flex-direction: row-reverse;
                    
                    input {
                      margin-right: 0;
                    }
                    
                    span {
                      margin-right: 10px;
                      margin-left: 0;
                    }
                  }
                }
              }
            }
            
            .additionals-price {
              display: flex;
              align-items: center;
              flex-direction: row;
              width: 100%;
              margin-bottom: 20px;
              padding: 20px;
              font-size: 16px;
              font-weight: 500;
              color: $gray-light-color;
              background: $secondary-color;
              border-radius: 5px;
              
              .icon {
                display: flex;
                align-items: center;
                flex-direction: row;
                justify-content: center;
                width: 22px;
                height: 22px;
                margin-right: 20px;
                
                svg {
                  width: 22px;
                  min-width: 22px;
                  height: 22px;
                  min-height: 22px;
                  fill: $primary-color;
                }
              }
              
              span:last-child {
                margin-left: auto;
                font-weight: 600;
                color: $light-color;
              }
            }
            
            .item-price {
              width: 100%;
              margin-bottom: 40px;
              padding: 0 30px;
              
              input {
                display: flex;
                align-items: center;
                flex-direction: row;
                width: 100%;
                margin-bottom: 30px;
                padding: 15px;
                font-size: 18px;
                font-weight: 500;
                background: $dark-secondary-color;
                border-radius: 5px;
                
                &.input-focus {
                  box-shadow: 0 0 0 1.5px inset $primary-color;
                }
              }
              
              .price-info {
                display: flex;
                flex-direction: column;
                width: 100%;
                
                .price-info-block {
                  display: flex;
                  flex-direction: row;
                  justify-content: space-between;
                  width: 100%;
                  
                  &:not(:last-child) {
                    margin-bottom: 10px;
                  }
                  
                  span {
                    .gray {
                      color: $gray-light-color !important;
                    }
                    
                    &:first-child {
                      font-size: 14px;
                      font-weight: 500;
                      color: $gray-color;
                    }
                    
                    &:last-child {
                      font-size: 16px;
                      font-weight: 600;
                      color: $light-color;
                    }
                  }
                }
              }
            }
            
            .buttons {
              display: flex;
              align-items: center;
              flex-direction: column;
              width: 100%;
              padding: 0 30px 30px 30px;
              
              .btn {
                width: 100%;
                
                &:first-child {
                  margin-bottom: 15px;
                }
              }
            }
          }
          
          &.second {
            width: 100%;
            
            .item-container {
              display: flex;
              align-items: center;
              flex-direction: column;
              height: 100%;
              width: 100%;
              
              .item-container-title {
                display: flex;
                align-items: center;
                flex-direction: row;
                width: 100%;
                margin-bottom: 40px;
                padding: 0 30px;
                font-size: 20px;
                font-weight: 700;
                color: $light-color;
              }
              
              .item-sales-table {
                overflow-x: scroll;
                width: 100%;
  
                table {
                  width: 100%;
                  border-spacing: 0px;
                  border-collapse: collapse;
    
                  thead {
                    th {
                      color: $gray-light-color;
                    }
                  }
    
                  tbody {
                    tr:nth-child(odd) {
                      background: #171C25;
                    }
                  }
    
                  th, td {
                    padding: 15px;
                    font-size: 16px;
                    font-weight: 500;
                    text-align: center;
                    white-space: nowrap;
      
                    span {
                      margin-right: 20px;
        
                      color: $gray-light-color;
                    }
                  }
    
                  th:first-child, td:first-child {
                    padding-left: 30px;
                  }
    
                  th:last-child, td:last-child {
                    padding-right: 30px;
                  }
    
                  .trash-icon {
                    svg {
                      width: 20px;
                      height: 20px;
                      fill: $danger-color;
                    }
                  }
                }
  
  
                tr {
                  display: table;
                  width: 100%;
                }
  
                tbody {
                  display: block;
                  height: 250px;
                }
              }
            }
          }
          
          &.third {
            width: 100%;
            
            .item-container {
              display: flex;
              align-items: center;
              flex-direction: column;
              height: 100%;
              padding: 0 30px;
              
              &:not(:last-child) {
                margin-bottom: 50px;
              }
              
              .item-container-title {
                display: flex;
                align-items: center;
                flex-direction: row;
                width: 100%;
                margin-bottom: 40px;
                font-size: 20px;
                font-weight: 700;
                color: $light-color;
              }
              
              .item-sales-table {
                display: flex;
                flex-direction: column;
                width: 100%;
                
                .item-sales-table-row {
                  display: flex;
                  align-items: center;
                  flex-direction: row;
                  flex-wrap: nowrap;
                  width: 100%;
                  padding: 15px 50px;
                  
                  &:last-child {
                    border-radius: 0 0 5px 5px;
                  }
                }
                
                .item-sales-table-cell {
                  flex: 1;
                  font-size: 16px;
                  font-weight: 500;
                  text-align: center;
                  white-space: nowrap;
                  color: $light-color;
                  
                  &:first-child {
                    text-align: left;
                  }
                  
                  &:last-child {
                    text-align: right;
                  }
                  
                  span {
                    color: $gray-light-color;
                  }
                }
                
                .item-sales-table-head {
                  display: flex;
                  align-items: center;
                  flex-direction: row;
                  
                  .item-sales-table-cell {
                    font-size: 16px;
                    font-weight: 600;
                    text-align: center;
                    white-space: nowrap;
                    color: $gray-light-color;
                    
                    &:first-child {
                      text-align: left;
                    }
                    
                    &:last-child {
                      text-align: right;
                    }
                  }
                }
                
                .item-sales-table-body {
                  display: flex;
                  align-items: center;
                  flex-direction: column;
                  
                  .item-sales-table-row {
                    background: $dark-color;
                    
                    &:nth-child(odd) {
                      background: #171C25;
                    }
                  }
                }
              }
              
              &.item-chart {
                position: relative;
                z-index: 10001;
                align-items: normal;
                width: 100%;
                height: 100%;
                
                .item-container-title {
                  display: flex;
                  align-items: flex-start;
                  flex-direction: column;
                  width: 100%;
                  
                  .title {
                    display: flex;
                    width: 100%;
                  }
                }
                
                .date-selector {
                  display: flex;
                  align-items: center;
                  flex-direction: row;
                  cursor: pointer;
                  user-select: none;
                  
                  span {
                    font-size: 18px;
                    font-weight: 500;
                    color: $gray-light-color;
                    
                    &.active {
                      color: $primary-color;
                    }
                    
                    &:not(:last-child) {
                      margin-right: 20px;
                    }
                  }
                }
                
                svg {
                  user-select: none;
                  width: 100%;
                }
              }
            }
          }
        }
      }
    }
  }
}