$breakpoint-tablet: 768px;
$breakpoint-laptop-small: 1024px;
$breakpoint-laptop-large: 1200px;
$breakpoint-desktop: 1600px;

.filter-bar {
  .icon svg {
    fill: #FFFFFF;
  }

  .filter-title {
    font-size: 18px;
    font-weight: 500;

    color: #6D7681;

    white-space: nowrap;
  }
  
  .game-selector {
    margin: 0 30px 0 20px;
  }

  .market-button {
    width: 163px;
    height: 47px;

    display: flex;
    align-items: center;
    justify-content: center;

    div.icon {
      width: 20px;
      min-width: 20px;
      height: 20px;

      & svg {
        fill: #4A99EB;
      }
    }

    span {
      margin-left: 10px;
      margin-right: 20px;
    }

    .arrow svg {
      width: 10px;

      fill: #FFFFFF;
    }
  }

  .filter-bar-search {
    //width: 625px;

    display: flex;
    align-items: center;

    svg {
      width: 19px;
      height: 19px;
    }

    //.icon {
    //  display: flex;
    //  align-items: center;
    //
    //  & svg {
    //    width: 19px;
    //    height: 19px;
    //  }
    //}
  }
  
  .dropdown-menu {
    position: unset;
  }

  @media (max-width: $breakpoint-desktop) {
    .filter-title {
      display: none;
    }

    .game-filter {
      margin: 0 15px 0 0;
    }

    .market-button {
      margin-right: 15px;
    }

    .filter-bar-search {
      width: 520px;
    }
  }

  @media (max-width: $breakpoint-laptop-large) {
    .filter-title {
      display: none;
    }

    .game-filter {
      margin: 0 15px 0 0;
    }

    .market-button {
      margin-right: 15px;
    }

    .filter-bar-search {
      width: 345px;
    }
  }

  /* TABLET (768 - 1024) */
  @media (max-width: $breakpoint-laptop-small) {
    .filter-title {
      display: none;
    }

    .game-filter {
      margin: 0 15px 0 0;
    }

    .market-button {
      margin-right: 15px;
    }

    .filter-bar-search {
      display: none;
    }
  }

  /* PHONES */
  @media (max-width: $breakpoint-tablet) {

  }
}