$breakpoint-tablet: 768px;
$breakpoint-laptop-small: 1024px;
$breakpoint-laptop-large: 1200px;
$breakpoint-desktop: 1600px;

@import "./src/assets/styles/colors";

.ticket-page {
  &.mobile {
    display: flex;
    align-items: center;
    flex-direction: column;
    width: 100%;
    height: 100%;
    padding: 30px;
    background: $dark-color;
    
    .ticket-page-head {
      display: flex;
      align-items: center;
      flex-direction: column;
      justify-content: center;
      margin-bottom: 30px;
      
      .ticket-page-title {
        margin-bottom: 15px;
        font-size: 24px;
        font-weight: 700;
        color: $light-color;
      }
      
      .ticket-page-description {
        font-size: 14px;
        font-weight: 500;
        line-height: 130%;
        color: $gray-light-color;
      }
    }
    
    .ticket-page-body {
      .category-selector {
        position: relative;
        z-index: 5;
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        width: 100%;
        height: 60px;
        margin-bottom: 20px;
        padding: 19px;
        cursor: pointer;
        user-select: none;
        font-size: 18px;
        font-weight: 600;
        background: #272E3D;
        border-radius: 5px;
    
        .title {
      
        }
    
        .arrow {
          position: relative;
          top: 7px;
          border-top: 7px solid;
          border-top-color: #FFFFFF;
          border-right: 6px solid transparent;
          border-left: 6px solid transparent;
        }
    
        &.active {
          border-bottom-right-radius: 0;
          border-bottom-left-radius: 0;
        }
    
        &.active .category-dropdown {
          display: flex;
          margin-top: 1px;
          border-top-left-radius: 0;
          border-top-right-radius: 0;
        }
    
        .category-dropdown {
          position: absolute;
          top: 60px;
          left: 0;
          display: none;
          align-items: flex-start;
          flex-direction: column;
          overflow-y: scroll;
          width: 100%;
          max-height: 180px;
          cursor: pointer;
          user-select: none;
          background: #272E3D;
          border-radius: 5px;
      
          .category-title {
            width: 100%;
            height: 60px;
            padding: 19px;
            color: #6D7681;
        
            &:hover {
              color: #FFFFFF;
            }
          }
        }
      }
  
      input {
        width: 100%;
        margin-bottom: 20px;
        padding: 15px;
        font-size: 18px;
        font-weight: 500;
        color: $light-color;
        background: $dark-secondary-color;
        border-radius: 5px;
    
        &.input-focus {
          box-shadow: 0 0 0 1.5px $primary-color;
        }
    
        &.input-error {
          box-shadow: 0 0 0 1.5px $danger-color;
        }
      }
  
      .ticket-body-input {
        position: relative;
        margin-bottom: 20px;
        
        .image-button {
          position: absolute;
          right: 15px;
          bottom: 15px;
          
          svg {
            width: 30px;
            height: 30px;
            fill: $gray-light-color;
          }
        }
        
        textarea {
          width: 100%;
          min-height: 200px;
          padding: 15px;
          resize: none;
          font-size: 14px;
          font-weight: 500;
          font-family: "Gilroy", sans-serif;
          color: $light-color;
          background: $dark-secondary-color;
          border: none;
          border-radius: 5px;
          outline: none;
          
          &::placeholder {
            color: $gray-light-color;
          }
        }
      }
      
      .btn {
        width: 100%;
        
        &:not(:last-child) {
          margin-bottom: 20px;
        }
      }
    }
  }
  
  &.tablet {
    display: flex;
    align-items: center;
    flex-direction: column;
    width: 100%;
    height: 100%;
    padding: 30px;
    background: $dark-color;
    
    .ticket-page-head {
      display: flex;
      align-items: center;
      flex-direction: column;
      justify-content: center;
      margin-bottom: 30px;
      
      .ticket-page-title {
        margin-bottom: 15px;
        font-size: 24px;
        font-weight: 700;
        color: $light-color;
      }
      
      .ticket-page-description {
        font-size: 14px;
        font-weight: 500;
        line-height: 130%;
        color: $gray-light-color;
      }
    }
    
    .ticket-page-body {
      width: 100%;
      
      .ticket-page-body-top {
        display: flex;
        align-items: center;
        flex-direction: row;
        flex-wrap: nowrap;
        margin-bottom: 20px;
        justify-items: center;
  
        .category-selector {
          position: relative;
          z-index: 5;
          display: flex;
          flex-direction: row;
          justify-content: space-between;
          width: 100%;
          height: 60px;
          margin-right: 20px;
          padding: 19px;
          cursor: pointer;
          user-select: none;
          font-size: 18px;
          font-weight: 600;
          background: #272E3D;
          border-radius: 5px;
    
          .title {
      
          }
    
          .arrow {
            position: relative;
            top: 7px;
            border-top: 7px solid;
            border-top-color: #FFFFFF;
            border-right: 6px solid transparent;
            border-left: 6px solid transparent;
          }
    
          &.active {
            border-bottom-right-radius: 0;
            border-bottom-left-radius: 0;
          }
    
          &.active .category-dropdown {
            display: flex;
            margin-top: 1px;
            border-top-left-radius: 0;
            border-top-right-radius: 0;
          }
    
          .category-dropdown {
            position: absolute;
            top: 60px;
            left: 0;
            display: none;
            align-items: flex-start;
            flex-direction: column;
            overflow-y: scroll;
            width: 100%;
            max-height: 180px;
            cursor: pointer;
            user-select: none;
            background: #272E3D;
            border-radius: 5px;
      
            .category-title {
              width: 100%;
              height: 60px;
              padding: 19px;
              color: #6D7681;
        
              &:hover {
                color: #FFFFFF;
              }
            }
          }
        }
  
        input {
          width: 100%;
          height: 60px;
          padding: 15px;
          font-size: 18px;
          font-weight: 500;
          color: $light-color;
          background: $dark-secondary-color;
          border-radius: 5px;
    
          &.input-focus {
            box-shadow: 0 0 0 1.5px $primary-color;
          }
    
          &.input-error {
            box-shadow: 0 0 0 1.5px $danger-color;
          }
        }
      }
      
      .ticket-body-input {
        position: relative;
        margin-bottom: 20px;
        
        .image-button {
          position: absolute;
          right: 15px;
          bottom: 15px;
          
          svg {
            width: 30px;
            height: 30px;
            fill: $gray-light-color;
          }
        }
        
        textarea {
          width: 100%;
          min-height: 200px;
          padding: 15px;
          resize: none;
          font-size: 16px;
          font-weight: 500;
          font-family: "Gilroy", sans-serif;
          color: $light-color;
          background: $dark-secondary-color;
          border: none;
          border-radius: 5px;
          outline: none;
          
          &::placeholder {
            color: $gray-light-color;
          }
        }
      }
      
      .ticket-body-buttons {
        display: flex;
        align-items: center;
        flex-direction: row;
        justify-items: center;
        
        .btn {
          width: 100%;
          
          &:first-child {
            margin-right: 10px;
          }
          
          &:last-child {
            margin-left: 10px;
          }
        }
      }
    }
  }
  
  &.non-tablet {
    width: 100%;
    
    .ticket-page-inner {
      display: flex;
      align-items: center;
      flex-direction: column;
      width: 100%;
      height: 100%;
      padding: 50px;
      background: $dark-color;
      border-radius: 5px;
    }
    
    .ticket-page-head {
      position: relative;
      display: flex;
      align-items: center;
      flex-direction: column;
      width: 100%;
      margin-bottom: 60px;
      
      .ticket-page-title {
        margin-bottom: 20px;
        font-size: 36px;
        font-weight: 700;
        line-height: 40px;
        color: #FFFFFF;
      }
      
      .ticket-page-description {
        max-width: 600px;
        font-size: 18px;
        font-weight: 500;
        line-height: 30px;
        text-align: center;
        color: #6D7681;
      }
      
      .my-tickets-button {
        position: absolute;
        top: 0;
        right: 0;
      }
    }
    
    .ticket-page-body {
      max-width: 935px;
      
      .ticket-page-form-head {
        display: flex;
        align-items: center;
        flex-direction: row;
        justify-content: space-between;
        margin-bottom: 35px;
        
        .category-selector {
          position: relative;
          display: flex;
          flex-direction: row;
          justify-content: space-between;
          width: 450px;
          height: 60px;
          margin-right: 35px;
          padding: 19px;
          cursor: pointer;
          user-select: none;
          font-size: 18px;
          font-weight: 600;
          background: #272E3D;
          border-radius: 5px;
          
          .title {
          
          }
          
          .arrow {
            position: relative;
            top: 7px;
            border-top: 7px solid;
            border-top-color: #FFFFFF;
            border-right: 6px solid transparent;
            border-left: 6px solid transparent;
          }
          
          &.active {
            border-bottom-right-radius: 0;
            border-bottom-left-radius: 0;
          }
          
          &.active .category-dropdown {
            display: flex;
            margin-top: 1px;
            border-top-left-radius: 0;
            border-top-right-radius: 0;
          }
          
          .category-dropdown {
            position: absolute;
            top: 60px;
            left: 0;
            display: none;
            align-items: flex-start;
            flex-direction: column;
            overflow-y: scroll;
            width: 100%;
            max-height: 180px;
            cursor: pointer;
            user-select: none;
            background: #272E3D;
            border-radius: 5px;
            
            .category-title {
              width: 100%;
              height: 60px;
              padding: 19px;
              color: #6D7681;
  
              &:hover {
                color: #FFFFFF;
              }
            }
          }
        }
  
        input {
          width: 100%;
          height: 60px;
          padding: 15px;
          font-size: 18px;
          font-weight: 500;
          color: $light-color;
          background: $dark-secondary-color;
          border-radius: 5px;
    
          &.input-focus {
            box-shadow: 0 0 0 1.5px $primary-color;
          }
    
          &.input-error {
            box-shadow: 0 0 0 1.5px $danger-color;
          }
        }
  
      }
      
      .ticket-page-form-body {
        textarea {
          width: 100%;
          height: 230px;
          padding: 25px;
          resize: none;
          font-size: 18px;
          font-weight: 500;
          font-family: "Gilroy", sans-serif;
          color: #FFFFFF;
          background: #0F141B;
          border: none;
          border-radius: 5px;
          outline: none;
          
          &::placeholder {
            color: #6D7681;
          }
        }
        
        .attached {
          display: flex;
          flex-direction: row;
          flex-wrap: wrap;
          width: 100%;
          margin: 30px 0;
          
          .file {
            position: relative;
            display: flex;
            align-items: center;
            flex-direction: column;
            justify-content: center;
            width: 100px;
            height: 80px;
            color: $gray-light-color;
            background: $secondary-color;
            border-radius: 15px;
            
            &:not(:last-child) {
              margin-right: 15px;
              margin-bottom: 15px;
            }
            
            .remove {
              position: absolute;
              top: 10px;
              right: 10px;
              display: flex;
              align-items: center;
              flex-direction: row;
              justify-content: center;
              padding: 5px;
              cursor: pointer;
              background: $dark-secondary-color;
              border-radius: 5px;
              
              &:hover {
                svg {
                  fill: $danger-color;
                }
              }
              
              svg {
                width: 15px;
                height: 15px;
                fill: $gray-light-color;
              }
            }
            
            span {
              display: none;
              max-width: 150px;
              font-size: 14px;
              font-weight: 500;
            }
            
            img {
              width: 100px;
              max-width: 100px;
              height: 80px;
              border-radius: 15px;
              object-fit: cover;
            }
          }
        }
      }
      
      .ticket-page-form-footer {
        display: flex;
        align-items: center;
        flex-direction: row;
        justify-content: space-between;
        
        .switch-read {
          label {
            cursor: pointer;
            
            span {
              margin-left: 20px;
              font-size: 18px;
              font-weight: 600;
              
              .attention-text {
                font-weight: 700;
              }
            }
          }
        }
        
        .buttons-group {
          display: flex;
          align-items: center;
          flex-direction: row;
          
          .image-button {
            display: flex;
            align-content: center;
            justify-content: center;
            cursor: pointer;
            
            svg {
              width: 30px;
              height: 30px;
              fill: #262D3B;
            }
          }
          
          .create-ticket-button {
            width: 142px;
            margin-left: 45px;
          }
        }
      }
    }
  }
}

/* TABLETS (768 - 1024) */
@media (max-width: $breakpoint-laptop-small) {
  .ticket-page {
    .ticket-page-head {
      .ticket-page-title {
        width: 100%;
        text-align: center;
      }
      
      .ticket-page-description {
        width: 100%;
        min-width: 100%;
        text-align: center !important;
      }
      
      .my-tickets-button {
        position: relative;
        width: 100%;
        margin-top: 20px;
      }
    }
    
    .ticket-page-body {
      .ticket-page-form-head {
        flex-direction: column;
        width: 100%;
        
        .category-selector {
          width: 100%;
          margin: 0;
        }
        
        .ticket-input-header {
          width: 100%;
          margin-top: 15px;
        }
      }
    }
  }
}

/* LAPTOP (1024 - 1200) */
@media (max-width: $breakpoint-laptop-large) {
  .ticket-page {
    height: 100%;
    
    .ticket-page-head {
      align-items: flex-start;
      
      .ticket-page-description {
        max-width: 450px;
        text-align: left;
      }
    }
    
    .ticket-page-body {
      width: 100%;
    }
  }
}

/* LAPTOP (1200 - 1600) */
@media (min-width: $breakpoint-laptop-large) and (max-width: $breakpoint-desktop) {
