@import "src/assets/styles/colors";

.dropdown-menu {
  display: flex;
  flex-direction: row;
  align-items: center;
  color: $light-color;
  font-size: 18px;
  font-weight: 600;
  min-width: 280px;
  position: relative;
  &:not(:last-child) {
    margin-right: 30px;
  }
  
  &.quality-filter {
    height: 60px;
  }
  
  .dropdown-menu--head {
    display: flex;
    flex-direction: row;
    align-items: center;
    cursor: pointer;
    width: 100%;
    padding: 20px;
    border-radius: 5px;
    background: $secondary-color;
    
    .dropdown-menu--title {
      display: flex;
      flex-direction: row;
      align-items: center;
      user-select: none;
    }
    
    .dropdown-menu--controls {
      display: flex;
      flex-direction: row;
      align-items: center;
      margin-left: auto;
      
      .dropdown-menu--reset {
        margin-right: 10px;
        display: flex;
        flex-direction: row;
        align-items: center;
        transform: rotate(45deg);
        
        &:hover {
          svg {
            fill: $primary-color;
          }
        }
        
        svg {
          pointer-events: none;
          width: 14px;
          height: 14px;
          fill: $light-color;
        }
      }
      
      .dropdown-menu--arrow {
        border-right: 6px solid transparent;
        border-left: 6px solid transparent;
        border-top: 7px solid;
        border-top-color: $light-color;
        
        &.active {
          border-top-color: $primary-color;
        }
      }
    }
  }
  
  .dropdown-menu--options {
    position: absolute;
    top: 70px;
    max-height: 310px;
    overflow-y: auto;
    display: flex;
    flex-direction: column;
    width: 100%;
    z-index: 999;
    border-radius: 5px;
    background: $secondary-color;
    
    .dropdown-menu--option {
      display: flex;
      flex-direction: row;
      align-items: center;
      padding: 20px;
      cursor: pointer;
      color: $gray-light-color;
      user-select: none;
      
      &.selected {
        background: $primary-color;
        color: $light-color;
        
        &:hover {
          background: darken($primary-color, 5%);
        }
      }
      
      &:hover {
        background: darken($secondary-color, 5%);
      }
    }
  }
}