* {
  outline: none;
}



.not-found-page {
  width: 100%;
  padding: 50px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  
  .not-found-error {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    
    .error-header {
      font-size: 100px;
      font-family: "TT Lakes Condensed Bold Italic", sans-serif;
      letter-spacing: 0.12em;
  
      span {
        color: #4A99EB;
      }
    }
    
    .error-description {
      font-size: 36px;
      font-weight: 700;
    }
    
    .error-back-to-home {
      margin-top: 25px;
    }
  }
}