  &.tablet {
    position: relative;
    z-index: 0;
    display: flex;
    align-items: center;
    flex-direction: row;
    justify-content: space-around;
    width: 100%;
    //height: 475px;
    &:not(:last-child) {
      margin-bottom: 15px;
    }
    padding: 20px;
    white-space: nowrap;
    background: $dark-color;
    border-radius: 5px;
    transition: 175ms cubic-bezier(0.1, 0.1, 0.25, 1);
    
    .selected-overlay {
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      z-index: 2;
      display: flex;
      align-items: center;
      flex-direction: row;
      justify-content: center;
      width: 100%;
      height: 100%;
      background: rgba($dark-secondary-color, 0.5);
      border: 5px solid $primary-color;
      border-radius: 5px;
      
      .icon {
        display: flex;
        align-items: center;
        flex-direction: row;
        justify-content: center;
        width: 70px;
        height: 70px;
        background: $primary-color;
        border-radius: 100%;
      }
      
      svg {
        width: 32px;
        height: 32px;
        fill: $light-color;
      }
    }
    
    .item-visual {
      position: relative;
      display: flex;
      width: 125px;
      height: 100%;
      margin-right: 20px;
      
      .item-image {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 125px;
        height: 64px;
        
        img {
          width: auto;
          max-width: 125px;
          height: auto;
        }
      }
      
      .item-stickers {
        position: absolute;
        right: 0;
        display: flex;
        align-items: center;
        flex-direction: column;
        
        .item-sticker {
          display: flex;
          align-items: center;
          flex-direction: row;
          cursor: pointer;
          
          &:not(:last-child) {
            margin-bottom: 5px;
          }
          
          img {
            width: 25px;
            height: 25px;
          }
          
          .__react_component_tooltip {
            display: flex;
            align-items: center;
            flex-direction: column;
            justify-content: center;
            min-width: 150px;
            max-width: 280px;
            padding: 15px;
            font-size: 14px;
            font-weight: 600;
            white-space: normal;
            background: $secondary-color;
            border-radius: 5px;
            opacity: 1;
            
            &.place-top {
              &:after {
                border-top-color: $secondary-color;
              }
            }
            
            &.place-left {
              &:after {
                border-left-color: $secondary-color;
              }
            }
            
            &.place-bottom {
              &:after {
                border-bottom-color: $secondary-color;
              }
            }
            
            &.place-right {
              &:after {
                border-right-color: $secondary-color;
              }
            }
            
            .sticker-title {
              margin-bottom: 10px;
              font-size: 18px;
              font-weight: 600;
              color: $light-color;
            }
            
            .sticker-wear {
              font-size: 18px;
              font-weight: 600;
              color: $gray-light-color;
            }
          }
        }
      }
    }
    
    .item-info {
      width: 170px;
      display: flex;
      flex-direction: column;
      margin-right: 20px;
      
      .weapon-name {
        margin-bottom: 10px;
        font-size: 14px;
        font-weight: 500;
        color: $gray-light-color;
        
        .exterior {
          text-transform: uppercase;
          color: $gray-light-color;
        }
        
        &.st {
          color: #FF9523;
        }
        
        &.souvenir {
          color: #FFD237;
        }
      }
      
      .item-name {
        margin-bottom: 10px;
        font-size: 20px;
        font-weight: 600;
        color: $light-color;
      }
      
      .item-lock {
        display: flex;
        align-items: center;
        flex-direction: row;
        font-size: 18px;
        font-weight: 600;
        color: $light-color;
        
        &.unlocked {
          color: $success-color;
          
          svg {
            fill: $success-color;
          }
        }
        
        svg {
          width: 20px;
          height: 20px;
          margin-right: 10px;
          fill: $danger-color
        }
      }
    }
    
    .item-price {
      width: 200px;
      display: flex;
      align-items: flex-start;
      flex-direction: column;
      font-size: 30px;
      font-weight: 700;
      color: $light-color;
      
      .market-price {
        display: flex;
        align-items: center;
        flex-direction: row;
        margin-bottom: 10px;
        font-size: 30px;
        font-weight: 600;
        color: $light-color;
        
        .discount {
          display: flex;
          align-items: center;
          justify-content: center;
          margin-left: 20px;
          padding: 3px 6px;
          font-size: 14px;
          background: $danger-color;
          border-radius: 5px;
        }
      }
      
      .steam-price {
        display: flex;
        align-items: center;
        flex-direction: row;
        font-size: 18px;
        font-weight: 600;
        color: $gray-light-color;
        
        .title {
          display: flex;
          align-items: center;
          flex-direction: row;
          margin-right: 5px;
        }
        
        svg {
          width: 24px;
          min-width: 24px;
          height: 24px;
          margin-right: 10px;
          background: $gray-light-color;
          border-radius: 100%;
          fill: $gray-light-color;
          
          .st0 {
            fill: transparent !important;
          }
          
          .st1 {
            fill: $light-color !important;
          }
        }
      }
    }
    
    .item-exterior {
      display: flex;
      align-items: flex-start;
      flex-direction: column;
      height: 100%;
      
      .item-exterior-title {
        display: flex;
        align-items: center;
        flex-direction: row;
        justify-content: space-between;
        width: 100%;
        margin-bottom: 10px;
        font-size: 18px;
        font-weight: 600;
        color: $gray-color;
        
        .inspect-link {
          display: flex;
          align-items: center;
          font-size: 18px;
          font-weight: 600;
          color: $gray-light-color;
          
          svg {
            width: 20px;
            height: 20px;
            margin-right: 10px;
            fill: $gray-light-color;
          }
        }
      }
      
      .item-float {
        display: flex;
        align-items: center;
        flex-direction: row;
        height: 100%;
        
        .float {
          display: flex;
          align-items: center;
          flex-direction: row;
          width: 70px;
          font-size: 18px;
          font-weight: 700;
          color: $light-color;
          
          span {
            color: $gray-light-color;
            margin-right: 5px;
          }
        }
        
      }
    }
    
    .item-buttons {
      display: flex;
      align-items: center;
      flex-direction: row;
      margin-left: 50px;
      
      .btn {
        height: 54px;
      }
      
      svg {
        width: 20px;
        min-width: 20px;
        height: 20px;
        fill: $gray-light-color;
      }
      
      & > .btn {
        &:first-child {
          margin-right: 10px;
        }
      }
    }
  }
}