@import "../../../assets/styles/colors.scss";

.switch {
  input[type="checkbox"] {
    position: relative;
    width: 68px;
    height: 30px;

    margin: 0 15px 0 0;

    border-radius: 25px;

    background: $secondary-color;

    appearance: none;
    outline: 0;
    cursor: pointer;
    transition: 175ms cubic-bezier(0.1, 0.1, 0.25, 1);

    &:before {
      content: '';

      position: absolute;
      top: 5px;
      left: 6px;

      display: block;

      width: 20px;
      height: 20px;

      border-radius: 50%;
      background: $gray-light-color;

      opacity: 1;

      transition: 175ms cubic-bezier(0.1, 0.1, 0.25, 1);
    }

    &:checked {
      border-color: $primary-color;
      background: $primary-color;

      &::before {
        background: $light-color;
        transform: translateX(36px);
      }

      ~ label {
        color: $light-color;
      }

      ~ span {
        color: $light-color;
      }
    }
  }

  label {
    position: relative;

    cursor: pointer;
    font-size: 18px;
    font-weight: 500;
    user-select: none;

    display: flex;
    flex-direction: row;
    align-items: center;

    span {
      color: $gray-light-color;
    }
  }
}