@import "src/assets/styles/colors";

.modal-wrapper-trade-modal {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 10000;
  display: none;
  align-items: center;
  flex-direction: row;
  justify-content: center;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  
  &.fullscreen {
    &.active {
      display: flex;
    }
    
    .modal-block {
      position: relative;
      display: flex;
      overflow-y: hidden;
      width: 750px;
      min-width: 750px;
      height: 500px;
      padding: 50px;
      white-space: nowrap;
      background: #1A1E29;
      border-radius: 5px;
      
      @keyframes rotate {
        100% {
          transform: rotate(1turn);
        }
      }
      
      .close-button {
        position: absolute;
        top: 20px;
        right: 20px;
        cursor: pointer;
        
        svg {
          width: 20px;
          height: 20px;
          fill: #FFFFFF;
        }
      }
      
      .modal-body {
        display: flex;
        align-items: flex-start;
        flex-direction: column;
        width: 100%;
        
        .trade-modal-title {
          margin-bottom: 50px;
          font-size: 20px;
          font-weight: 700;
          color: $light-color;
        }
        
        .trade-modal-list {
          width: 100%;
          
          .trade-modal-list-head {
            display: flex;
            flex-direction: row;
            justify-content: space-between;
            width: 100%;
            margin-bottom: 20px;
            
            .trade-modal-list-cell {
              font-size: 14px;
              font-weight: 500;
              color: $gray-light-color;
              
              &:first-child {
                margin-right: 70px;
                margin-left: 30px;
              }
              
              &:last-child {
                margin-right: 80px;
                margin-left: 115px;
              }
            }
          }
          
          .trade-modal-list-body {
            display: flex;
            flex-direction: column;
            overflow-y: auto;
            width: 100%;
            height: 300px;
            margin-bottom: 50px;
            
            .trade-modal-list-row {
              display: flex;
              align-items: center;
              flex-direction: row;
              width: 100%;
              padding: 20px;
              background: $secondary-color;
              border-radius: 5px;
              
              &:not(:last-child) {
                margin-bottom: 10px;
              }
              
              .bot-number {
                width: 75px;
                margin-right: 35px;
                font-size: 16px;
                font-weight: 600;
                color: $light-color;
                
                svg {
                  width: 20px;
                  height: 20px;
                  margin-right: 15px;
                  fill: $primary-color;
                }
              }
              
              .bot-level {
                margin-right: 30px;
                padding: 12px 16px;
                font-size: 14px;
                border: 3px solid $danger-color;
                border-radius: 15px;
                
                span {
                  font-weight: 600;
                  color: $gray-light-color;
                }
                
                &.danger {
                  border-color: $danger-color;
                }
                
                &.success {
                  border-color: $success-color;
                }
              }
              
              .registration-date {
                font-size: 16px;
                font-weight: 600;
                color: $light-color;
              }
              
              .buttons {
                display: flex;
                align-items: center;
                flex-direction: row;
                margin-left: auto;
                
                
                & > .btn {
                  width: 112px;
                  height: 44px;
                  
                  svg {
                    width: 20px;
                    min-width: 20px;
                    height: 20px;
                    margin-right: 5px;
                    fill: $light-color;
                    
                    .st0 {
                      fill: transparent;
                    }
                    
                    .st1 {
                      fill: $dark-color;
                    }
                  }
                  
                  &:first-child {
                    margin-right: 10px;
                    
                    svg {
                      background: $light-color;
                      border-radius: 100%;
                    }
                  }
                }
              }
              
              .indicator {
                display: flex;
                align-items: center;
                flex-direction: row;
                margin-left: auto;
                font-size: 14px;
                font-weight: 600;
                
                .loader-container {
                  width: 45px;
                  height: 20px;
                  margin-right: 15px;
                  
                  .spinner {
                    height: 100%;
                    
                    & > div {
                      width: 8px;
                      height: 8px;
                      margin: 0 5px;
                      background: $light-color;
                    }
                  }
                  
                  .loader-text {
                    display: none;
                  }
                }
                
                .indicator-circle {
                  display: block;
                  width: 12px;
                  min-width: 12px;
                  height: 12px;
                  margin-right: 10px;
                  border-radius: 100%;
                }
                
                &.sending {
                  svg {
                    fill: $light-color;
                  }
                }
                
                &.accepted {
                  color: $success-color;
                  fill: $success-color;
                  
                  .indicator-circle {
                    background: $success-color;
                  }
                }
                
                &.canceled {
                  color: $danger-color;
                  fill: $danger-color;
                  
                  .indicator-circle {
                    background: $danger-color;
                  }
                }
                
                &.timed-out {
                  color: $warning-color;
                  fill: $warning-color;
                  
                  .indicator-circle {
                    background: $warning-color;
                  }
                }
              }
            }
          }
        }
      }
    }
  }
  
